import React, { Component } from 'react';
import { Route, Switch, Redirect, withRouter } from 'react-router-dom';
import AuthHelper from './helpers/auth';

/* layouts */
import BasicLayout from './layouts/basic-layout';
import NavigationLayout from './layouts/navigation-layout';

/* containers */
import LoginPage from './containers/LoginPage';
import LogoutPage from './containers/LogoutPage';
import UnauthorizedPage from './containers/UnauthorizedPage';

import CustomerForm from './containers/CustomerForm';

import RegisterPage from './containers/RegisterPage';
import ForgotPasswordPage from './containers/ForgotPasswordPage';

import TicketPage from './containers/TicketPage';
import SettingsPage from './containers/SettingsPage';
import StaffListPage from './containers/StaffListPage';
import DashboardPage from './containers/DashboardPage';
import ReportPage from './containers/ReportPage';
import MessengerIntegrationPage from './containers/Channels/MessengerIntegrationPage';
import MessengerSettingsPage from './containers/Channels/MessengerSettingsPage';
import SubscriptionPage from './containers/SubscriptionPage';
import StaffDetailsPage from './containers/StaffDetailsPage';
import StaffBasicPage from './containers/StaffDetailsPage/StaffBasicPage';

const routes = [
	{ path: '/unauthorized', exact: true, isPrivate: false, Layout: BasicLayout, layoutClass: 'narrow', Component: UnauthorizedPage },
	{ path: '/login', exact: true, isPrivate: false, Layout: BasicLayout, layoutClass: 'narrow', Component: LoginPage },
	{ path: '/logout', exact: true, isPrivate: false, Layout: BasicLayout, layoutClass: 'narrow', Component: LogoutPage },
	{ path: '/register', exact: true, isPrivate: false, Layout: BasicLayout, layoutClass: 'narrow', Component: RegisterPage },
	{ path: '/forgot-password', exact: true, isPrivate: false, Layout: BasicLayout, layoutClass: 'narrow', Component: ForgotPasswordPage },
	{ path: '/customer-form', exact: true, isPrivate: true, Layout: BasicLayout, layoutClass: 'narrow', Component: CustomerForm, forStaff: false },

	// Tickets
	{ path: '/tickets', exact: true, isPrivate: true, Layout: NavigationLayout, title: 'Tickets', layoutClass: 'narrow', Component: TicketPage, forStaff: true },
	// Settings
	{ path: '/settings', exact: true, isPrivate: true, Layout: NavigationLayout, title: 'Settings', layoutClass: 'narrow', Component: SettingsPage, forStaff: false },
	//StaffList
	{ path: '/staffs', exact: true, isPrivate: true, Layout: NavigationLayout, title: ' ', layoutClass: 'narrow', Component: StaffListPage, forStaff: false },
	//Dashboard
	{ path: '/dashboard', exact: true, isPrivate: true, Layout: NavigationLayout, title: ' ', layoutClass: 'narrow', Component: DashboardPage, forStaff: false },
	//Reports
	{ path: '/reports', exact: true, isPrivate: true, Layout: NavigationLayout, title: ' ', layoutClass: 'narrow', Component: ReportPage, forStaff: false },
	//Channels
	{ path: '/messenger-integration', exact: true, isPrivate: true, Layout: NavigationLayout, title: ' ', layoutClass: 'narrow', Component: MessengerIntegrationPage, forStaff: false },
	{ path: '/messenger-settings', exact: true, isPrivate: true, Layout: NavigationLayout, title: ' ', layoutClass: 'narrow', Component: MessengerSettingsPage, forStaff: false },
	//Subsciptions
	{ path: '/subscription', exact: true, isPrivate: true, Layout: NavigationLayout, title: ' ', layoutClass: 'narrow', Component: SubscriptionPage, forStaff: false },
	{ path: '/staffs/create', exact: true, isPrivate: true, Layout: NavigationLayout, title: ' ', layoutClass: 'narrow', Component: StaffBasicPage, forStaff: false },
	{ path: '/staffs/id/basic', exact: true, isPrivate: true, Layout: NavigationLayout, title: ' ', layoutClass: 'narrow', Component: StaffDetailsPage, forStaff: false },
	{ path: '/staffs/id/tickets', exact: true, isPrivate: true, Layout: NavigationLayout, title: ' ', layoutClass: 'narrow', Component: StaffDetailsPage, forStaff: false }
];

const getLoginType = () => (localStorage.getItem('app') ? JSON.parse(localStorage.getItem('app')).type : '');

function getRedirectedPath(location) {
	if (location.pathname.split('?')[0] === '/login' && location.pathname.split('?').length > 1) {
		return location.pathname + location.search + location.hash;
	}

	// skip the redirection from these routes
	if (['/login', '/logout'].indexOf(location.pathname.split('?')[0]) !== -1) {
		return '/login';
	}
	return `/login?path=${location.pathname + location.search + location.hash}`;
}

const PrivateRoute = ({ layout: Layout, layoutClass, component: Component, title, forStaff, ...rest }) => (
	<Route
		{...rest}
		render={props => {
			if (AuthHelper.isAuthenticated()) {
				const type = getLoginType();

				if (!forStaff && type === 'staff') {
					return (
						<Redirect
							to={{
								pathname: '/tickets',
								state: { from: props.location }
							}}
						/>
					);
				} else {
					return (
						<Layout
							containerClass={layoutClass}
							title={title}
						>
							<Component {...props} />
						</Layout>
					);
				}
			} else {
				return (
					<Redirect
						to={{
							pathname: getRedirectedPath(props.location),
							state: { from: props.location }
						}}
					/>
				);
			}
		}}
	/>
);

export class Routes extends Component {
	render() {
		const { location } = this.props;

		return (
			<Switch location={location}>
				{routes.map(({ path, exact, isPrivate, Layout, layoutClass, Component, title, forStaff }) => {
					if (isPrivate) {
						return <PrivateRoute
							key={0}
							path={path}
							exact={exact}
							layout={Layout}
							layoutClass={layoutClass}
							component={Component}
							title={title}
							forStaff={forStaff}
						       />;
					} else {
						return (
							<Route
								key={0}
								path={path}
								exact={exact}
								render={props => (
									<Layout
										containerClass={layoutClass}
										location={location}
									>
										<Component {...props} />
									</Layout>
								)}
							/>
						);
					}
				})}
				<Route render={props => (AuthHelper.isAuthenticated() ? <Redirect to={'/tickets'} /> : <Redirect to={getRedirectedPath(props.location)} />)} />
			</Switch>
		);
	}
}

export default withRouter(Routes);
