import React, { Component } from 'react';
import { Link } from 'react-router-dom';
import { Layout, Breadcrumb, Typography } from 'antd';
import Drawer from '../components/drawer';
import NavigationHeader from '../components/header';

import GeneralHelper from '../helpers/general';
import LocalStorageHelper from '../helpers/local_storage';

const { Header, Content, Footer } = Layout;
const { Title } = Typography;

export class NavigationLayout extends Component {
	state = {
		collapsed: false,
		BreadcrumbSection: [
			{ key: 'homePage', content: 'Lab Booking System', active: true }
		],
		BreadcrumbTitle: ' '
	}

	handleOnCollapse = () => {
		this.setState({
			collapsed: !this.state.collapsed
		});
	}

	setBreadcrumbNavigationBar = (input) => {
		this.setState({
			BreadcrumbSection: input
		});
	}

	setBreadcrumbTitle = (input) => {
		this.setState({
			BreadcrumbTitle: input
		});
	}

	ShowNavigationBar = () => {
		var sections = this.state.BreadcrumbSection;
		var title = this.state.BreadcrumbTitle;
		return (
			<Header
				className="breadcrumb-header"
				style={{ marginTop: 64 }}
			>
				<Breadcrumb>
					{sections &&
						sections.map(section => {
							return (
								<Breadcrumb.Item key={section.key}>
									{section.link ? (
										<a
											href="#"
											onClick={section.onClick}
										>
											{section.content}
										</a>
									) : (
										section.content
									)}
								</Breadcrumb.Item>
							);
						})}
				</Breadcrumb>
				<Title
					level={5}
					style={{ paddingTop: 10 }}
				>
					{title}
				</Title>
			</Header>
		);
	};

	render() {
		const { location, history } = this.props.children.props;
		var setBreadcrumbNavigationBar = this.setBreadcrumbNavigationBar.bind(this);
		var setBreadcrumbTitle = this.setBreadcrumbTitle.bind(this);
		return (
			<Layout style={{ minHeight: '100vh' }}>
				<Drawer
					location={location}
					history={history}
					collapsed={this.state.collapsed}
					handleOnCollapse={() => this.handleOnCollapse()}
				/>
				<Layout>
					<NavigationHeader
						location={location}
						history={history}
						collapsed={this.state.collapsed}
						handleOnCollapse={() => this.handleOnCollapse()}
					/>
					<Content style={{ margin: '16px 16px 0' }}>
						{React.cloneElement(this.props.children, { setBreadcrumbNavigationBar: setBreadcrumbNavigationBar, setBreadcrumbTitle: setBreadcrumbTitle })}
					</Content>
				</Layout>
			</Layout>
		);
	}
}

export default NavigationLayout;
