import { connect } from 'react-redux';
import SettingsPage from './SettingsPage';
import { deleteStaff, submitStaff, getStaffById, updateAccount, getAccount, resetSelectedStaff, reset, resetMeta } from '../../actions/SettingsPage/SettingsPage';
import { addFacebokConnection, getFacebookConnections, updatePageSubcribedFieldById } from '../../actions/SettingsPage/Facebook';
import { getTwilioConnections, getTwilioConnectionById, submitTwilioConnection } from '../../actions/SettingsPage/TwilioConnection';

const mapsStateToProps = state => ({
	settingsPageProps: state.settingsPageReducer
});

const mapsDispatchToProps = dispatch => ({
	getAccount() {
		dispatch(getAccount());
	},
	updateAccount(options) {
		dispatch(updateAccount(options));
	},
	getStaffById(options) {
		dispatch(getStaffById(options));
	},
	submitStaff(options) {
		dispatch(submitStaff(options));
	},
	deleteStaff(options) {
		dispatch(deleteStaff(options));
	},
	resetSelectedStaff() {
		dispatch(resetSelectedStaff());
	},
	// Facebook
	addFacebokConnection(options) {
		dispatch(addFacebokConnection(options));
	},
	getFacebookConnections(options) {
		dispatch(getFacebookConnections(options));
	},
	updatePageSubcribedFieldById(options) {
		dispatch(updatePageSubcribedFieldById(options));
	},
	// WhatsApp/Twilio
	submitTwilioConnection(options) {
		dispatch(submitTwilioConnection(options));
	},
	getTwilioConnections(options) {
		dispatch(getTwilioConnections(options));
	},
	getTwilioConnectionById(options) {
		dispatch(getTwilioConnectionById(options));
	},
	resetPage() {
		dispatch(reset());
	},
	resetMeta() {
		dispatch(resetMeta());
	}
});

export default connect(mapsStateToProps, mapsDispatchToProps)(SettingsPage);
