import { combineReducers } from 'redux';
import { connectRouter } from 'connected-react-router';
import { reducer as formReducer } from 'redux-form';

import loginPageReducer from './LoginPage';
import registerPageReducer from './RegisterPage';
import forgotPasswordReducer from './ForgotPasswordPage';
import ticketPageReducer from './TicketPage';
import staffListReducer from './StaffList';
import settingsPageReducer from './SettingsPage';
import staffDetailsPageReducer from './StaffDetailsPage';
import messengerIntegrationPageReducer from './MessengerIntegrationPage';
import reportPageReducer from './ReportPage';

export default history =>
	combineReducers({
		form: formReducer,
		router: connectRouter(history),
		loginPageReducer,
		registerPageReducer,
		forgotPasswordReducer,
		ticketPageReducer,
		staffListReducer,
		settingsPageReducer,
		staffDetailsPageReducer,
		messengerIntegrationPageReducer,
		reportPageReducer
	});