import React, { Component } from 'react';
import { TextContainer, Layout, Caption, Card, Avatar, Modal, Banner, SkeletonBodyText, SkeletonDisplayText, Icon } from '@shopify/polaris';
import { SendMajorMonotone, AttachmentMajorMonotone } from '@shopify/polaris-icons';
import { compose } from 'redux';
import { reduxForm } from 'redux-form';
import _ from 'lodash';
import UrlParse from 'url-parse';
import moment from 'moment';
import Dropzone from 'react-dropzone';
import ioClient from 'socket.io-client';
import { withAlert } from 'react-alert';
import GeneralHelper from '../../../helpers/general';
import config from './../../../config';
import FileTypes from '../../../constants/FileTypes';
import LocalStorageHelper from '../../../helpers/local_storage';
import './index.css';
import MessageBubble from './MessageBubble';

const FILE_SIZE_MB = 4; //mb 5mb is maximum for twilio (https://www.twilio.com/docs/sms/accepted-mime-types)
const MAX_FILE_SIZE = FILE_SIZE_MB * 1e+6; //converted the mb to byte

// show this if the chatview is loading.
const skeletonConversation = () => {
	return (
		<Layout>
			<Layout.Section>
				<Card sectioned>
					<TextContainer>
						<SkeletonDisplayText size="extraLarge" />
						<SkeletonBodyText lines={2}/>
						<SkeletonBodyText lines={4}/>
						<SkeletonBodyText lines={8}/>
						<SkeletonBodyText lines={4}/>
					</TextContainer>
				</Card>
			</Layout.Section>
		</Layout>
	);
};

class TicketList extends Component {
	constructor(props){
		super(props);
		this.state = {
			inputText: '',
			file: null,
			ticket_id: null,
			loggedIn: null
		};
		this.socket = ioClient(config.socket.host, {
			path: config.socket.path,
			forceNew: true
		});
	}

	componentDidMount() {
		const { getTicketById, getTwilioMessages, getTwilioMessageById,
			getUsers, history, location } = this.props;
		const { query } = UrlParse(location.search, true);
		const { socket } = this;
		const { ticket_id } = query;
		// const loggedInUser = JSON.parse(localStorage.getItem('app'));
		if (!ticket_id) {
			return history.push('/tickets?type=all');
		}

		const loggedIn = LocalStorageHelper.getLoggedIn();
		const loggedInType = LocalStorageHelper.getLoginType();

		// get ticket by id .
		getTicketById({ ticket_id });

		// get messages
		getTwilioMessages({ ticket_id });

		// get users list, so it ticket can change the asignee
		getUsers();

		socket.on('connection', () => { });
		socket.on('valid_connection', () => {
			this.socketJoinTicket({ ticket_id, user_id: loggedIn.user_id || loggedIn._id, staff_id: loggedIn._id });
			// open chat emiting to inform that all messages are read.
			// socket.emit('open_chat',{ session_id, user_id: loggedInUserId  });
			this.messagesEnd && this.messagesEnd.scrollIntoView({ behavior: 'auto' });
		});
		this.socket.on('message', async message => {
			// RECEIVER
			if (message) {
				getTwilioMessageById({ message_id: message.twilio_message_id });
				// emit || receiver able read the message.
				socket.emit('open_chat',{ user_id: loggedIn.user_id || loggedIn._id, ticket_id, staff_id: loggedIn._id  });

				// get twillio message by id.
				this.messagesEnd.scrollIntoView({ behavior: 'smooth' });
			}
		});


		this.setState({
			loggedIn: {...loggedIn, loggedInType },
			ticket_id
		});
	}

	componentDidUpdate(prevProps) {
		const { location: prevLocation, ticketPageProps: prev_ticketPageprops } = prevProps;
		const { location, getTwilioMessages, getTicketById, ticketPageProps } = this.props;
		const { query } = UrlParse(location.search, true);
		const { query: prevQuery } = UrlParse(prevLocation.search, true);
		const { ticket_id } = query;
		if (ticket_id && ticket_id !== prevQuery.ticket_id) {
			
			getTwilioMessages({ ticket_id });
			getTicketById({ ticket_id });
			this.socketJoinTicket({ ticket_id });
			this.messagesEnd.scrollIntoView && this.messagesEnd.scrollIntoView({ behavior: 'auto' });

			this.setState({
				ticket_id
			});
		}		
	}

	componentWillUnmount() {
		this.socket.disconnect();
		this.props.resetTicket();
	}

	
	socketJoinTicket = ({ ticket_id, user_id, staff_id }) => {
		const { socket } = this;
		socket.emit('join_ticket', { ticket_id });
		socket.emit('open_chat',{ user_id, ticket_id, staff_id  });

	}

	socketConnection = () => {
		const { socket } = this;
		const { getTwilioMessageById, location } = this.props;
		const { query } = UrlParse(location.search, true);
		const { ticket_id } = query;
		socket.on('connection', () => { });
		socket.on('valid_connection', () => {
			socket.emit('join_ticket', { ticket_id });
			// open chat emiting to inform that all messages are read.
			// socket.emit('open_chat',{ session_id, user_id: loggedInUserId  });
			this.messagesEnd.scrollIntoView({ behavior: 'auto' });
		});
		this.socket.on('message', async message => {
			// RECEIVER
			if (message) {
				getTwilioMessageById({ message_id: message.twilio_message_id });
				// get twillio message by id.
				this.messagesEnd.scrollIntoView({ behavior: 'smooth' });
			}
		});
	}

	handleChange = (value) => {
		this.setState({
			inputText: value
		});
	}
	
	handleSendMessage = async() => {
		const { inputText, ticket_id, file } = this.state;
		if (inputText) {
			// text message type
			await this.props.sendMessage({ message: inputText, ticket_id });

		} else {
			await this.props.sendMessage({ ticket_id, file, filename: file.name, mime: file.type });
		}
		this.setState({
			inputText: '',
			file: null
		});
	}

	onKeyDownHandle = (e) => {
		if(e.keyCode == 13 && e.shiftKey == false) {
			e.preventDefault();
			this.handleSendMessage();
		}
	}

	onRejectedAttachFile = (files) => {
		if (!_.isEmpty(files)) {
			const file = files[0];
			if (file && file.errors[0].code === 'file-too-large') {
				this.props.alert.show(`File too large. Maximum file size is ${FILE_SIZE_MB} MB`, { type: 'error' });
			}
		}
	}

	attachFile = (files) => {
		if (files){
			this.setState({
				file: files[0]
			});
		}
	}

	dropZoneAttachment = () => (
		<Dropzone 
			onDrop={this.attachFile}
			accept={FileTypes.image.concat(FileTypes.document).concat(FileTypes.video).concat(FileTypes.audio).join(',')}
			onDropRejected={this.onRejectedAttachFile}
			multiple={false}
			maxSize={MAX_FILE_SIZE}
		>
			{({getRootProps, getInputProps}) => (
				<section style={{ display: 'flex',  width: '10%', cursor: 'pointer', alignItems: 'center', justifyContent: 'center', marginBottom: 5 }}>
					<div {...getRootProps()}>
						<input {...getInputProps()} />
						<Icon 
							source={AttachmentMajorMonotone} 
							color="primary"
						/>
					</div>
				</section>
			)}
		</Dropzone>
	)


	closeAttachmentPreview = () => {
		this.setState({ file: '' });
	}

	renderPreviewModal = () => {
		const { file } = this.state;
		const { type, name } = file;
		return (
			<Modal
				open={Boolean(file)}
				onClose={() => this.closeAttachmentPreview()}
				title="Attachment Preview"
				primaryAction={{
					content: 'Send',
					onAction: () => this.handleSendMessage()
				}}
				secondaryActions={[
					{
						content: 'Cancel',
						onAction: () => this.closeAttachmentPreview()
					
					}
				]}
			>
				<Modal.Section>
					<div style={{ textAlign: 'center' }}>
						{type.includes('image') 
							? <img 
								src={URL.createObjectURL(file && file)}
								alt={ file && file.name}
								style={{  maxWidth: 250, maxHeight: 250 }}
							  />
							: type.includes('video')
								? <video
									src={URL.createObjectURL(file)}
									controls
									style={{ maxWidth: 250, maxHeight: 250 }}
								  />
								: 
								<p>
									{name}
								</p>
						}
					</div>

				</Modal.Section>
			</Modal>
		);
	}
		
	render() {
		const { inputText, file, loggedIn } = this.state;
		const { updateTicketStatus, ticketPageProps } = this.props;
		const { twilio_messages, ticket, loadingSelectedTicket, loadingUpdateTicket, loadingSendMessage, loadingMessages } = ticketPageProps;
		const { renderPreviewModal, handleSendMessage, dropZoneAttachment } = this;
		const customer_phone_number = _.get(ticket, 'twilio_contact.phone');
		const customer_name = _.get(ticket, 'twilio_contact.name');
		const ticket_status = _.get(ticket, 'status');
		const loggedInType = _.get(loggedIn, 'loggedInType'); //user or staff
		const isStaffLoggedIn = loggedInType === 'staff' ? true : false;
		const loggedInId = _.get(loggedIn, '_id');
		const ticketAssigneeId = _.get(ticket, 'staff_id');
		const isAssigned = loggedInId === ticketAssigneeId; //identify if the ticket is assigned to the logged in staff.
		const ticketIsPendingAcceptance = isAssigned && ticket_status === 'pending_acceptance'; //check if the ticket is assigned to the logged in staff & ticket status is pending_acceptance
		const ticketIsResolved = ticket_status === 'resolved';
		const showChatInput = isAssigned && !ticketIsResolved && !ticketIsPendingAcceptance;
		const messagesGroupByDate = twilio_messages && _(twilio_messages)
			.groupBy(message => message && moment(message.created_at).format('ddd, D MMM YYYY'))
			.map((value, key) => ({ dateTime: key, messages: value }))
			.value();
		if (loadingSelectedTicket || loadingMessages) {
			return (
				skeletonConversation()
			);
		}
		return (
			<Card>
				<div>
					<Card>
						<div style={styles.conversationHeader}>
							<Avatar 
								name={customer_phone_number}
								customer
							/>
							<div style={{ marginLeft: 10 }}>
								{customer_name && <span style={{ fontSize: 15 }}>{customer_name}</span>}
								<span><Caption>{customer_phone_number}</Caption></span>
							</div>
						</div>
						{/* Pending ticket acceptance */}
						{ticketIsPendingAcceptance && 
							// <div style={styles.pendingAcceptance}>
							// 	<Card
							// 		subdued={true} 
							// 		sectioned={true}
							// 		primaryFooterAction={{ 
							// 			content: 'Accept', 
							// 			onAction: () => updateTicketStatus({ ticket_id, status: 'in-progress' }),
							// 			disabled: loadingUpdateTicket
							// 		}}
							// 	>
							// 		<p>
							// 			You have new ticket assigned for you. Accept to start chatting.
							// 		</p>
							// 	</Card>
							// </div>
							<div style={{ width: 400, position: 'absolute', right: 0, top: 0, zIndex: 999, margin: 5}}> 
								{!loadingUpdateTicket && 
								<Banner
									title="Notification"
									status="success"
									onDismiss={() => this.props.history.push('/tickets?type=my')}
									action={{content: 'Accept', onAction: () => updateTicketStatus({ ticket_id: ticket._id, status: 'in-progress' }) }}
								>
									<p>
									New ticket has been assigned to you.
									Click Accept and start chatting.
									</p>
								</Banner>}
							</div>
						}
						<div style={styles.conversation}>
							{messagesGroupByDate && messagesGroupByDate.map((item, index) => {
								return (
									<React.Fragment key={index}>
										<div style={{ textAlign: 'center', fontSize: 11, marginBottom: 5, marginTop: 5 }}>{item.dateTime}</div>
										{_.get(item, 'messages').map((msg, idx) => {
											if (msg) {
												return (
													<MessageBubble
														key={idx}
														message={msg}
														getAssetFileById={this.props.getAssetFileById}
														getTwilioFileById={this.props.getTwilioFileById}
													/>
												);
											}
										})}
									</React.Fragment>
								);})
							}
							<div 
								ref={el => { this.messagesEnd = el; }}
								style={{ float: 'left', clear: 'both' }}
							>
							</div>
						</div>
						{ showChatInput &&
							<div style={styles.inputContainer}>
								<span 
									style={{ width: '90%', marginLeft: 5 }}
								>
									{/* <TextField 
										value={inputText} 
										onChange={ (value) => this.handleChange(value)}
										autoFocus={true}
										multiline={true}
										autoComplete={false}
										placeholder="Send a message."
										disabled={loadingSendMessage}
										// maxLength={300}	
										// showCharacterCount
									/> */}
									<textarea
										type="text"
										autoFocus={true}
										onChange={ (e) => this.handleChange(e.target.value)}
										className="chat-input-text"
										placeholder="Send a message"
										onKeyDown={(e) => this.onKeyDownHandle(e)}
										tabIndex="0"
										value={inputText}
										maxLength="1600" // twilio max characters is 1600 (https://support.twilio.com/hc/en-us/articles/360033806753-Maximum-Message-Length-with-Twilio-Programmable-Messaging)
									/>
								</span>
								{inputText ?
									<span 
										onClick={ 
											() => !loadingSendMessage && handleSendMessage()
										}
										style={{ display: 'flex',  width: '10%', cursor: 'pointer', marginBottom: 5 }}
									>
										<Icon 
											source={SendMajorMonotone} 
											color="primary"
										/>
									</span>
									:
									dropZoneAttachment()
								}
							</div>
						}
					</Card>
				</div>
				{file && renderPreviewModal()}
			</Card>
		);
	}
}


const styles = {
	conversationHeader: {
		display: 'flex',
		alignItems: 'center',
		height: 60,
		background: '#173630',
		padding: 15,
		color: 'white'
	},
	conversation: {
		height: '400px',
		display: 'flex',
		flexDirection: 'column',
		overflowY: 'auto',
		overflowX: 'hidden',
		paddingTop: 20,
		paddingBottom: 20,
		backgroundImage: `url(${GeneralHelper.getImageBasePath()}/images/bg.png)`
	},
	inputContainer: {
		display: 'flex',
		backgroundColor: '#f1f3f4',
		borderTop: '0.1px solid #d4d4d4',
		padding: '5px 0px 0px 0px'
	},
	pendingAcceptance: {
		background: '#173630',
		padding: 5
	}
};
export default compose(
	withAlert(),
	reduxForm({
		form: 'TicketList'
	})
)(TicketList);