/* eslint-disable jsx-a11y/alt-text */
import React, { Component } from 'react';
import { Card, Avatar, TextField } from '@shopify/polaris';

class InstagramPostComment extends Component {
	constructor(props) {
		super(props);
		this.state = {
			instagramCommentValue: ''
		};
	}

	handleInstagramCommentValue = value => {
		this.setState({ instagramCommentValue: value });
	};

	render() {
		const { instagramCommentValue } = this.state;
		const sampleData = [
			{
				comments: [
					{
						created_at: '2020-06-03T08:05:46.311Z',
						date: '2020-06-03T08:05:43.000Z',
						flow: 'inbound',
						from: 'Cong TV',
						message: 'Hello there jay'
					},
					{
						created_at: '2020-06-03T08:05:46.311Z',
						date: '2020-06-03T08:05:43.000Z',
						flow: 'outbound',
						from: 'Jay Cruzada',
						message: 'Hello there morlak'
					},
					{
						created_at: '2020-06-03T08:05:46.311Z',
						date: '2020-06-03T08:05:43.000Z',
						flow: 'inbound',
						from: 'Cong TV',
						message:
							'Non amet proident pariatur est non. Laboris aliquip sit nulla enim enim reprehenderit veniam sunt reprehenderit consectetur mollit irure. Reprehenderit adipisicing proident irure consectetur cillum proident incididunt aliquip sint.'
					},
					{
						created_at: '2020-06-03T08:05:46.311Z',
						date: '2020-06-03T08:05:43.000Z',
						flow: 'inbound',
						from: 'Jay Cruzada',
						message: 'Up? naas babaw'
					}
				],
				dateTime: 'Wed, 3 June 2020',
				pagePhoto: 'https://picsum.photos/200/300',
				pagePostedPhoto: 'https://picsum.photos/600/600',
				pageName: 'Morlak STORE',
				description:
					'Sint nulla ad anim eu ullamco pariatur ullamco aliqua cillum irure aliqua mollit ipsum voluptate. Exercitation aliqua et duis labore veniam do.Nulla aute amet ex consectetur proident magna est mollit cillum enim. Est do elit et mollit dolore. Amet in voluptate id commodo Lorem sint quis culpa sit anim.'
			}
		];

		return (
			<React.Fragment>
				<Card>
					<div style={styles.instagramCommentHeader}>
						<div style={styles.avatarAndTitleHolder}>
							<div>
								<Avatar
									customer
									source={sampleData[0].pagePhoto}
								/>
							</div>
							<div style={styles.titleHolder}>{sampleData[0].pageName}</div>
						</div>
					</div>
					<div style={styles.bgWhite}>
						<div style={styles.commentSectionHolder}>
							<div className="ig-post-details">
								<div style={{ marginBottom: 12 }}>
									<img
										src={sampleData[0].pagePostedPhoto}
										style={styles.igPostImage}
									/>
								</div>
								<div>
									<p style={styles.igPostDescription}>
										<strong style={styles.name}>{sampleData[0].pageName} </strong>
										{sampleData[0].description}
									</p>
								</div>
							</div>
							<div
								className="ig-users-comment"
								style={styles.userCommentHolder}
							>
								<div style={styles.comment}>
									{sampleData[0].comments.map((v, i) => {
										return (
											<React.Fragment key={i}>
												<div style={{ marginTop: 20 }}>
													<div>
														<strong style={styles.name}>{v.from} </strong>
														<span style={styles.commentMessage}>{v.message}</span>
													</div>
													<div style={styles.agoHolder}>
														<p style={styles.timeAgo}>2h</p>
													</div>
												</div>
											</React.Fragment>
										);
									})}
								</div>
								<div style={styles.inputHolder}>
									<div style={styles.inputBorder}>
										<div
											className="textfield-holder"
											style={styles.textFieldHolder}
										>
											<TextField
												placeholder="Add comment..."
												value={instagramCommentValue}
												onChange={this.handleInstagramCommentValue}
												multiline
											/>
										</div>
										<div style={styles.buttonHolder}>
											<button
												className="ig-post-btn"
												style={styles.postBtn}
											>
												POST
											</button>
										</div>
									</div>
								</div>
							</div>
						</div>
					</div>
				</Card>
			</React.Fragment>
		);
	}
}

const styles = {
	instagramCommentHeader: {
		backgroundColor: 'white',
		padding: '7px 15px 7px 15px',
		borderBottom: '1px solid #e6e6e6'
	},
	avatarAndTitleHolder: {
		display: 'flex',
		flexDirection: 'row',
		alignItems: 'center',
		padding: 8
	},
	titleHolder: {
		marginLeft: 15,
		fontWeight: 500,
		fontSize: '14px'
	},
	bgWhite: {
		backgroundColor: 'white'
	},
	commentSectionHolder: {
		padding: 30
	},
	igPostImage: {
		width: '100%',
		borderRadius: 1
	},
	igPostDescription: {
		lineHeight: 1.4,
		fontSize: '13px',
		marginLeft: '3px'
	},
	name: {
		fontSize: 12,
		color: '#333'
	},
	userCommentHolder: {
		marginTop: 5,
		height: '600px'
	},
	comment: {
		marginTop: 12,
		flexDirection: 'column',
		fontSize: '14px'
	},
	commentMessage: {
		fontSize: '13px',
		marginLeft: '3px'
	},
	agoHolder: {
		position: 'relative'
	},
	timeAgo: {
		position: 'absolute',
		top: -4,
		fontSize: 10,
		color: '#777'
	},
	inputHolder: {
		marginTop: 25,
		flexDirection: 'column'
	},
	inputBorder: {
		display: 'flex',
		flexDirection: 'row',
		border: '1px solid #e6e6e6'
	},
	textFieldHolder: {
		marginRight: 'auto',
		flex: 1
	},
	buttonHolder: {
		alignSelf: 'center',
		marginRight: '10px'
	},
	postBtn: {
		color: '#33adff',
		fontWeight: 600,
		backgroundColor: 'inherit',
		outline: 'none',
		border: 'none'
	}
};
export default InstagramPostComment;
