import React, { Component } from 'react';
import PropTypes from 'prop-types';
import { Button, Form, Caption, Card, FormLayout, TextField, Modal, TextContainer, Icon } from '@shopify/polaris';
import { compose } from 'redux';
import { Field, reduxForm } from 'redux-form';
import { withAlert } from 'react-alert';
import { renderFormInput, validateRequired, validateEmail } from '../../../helpers/redux_form';
import Loading from '../../../components/loading';

class StaffForm extends Component {
	componentDidUpdate(prevProps) {
		const { loadingStaffModal: prevLoading } = prevProps;
		const { loadingStaffModal, staffFormType } = this.props;

		if (prevLoading && prevLoading !== loadingStaffModal && staffFormType === 'updating') {
			this.props.initialize({ ...this.props.data });
		}
	}

	render() {
		const { data: selectedStaff, pristine, submitting, invalid, loadingStaffModal, loadingSubmit, isOpen, submitStaff, modalHandle, staffFormType } = this.props;
		return (
			<Card>
				<Modal
					open={isOpen}
					onClose={e => modalHandle(false)}
					title={staffFormType === 'adding' ? 'Add Staff' : 'Update Staff'}
					primaryAction={{
						content: 'Save',
						disabled: pristine || submitting || invalid || loadingSubmit,
						onAction: this.props.handleSubmit(data => submitStaff({ ...data, selectedStaff }))
					}}
					// secondaryActions={[{
					// 	content: 'Learn more',
					// 	onAction: (e) => modalHandle
					// }]}
					size="Full"
				>
					<Modal.Section>
						{loadingStaffModal ? (
							<Loading />
						) : (
							<Form onSubmit={this.props.handleSubmit(data => submitStaff({ ...data, selectedStaff }))}>
								<FormLayout>
									<Field
										name="name"
										label="Full Name"
										component={renderFormInput}
										validate={[validateRequired]}
									/>
									<Field
										name="email"
										label="Email Address"
										component={renderFormInput}
										validate={[validateRequired, validateEmail]}
									/>
									<Field
										name="old_password"
										label="Password"
										component={renderFormInput}
										validate={staffFormType === 'adding' && [validateRequired]}
										type="password"
									/>
									<Field
										name="password"
										label="Confirm Password"
										validate={staffFormType === 'adding' && [validateRequired]}
										component={renderFormInput}
										type="password"
									/>
								</FormLayout>
							</Form>
						)}
					</Modal.Section>
				</Modal>
			</Card>
		);
	}
}

export default compose(
	withAlert(),
	reduxForm({
		form: 'StaffForm'
	})
)(StaffForm);
