import React, { useEffect, useState } from 'react';
import moment from 'moment';
import { Modal, Icon } from '@shopify/polaris';
import {
	CircleDownMajorMonotone, ClockMinor
} from '@shopify/polaris-icons';

import Loading from '../../../components/loading';
import FileTypes from '../../../constants/FileTypes';


const styles = {
	container: {
		display: 'flex'
	},
	bubble: {
		maxWidth: 230,
		overflowWrap: 'break-word',
		whiteSpace: 'pre-wrap',
		padding: '5px 8px 3px 8px',
		marginBottom: 15,
		boxShadow: '0 2px 5px 0 rgba(0, 0, 0, 0.2), 0 1px 2px 0 rgba(0, 0, 0, 0.19)',
		marginLeft: '5px',
		marginRight: '5px'
	},
	message: {
		margin: 0
	},
	dateTime: {
		display: 'flex',
		justifyContent: 'flex-end',
		alignContent: 'center',
		alignItems: 'center',
		color: '#8c8c8c',
		fontSize: 10
	},
	mediaContainer: {
		maxWidth: 200,
		// height: 'auto',
		cursor: 'pointer'
	},
	fileAttachmentContainer: {
		display: 'flex',
		icon: {
			padding: 10,
			cursor: 'pointer'
		},
		fileType: {
			fontSize: '10px',
			marginTop: -5
		},
		fileName: {
			fontSize: '15px'
		}
	}
};



const MessageBubble = (props) => {
	const { _id, twilio_file_id, twilio_file, flow, created_at, message, outbound_status, loading } = props.message;
	// const { mime } = twilio_file || '';
	const [preview, setPreview] = useState(false);

	useEffect(() => {
		const getFile = async() => (
			props.getTwilioFileById({ message_id: _id })
		);
		if (twilio_file_id && !twilio_file) {
			getFile(); 
		}
	}, [_id, loading, props, twilio_file, twilio_file_id]);


	const modalHandle = (value) => {
		setPreview(value);
	};

	const modalPreview = (file) => {
		return (
			<Modal
				open={preview}
				onClose={() => modalHandle(false)}
				title="Attachment Preview"
				large={true}
				secondaryActions={[
					{
						content: 'Close',
						onAction: () => modalHandle(false)
						
					}
				]}
			>
				<Modal.Section>
					<div style={{ textAlign: 'center' }}>
						<img 
							src={file.remote_url || file.file}
							// style={{  maxWidth: 500, maxHeight: 300 }}
							alt="attached"
						/>
					</div>

				</Modal.Section>
			</Modal>
		);
	};

	return (
		<div
			style={{ ...styles.container, justifyContent: flow === 'outbound' ? 'flex-end' : 'flex-start' }}
		>
			{preview && modalPreview(twilio_file)}
			<div
				style={{
					...styles.bubble,
					background: flow === 'outbound' ? '#ceff91' : '#F4F6F8',
					borderRadius: flow === 'outbound' ? '10px 10px 0px 10px' : '10px 10px 10px 0px'
				}}
			>
				{/* {flow === 'outbound' &&
					<span style={{ fontSize: 10 }}>"Staff name</span>
				} */}
				{loading ? <Loading />
					:
					!twilio_file
						? <p style={styles.message}>{message}</p>
						: 
						<RenderAttachment
							type={twilio_file.mime}
							file={twilio_file}
							modalHandle={modalHandle}
							filename={message}
						/> 
				}
				<div
					style={styles.dateTime}
				>
					<span>{moment(created_at).format('hh:mm A')}</span>
					{flow === 'outbound' && outbound_status ==='sending' && <span style={{ fontSize: 11, width: 15, height: 15}}> 
						<Icon 
							source={ClockMinor} 
							color="inkLighter" 
						/>
					</span>}
				</div>
			</div>
		</div>
	);
};

const RenderAttachment = ({ type, file, modalHandle, filename }) => {
	const downloadFile = ({ file, filename }) => {
		let tempLink = document.createElement('a');
		tempLink.href= file;
		tempLink.setAttribute('target', '_blank');
		tempLink.setAttribute('download', filename || 'attachment_download');
		tempLink.click();
	};

	if (type.includes('image')) {
		if (file) {
			return (
				<img
					onClick= {() => modalHandle(true)}
					style={styles.mediaContainer}
					src={file.remote_url || file.file}
					alt={type}
				/>
			);
		}

	} else if (type.includes('video')) {
		if (file) {
			return (
				<video
					style={styles.mediaContainer}
					src={file.remote_url || file.file}
					controls
				/>
			);
		}
	} else if (type.includes('audio')) {
		return (
			<audio 
				style={styles.mediaContainer}
				controls
			>
				<source 
					src={file.remote_url || file.file}
					type="audio/mpeg" 
				/>
			</audio>
		);
	}
	else if (FileTypes.document.indexOf(type) !== -1) {
		return (
			<div style={styles.fileAttachmentContainer}>
				<span 
					style={styles.fileAttachmentContainer.icon}
					onClick={ () => downloadFile({ filename, file: file.remote_url || file.file })}
				>	
					<Icon 
						source={CircleDownMajorMonotone} 
					/>
				</span>
				<div style={{ display: 'flex', flexDirection: 'column', justifyContent: 'center' }}>
					<span style={styles.fileAttachmentContainer.fileName}>{filename}</span>
					<span style={styles.fileAttachmentContainer.fileType}>{type}</span>
				</div>
			</div>
		);
	} else {
		return <span>File not supported.</span>;
	}
};

export default MessageBubble;