import React, { Component } from 'react';
import _ from 'lodash';
import { compose } from 'redux';
import { withAlert } from 'react-alert';
import { Card, DatePicker, Divider, Image, Input, Select, Space } from 'antd';
import Customer from './Customer';
import tickets from '../../../helpers/api/admin/tickets';
const { Search } = Input;
const { Option } = Select;

class CustomerList extends Component {
	constructor(props) {
		super(props);
		this.state = {
			test: 'Hello World',
			filters: {
				customer: '',
				channel: '',
				date: ''
			}
		};
	}
	
	render(){
		const { filters } = this.state;
		const { tickets } = this.props.ticketPageProps;

		const searchResult = () => {
			return tickets.filter((element)=> {
				//If filter state is empty return true.
				const customerFilter = ("" === filters.customer) ? true : element.name.toLowerCase().includes(filters.customer.toLowerCase());
				const channelFilter = ("" === filters.channel) ? true : element.channel === filters.channel;
				const dateFilter = ('' === filters.date) ? true : element.date === filters.date;
				//return data that satisfies filtering
				return (customerFilter && channelFilter && dateFilter);
			});
		};

		const modalHandle = (type, e) => {
			//Check if 'e' has value and return the value else return empty string.
			const value = (e) ? e: '';
			const newFilter = filters;
			switch(type){
				case 'customer':
					newFilter.customer = value;
					break;
				case 'channel':
					newFilter.channel = value;
					break;
				case 'date':
					newFilter.date = e.dateString;
					break;
				default:
			}
			this.setState({filters: newFilter});	
		};



		const SearchFilter = 
			<Space
				direction="vertical"
				style={{
					width: '100%'
				}}
			>
				<Search
					placeholder="Search Customer"
					allowClear
					onSearch={(value) => modalHandle('customer', value)}
				/>
				<Space>
					<DatePicker
						placeholder="Search by Date"
						format={"MM-DD-YYYY"}
						onChange={(date, dateString) => {const e = {date, dateString}; modalHandle("date", e);}}
					/>
					<Select
						style={styles.filterBox}
						allowClear
						placeholder="Search by Channel"
						onChange={(value) => modalHandle('channel', value)}
					>
						<Option value="Messenger">Messenger</Option>
						<Option value="Instagram">Instagram</Option>
						<Option value="Email">Email</Option>
						<Option value="WhatsApp">WhatsApp</Option>
					</Select>
				</Space>
			</Space>;

		return(
			<Card
				title={SearchFilter}
				style={{width: '100%', marginRight: '16px'}}
			>
				<div>
					{searchResult().map(customer => {
						customer.img = '/images/generic_facebook_photo.jpeg';
						return <Customer
							key={customer._id}
							customer={customer}
						       />;
					})}
				</div>
			</Card>
		);
	}
}

const styles = {
	filterBox: {
		width: '12em'
	}
};

export default compose(withAlert())(CustomerList);
