import React, { Component } from 'react';
import _ from 'lodash';
import { compose } from 'redux';
import { withAlert } from 'react-alert';
import { Avatar, Button, Card, DatePicker, Divider, Image, Input, Row, Select, Space, Typography } from 'antd';
import Icon from '@ant-design/icons';
import { Link } from 'react-router-dom';
import GeneralHelper from '../../../helpers/general';
const { Title } = Typography;
class ChatBox extends Component {
	constructor(props) {
		super(props);
		this.state = {
			selected: 'NON'
		};
	}
	render(){
		
		const { selected } = this.state;


		return(
			<Card style={{width: '100%', padding: 0, marginRight: '16px'}}>
				<Row
					style={{
						height: '80vh'
					}}
					justify="center"
					align="middle"
				>
					<Space
						direction="vertical"
						align="center"
					>
						<Image
							src={`${GeneralHelper.getImageBasePath()}/images/anndesk-logo.png`}
							preview={false}
							style={{width: '16em', marginBottom: 16}}
						/>
						<Title
							level={3}
							style={{textAlign: 'center'}}
						>
						Click the tickets in the left side to start chat here
						</Title>
					</Space>
				</Row>
			</Card>
		);
	}
}

const styles = {
	filterBox: {
		width: '12em'
	}
};

export default compose(withAlert())(ChatBox);
