import request from '.././request';

export default {
	async getTwilioMessages(ticket_id) {
		const res = await request.get(`/app/twilio-messages?ticket_id=${ticket_id}`);
		return res;
	},
	async getTwilioMessageById(id) {
		const res = await request.get(`/app/twilio-messages/id?id=${id}`);
		return res;
	},
	async createTwilioMessage(body) {
		const res = await request.post(`/app/twilio-messages`, body);
		return res;
	},
	async getFbMessages(ticket_id) {
		const res = await request.get(`/app/facebook-messages?ticket_id=${ticket_id}`);
		return res;
	},
	async getFbMessagesByID(id) {
		const res = await request.get(`/app/facebook-messages/id?id=${id}`);
		return res;
	},
	async createFbMessage(body) {
		const res = await request.post(`/app/facebook-messages`, body);
		return res;
	}
};
