import React, { Component } from 'react';
import { Link } from 'react-router-dom';
import { Button, Form, FormLayout, DisplayText, TextStyle, Layout, Card, Page } from '@shopify/polaris';
import { Checkbox, Row, Image, Space } from 'antd';
import { AbandonedCartMajorMonotone } from '@shopify/polaris-icons';
import { Button as ButtonAD, Divider } from 'antd';
import { GoogleOutlined } from '@ant-design/icons';
import { Field, reduxForm } from 'redux-form';
import { compose } from 'redux';
import { withAlert } from 'react-alert';
import GeneralHelper from '../../helpers/general';
import { renderFormInput, validateEmail, validateRequired } from '../../helpers/redux_form';
import './LoginPage.scss';
import { MessengerChat } from "react-messenger-chat-plugin";

class LoginPage extends Component {
	componentDidUpdate(prevProps) {
		const { meta: prevMeta } = prevProps.loginPageProps;
		const { meta } = this.props.loginPageProps;

		if (meta && meta !== prevMeta) {
			const alertOptions = {
				type: meta.code === 200 ? 'success' : 'error',
				...(meta.code === 40399 && { timeout: 0 })
			};

			this.props.alert.show(meta.message, alertOptions);
			this.props.resetMeta();
		}
	}

	componentWillUnmount() {
		this.props.resetPage();
	}
	render() {
		const { pristine, submitting, invalid, loadingSubmit } = this.props;
		const checkbox = (e) => {
			return (e.target.checked);
		};
		return (
			<Row
				style={{
					height: '92vh'
				}}
				justify="center"
				align="middle"
			>
				<MessengerChat
					pageId="286645431852598"
					language="sv_SE"
					themeColor={"#000000"}
					bottomSpacing={300}
					loggedInGreeting="loggedInGreeting"
					loggedOutGreeting="loggedOutGreeting"
					greetingDialogDisplay={"show"}
					debugMode={true}
					onMessengerShow={() => {
						console.log("onMessengerShow");
					}}
					onMessengerHide={() => {
						console.log("onMessengerHide");
					}}
					onMessengerDialogShow={() => {
						console.log("onMessengerDialogShow");
					}}
					onMessengerDialogHide={() => {
						console.log("onMessengerDialogHide");
					}}
					onMessengerMounted={() => {
						console.log("onMessengerMounted");
					}}
					onMessengerLoad={() => {
						console.log("onMessengerLoad");
					}}
				/>
				<div style={{ width: '40em', margin: 'auto' }}>
					<br />
					<Card>
						<center style={{ float: 'center', padding: '50px' }}>
							<DisplayText
								size="large"
								element="h2"
							>
										Sign in to <span style={{ color: '#0088ff' }}>anndesk</span>
							</DisplayText>
							<DisplayText size="small">Enter your details below</DisplayText>
						</center>
						<Card.Section>
							<Form onSubmit={this.props.handleSubmit(data => this.props.login(data))}>
								<FormLayout>
									<Field
										style={{ width: '10px' }}
										name="email"
										label="Email Address"
										component={renderFormInput}
										validate={[validateEmail, validateRequired]}
									/>
									<Field
										name="password"
										label="Password"
										type="password"
										component={renderFormInput}
										validate={[validateRequired]}
									/>
									<div style={{ display: 'flex', alignItems: 'center', justifyContent: 'space-between' }}>
										<Checkbox onChange={checkbox}>Remember Me</Checkbox>
										<span style={{ float: 'right' }}>
											<Link
												id="forgot-password"
												to="/forgot-password"
											>
														Forgot your Password?
											</Link>
										</span>
									</div>
									<br />
									<ButtonAD
										type = "primary"
										block
										style = {{background: '#09509d', borderColor: '#09509d'}}
										onClick={this.props.handleSubmit(data => this.props.login(data))}
									>
												Login
									</ButtonAD>
								</FormLayout>
							</Form>
						</Card.Section>

						{/* Or Divider */}
						<Divider
							style={{paddingLeft: 25, paddingRight: 25}}
							orientation="center"
						>
							<span style={{color: 'grey'}}>or</span>
						</Divider>

						<Card.Section>
							<Button
								fullWidth
								id="google-button"
							>
								<Space>
									<Image
										width={20}
										src={`${GeneralHelper.getImageBasePath()}/images/google-logo.svg`}
										preview={false}
									/>

									<div>Continue with Google</div>
								</Space>
							</Button>
							<center style={{ margin: '2em' }}>
								<TextStyle>
									<span style={{ color: '#9b9ba1' }}>Don't have an account? &nbsp;</span>
									<Link
										style={{ textDecoration: 'none' }}
										to="/register"
									>
										<strong style={{color: '#09509d'}}>Get Started</strong>
									</Link>
								</TextStyle>
							</center>
						</Card.Section>
					</Card>
				</div>
			</Row>
		);
	}
}

export default compose(
	withAlert(),
	reduxForm({
		form: 'LoginPage'
	})
)(LoginPage);
