import request from '.././request';

export default {
	async getTickets(query) {
		const res = await request.get(`/app/tickets`, query);
		return res;
	},
	async getTicketById(id) {
		const res = await request.get(`/app/tickets/id?id=${id}`);
		return res;
	},
	async updateTicketAsigneeById({ id, user_id }) {
		const res = await request.patch(`/app/tickets/id/update-asignee?id=${id}&user_id=${user_id}`);
		return res;
	},
	async updateTicketStatusById({ id, status }) {
		const res = await request.patch(`/app/tickets/id/update-status?id=${id}&status=${status}`);
		return res;
	},
	async createTicketNote({ id, body }) {
		const res = await request.post(`/app/tickets/id/create-note?id=${id}`, body);
		return res;
	}
};
