import * as ActionTypes from '../constants/ActionTypes';

const initialState = {
	loading: false,
	tickets: [],
	twilio_messages: [],
	users: [],
	loadingUsers: false,
	loadingMessage: false,
	loadingTickets: false,
	loadingSelectedTicket: false,
	loadingSendMessage: false,
	loadingNewTicket: false,
	showNewTicketAssignedModal: false,
	fb_messages: []
};

export default function reducer(state = initialState, action) {
	switch (action.type) {
		case ActionTypes.GET_TICKETS_REQUEST:
		case ActionTypes.GET_TICKETS_SUCCESS:
		case ActionTypes.GET_TICKETS_ERROR:
		case ActionTypes.UPDATE_TICKET_STATUS_REQUEST:
		case ActionTypes.UPDATE_TICKET_STATUS_ERROR:
		case ActionTypes.UPDATE_TICKET_STATUS_SUCCESS:
		case ActionTypes.UPDATE_TICKET_ASIGNEE_REQUEST:
		case ActionTypes.UPDATE_TICKET_ASIGNEE_ERROR:
		case ActionTypes.UPDATE_TICKET_ASIGNEE_SUCCESS:
		case ActionTypes.SEND_MESSAGE_ERROR:
		case ActionTypes.SEND_MESSAGE_REQUEST:
		case ActionTypes.SEND_MESSAGE_SUCCESS:
		case ActionTypes.GET_MESSAGES_ERROR:
		case ActionTypes.GET_MESSAGES_REQUEST:
		case ActionTypes.GET_MESSAGES_SUCCESS:
		case ActionTypes.SEND_ATTACHMENT_SUCCESS:
		case ActionTypes.SEND_ATTACHMENT_ERROR:
		case ActionTypes.SEND_ATTACHMENT_REQUEST:
		case ActionTypes.GET_TICKET_BY_ID_SUCCESS:
		case ActionTypes.GET_TICKET_BY_ID_REQUEST:
		case ActionTypes.GET_TICKET_BY_ID_ERROR:
		case ActionTypes.GET_TICKET_STAFFS_SUCCESS:
		case ActionTypes.GET_TICKET_STAFFS_REQUEST:
		case ActionTypes.GET_TICKET_STAFFS_ERROR:
		case ActionTypes.GET_TWILIO_MESSAGE_BY_ID_SUCCESS:
		case ActionTypes.GET_TWILIO_MESSAGE_BY_ID_REQUEST:
		case ActionTypes.GET_TWILIO_MESSAGE_BY_ID_ERROR:
		case ActionTypes.NEW_TICKET_REQUEST:
		case ActionTypes.NEW_TICKET_ERROR:
		case ActionTypes.NEW_TICKET_SUCCESS:
		case ActionTypes.UPDATE_CUSTOMER_SUCCESS:
		case ActionTypes.UPDATE_CUSTOMER_REQUEST:
		case ActionTypes.UPDATE_CUSTOMER_ERROR:
		case ActionTypes.ADD_TICKET_NOTE_REQUEST:
		case ActionTypes.ADD_TICKET_NOTE_SUCCESS:
		case ActionTypes.ADD_TICKET_NOTE_ERROR:
		case ActionTypes.GET_FACEBOOK_MESSAGES_REQUEST:
		case ActionTypes.GET_FACEBOOK_MESSAGES_SUCCESS:
		case ActionTypes.GET_FACEBOOK_MESSAGES_ERROR:
		case ActionTypes.GET_FACEBOOK_MESSAGE_BY_ID_REQUEST:
		case ActionTypes.GET_FACEBOOK_MESSAGE_BY_ID_ERROR:
		case ActionTypes.GET_FACEBOOK_MESSAGE_BY_ID_SUCCESS:

			return {
				...state,
				...action.payload
			};
		case ActionTypes.RESET_TICKETS_PAGE_META:
			return {
				...state,
				meta: null
			};
		case ActionTypes.RESET_MESSAGES:
			return {
				...state,
				twilio_messages: [],
				fb_messages: [],
				staffs: [],
				ticket: []
			};
		case ActionTypes.RESET_TICKETS_PAGE:
			return {
				...initialState
			};
		default:
			return state;
	}
}
