import { push } from 'connected-react-router';
import * as ActionTypes from '../constants/ActionTypes'; 
import API from '../helpers/api';
export function getReports() {
	return async dispatch => {
		dispatch({
			type: ActionTypes.GET_REPORTS_REQUEST,
			payload: {
				loadingReports: true
			}
		});

		// const response = await API.admin.users.getReports();

		const response = {
			data: {
				reports: [
					{
						_id: '001', 
						assignee: 'Jane Doe',
						date: '08-21-2022',
						status: 'Resolved'
					},
					{
						_id: '002', 
						assignee: 'John Doe',
						date: '08-20-2022',
						status: 'In Progress'
					},
					{
						_id: '003', 
						assignee: 'Astelle Von',
						date: '08-21-2022',
						status: 'Not Started'
					},
					{
						_id: '004', 
						assignee: 'Michael Ross',
						date: '08-20-2022',
						status: 'In Progress'
					}
				]
			},
			meta: {
				code: 200
			}
		};
		if (response.meta.code !== 200) {
			return dispatch({
				type: ActionTypes.GET_REPORTS_ERROR,
				payload: {
					loadingReports: false
				}
			});
		}
		const { reports } = response.data;
		dispatch({
			type: ActionTypes.GET_REPORTS_SUCCESS,
			payload: {
				loadingReports: false,
				reports
			}
		});
	};
}

export function resetPage() {
	return async dispatch => {
		return dispatch({
			type: ActionTypes.RESET_REPORTS_PAGE
		});
	};
};

export function resetMeta() {
	return async dispatch => {
		return dispatch({
			type: ActionTypes.RESET_ACCOUNT_PAGE_META
		});
	};
};