import request from '../request';

export default {
	facebook: {
		async getFacebookConnections(query) {
			const res = await request.get(`/app/facebook-connections/me`, query);
			return res;
		},
		async createFacebookConnection(body) {
			const res = await request.post(`/app/facebook-connections`, body);
			return res;
		},
		async updatePageSubcribedFieldById({id, body}){
			const res = await request.patch(`/app/facebook-pages/update-subscribedfield/id?id=${id}`, body);
			return res;
		}
	}
};
