import { connect } from 'react-redux';
import StaffDetailsPage from './StaffDetailsPage';
import { getStaffById, navigateToListPage, onSubmit, resetPage, resetMeta } from '../../actions/StaffDetailsPage';

const mapsStateToProps = state => ({
	pageProps: state.staffDetailsPageReducer
});

const mapsDispatchToProps = dispatch => ({
	getStaffById(id) {
		dispatch(getStaffById(id));
	},
	navigateToListPage() {
		dispatch(navigateToListPage());
	},
	onSubmit(fields) {
		dispatch(onSubmit(fields));
	},
	resetPage() {
		dispatch(resetPage());
	},
	resetMeta() {
		dispatch(resetMeta());
	}
});

export default connect(
	mapsStateToProps,
	mapsDispatchToProps
)(StaffDetailsPage);
