const LocalStorageHelper = {
	getName() {
		try {
			const { admin, staff } = JSON.parse(localStorage.getItem('app'));
			return admin && admin.name || staff && staff.name;
		} catch (e) { }
		return '';
	},
	getLoginType() {
		try {
			const type = JSON.parse(localStorage.getItem('app')).type;
			return type;
		} catch (e) { }
		return '';
	},
	getUserId() {
		try {
			const { admin, staff } = JSON.parse(localStorage.getItem('app'));
			return admin && admin._id || staff && staff.parent_user_id;
		} catch (e) { }
		return '';
	},
	getLoggedIn() {
		try {
			const { admin, staff } = JSON.parse(localStorage.getItem('app'));
			return admin && admin || staff && staff;
		} catch (e) { }
		return '';
	}

};
export default LocalStorageHelper;
