import React, { Component } from 'react';
import { Modal, Card, Form, FormLayout } from '@shopify/polaris';
import { Field, reduxForm } from 'redux-form';
import { renderFormInput, validateRequired, renderCheckbox } from '../../../helpers/redux_form';
import Loading from '../../../components/loading';
import { compose } from 'redux';

class TwilioConnectionForm extends Component {
	componentDidUpdate(prevProps) {
		const { loadingTwilioForm: prevLoading } = prevProps;
		const { loadingTwilioForm } = this.props;
		if (prevLoading && prevLoading !== loadingTwilioForm) {
			this.props.initialize({ ...this.props.data });
		}
	}

	render() {
		const { data: selectedTwilio_connection, loadingTwilioForm, isOpen, modalHandle, submitTwilioConnection, invalid, twilioFormType } = this.props;

		return (
			<Card>
				<Modal
					open={isOpen}
					onClose={() => modalHandle('twilioConnectionForm', false)}
					title={twilioFormType === 'adding' ? 'Add WhatsApp Connection' : 'Edit WhatsApp Connection'}
					primaryAction={{
						content: 'Save',
						disabled: invalid,
						onAction: this.props.handleSubmit(data => submitTwilioConnection({ ...data, selectedTwilio_connection }))
					}}
				>
					<Modal.Section>
						{loadingTwilioForm ? (
							<Loading />
						) : (
							<Form onSubmit={this.props.handleSubmit(data => submitTwilioConnection({ ...data, selectedTwilio_connection }))}>
								<FormLayout>
									<Field
										name="name"
										label="Connection Name"
										component={renderFormInput}
										validate={[validateRequired]}
									/>
									<Field
										name="enabled"
										label="Enabled Status"
										component={renderCheckbox}
										type="checkbox"
									/>
									<Field
										name="account_sid"
										label="Account SID"
										component={renderFormInput}
										validate={[validateRequired]}
									/>
									<Field
										name="auth_token"
										label="Account Token"
										component={renderFormInput}
										validate={[validateRequired]}
									/>
								</FormLayout>
							</Form>
						)}
					</Modal.Section>
				</Modal>
			</Card>
		);
	}
}
export default compose(reduxForm({ form: 'TwilioConnectionForm' }))(TwilioConnectionForm);
