import request from '../request';

export default {
	async getAccount(query) {
		const res = await request.get(`/app/account/me`, query);
		return res;
	},
	async updateAccount(query) {
		const res = await request.patch(`/app/account/me`, query);
		return res;
	},
	async updateStatus({ status }) {
		const res = await request.patch(`/app/account/update-status/me?status=${status}`);
		return res;
	}
};
