import React, { Component } from 'react';
import { Link } from 'react-router-dom';
import { Button, Form, FormLayout, DisplayText, TextStyle, Layout, Card, Modal, TextContainer } from '@shopify/polaris';
import { Checkbox } from 'antd';
import { Field, reduxForm } from 'redux-form';
import { compose } from 'redux';
import { withAlert } from 'react-alert';
import { renderFormInput, validateRequired, validateEmail } from '../../helpers/redux_form';

class RegisterPage extends Component {
	constructor(props) {
		super(props);
		this.state = {
			success: false
		};
	}

	componentDidUpdate(prevProps) {
		const { meta: prevMeta } = prevProps.registerPageProps;
		const { meta } = this.props.registerPageProps;

		if (meta && meta !== prevMeta) {
			const alertOptions = {
				type: meta.code === 200 ? 'success' : 'error',
				...(meta.code === 40399 && { timeout: 0 })
			};
			if (meta.code === 200) {
				this.setState({ success: true });
			}

			this.props.alert.show(meta.message, alertOptions);
			this.props.resetMeta();
		}
	}

	componentWillUnmount() {
		this.props.resetPage();
	}

	render() {
		// const { registerPageProps } = this.props;
		const { pristine, submitting, invalid, loadingSubmit } = this.props;
		const checkbox = (e) => {
			return (e.target.checked);
		};
		return (
			<Layout>
				<Layout.Section>
					<div style={{ width: '25%', margin: 'auto' }}>
						{/* <center>
							<DisplayText size="large" element="h2">
								Welcome
							</DisplayText>
							<DisplayText size="small">Create your account</DisplayText>
						</center> */}
						<br />
						<Card>
							<center style={{ float: 'center', padding: '50px' }}>
								<DisplayText
									size="large"
									element="h2"
								>
									Sign up to <span style={{ color: '#0088ff' }}>anndesk</span>
								</DisplayText>
								<DisplayText size="small">Enter your details below</DisplayText>
							</center>
							<Card.Section>
								<Form onSubmit={this.props.handleSubmit(data => this.props.submitPage(data))}>
									<FormLayout>
										<Field
											name="name"
											placeholder="Full name"
											component={renderFormInput}
											validate={[validateRequired]}
											autoFocus
										/>
										<Field
											name="business_name"
											placeholder="Business Name"
											component={renderFormInput}
											validate={[validateRequired]}
										/>
										<Field
											name="mobile_number"
											placeholder="Mobile Number"
											component={renderFormInput}
											validate={[validateRequired]}
										/>
										<Field
											name="industry"
											placeholder="Industry"
											component={renderFormInput}
											validate={[validateRequired]}
										/>
										<Field
											name="email"
											placeholder="Email Address"
											type="email"
											component={renderFormInput}
											validate={[validateRequired, validateEmail]}
										/>
										<Field
											name="password"
											placeholder="Password"
											component={renderFormInput}
											validate={[validateRequired]}
											type="password"
										/>
										<Field
											name="confirm_password"
											placeholder="Confirm Password"
											component={renderFormInput}
											type="password"
											validate={[validateRequired]}
										/>
										<div style={{ display: 'flex', justifyContent: 'flex-start', alignItems: 'center' }}>
											<Checkbox onChange={checkbox}>
												<TextStyle>
													Accept
													<Link
														style={{ textDecoration: 'none' }}
														to="/login"
													>
														<strong style={{color: '#09509d'}}> Terms and Conditions</strong>
													</Link>
												</TextStyle>
											</Checkbox>
										</div>
										<Button
											primary
											fullWidth
											disabled={pristine || submitting || invalid || loadingSubmit}
											onClick={this.props.handleSubmit(data => this.props.submitPage(data))}
										>
											Create Account
										</Button>
										<center>
											<TextStyle>
												Already have an account? &nbsp;
												<Link
													style={{ textDecoration: 'none' }}
													to="/login"
												>
													<strong style={{color: '#09509d'}}>Sign In</strong>
												</Link>
											</TextStyle>
										</center>
									</FormLayout>
								</Form>
							</Card.Section>
						</Card>
					</div>
				</Layout.Section>
				{/* show only if success creating an account */}
				<Modal
					open={this.state.success}
					onClose={() => this.setState({ success: false })}
					title="Success"
					primaryAction={{
						content: 'Login',
						onAction: () => this.props.history.push('/login')
					}}
				>
					<Modal.Section>
						<TextContainer>
							<p>You have created successfuly your account, you can log in now.</p>
						</TextContainer>
					</Modal.Section>
				</Modal>
			</Layout>
		);
	}
}

export default compose(
	withAlert(),
	reduxForm({
		form: 'RegisterPage'
	})
)(RegisterPage);
