import React, { Component } from 'react';
import _ from 'lodash';
import { compose } from 'redux';
import { withAlert } from 'react-alert';
import { Card, DatePicker, Space } from 'antd';
import { Column } from '@ant-design/plots';
import moment from 'moment';
import tickets from '../../../helpers/api/admin/tickets';

class TicketStatistics extends Component {
	constructor(props) {
		super(props);
		this.state = {
		};
	}


	
	render(){
		const data = [
			{
				month: 'January',
				tickets: 38
			},
			{
				month: 'February',
				tickets: 52
			},
			{
				month: 'March',
				tickets: 61
			},
			{
				month: 'April',
				tickets: 145
			},
			{
				month: 'May',
				tickets: 89
			},
			{
				month: 'June',
				tickets: 232
			},
			{
				month: 'July',
				tickets: 49
			},
			{
				month: 'August',
				tickets: 121
			},
			{
				month: 'September',
				tickets: 72
			},
			{
				month: 'October',
				tickets: 178
			},
			{
				month: 'November',
				tickets: 89
			},
			{
				month: 'December',
				tickets: 98
			}
		];
		const config = {
			data,
			xField: 'month',
			yField: 'tickets',
			label: {
				//  label 
				position: 'middle',
				// 'top', 'bottom', 'middle',
				// 
				style: {
					background: 'red',
					fill: '#FFFFFF',
					opacity: 1
				}
			},
			xAxis: {
				label: {
					autoHide: true,
					autoRotate: false
				}
			},
			meta: {
				type: {
					alias: 'month'
				},
				sales: {
					alias: 'tickets'
				}
			}
		};

		const Title = () => {
			return(<Space style={{display: 'flex', justifyContent: 'space-between'}}>
				Tickets
				<DatePicker
					defaultValue={moment('2022')}
					picker="year"
					allowClear={false}
				/>
			</Space>);};

		return(
			<Card
				style={{width: '70%', marginRight: 16}}
				title={<Title/>}
			>
				<Column {...config} />
			</Card>
		);
	}
}

export default compose(withAlert())(TicketStatistics);
