import React, { Component } from 'react';
import { ResourceList, TextStyle, Card, ResourceItem, Icon, Badge } from '@shopify/polaris';
import { DeleteMinor, EditMinor, ClipboardMinor } from '@shopify/polaris-icons';
import Loading from '../../../components/loading';
import HoverIcon from './HoverIcon';

class TwilioConnectionList extends Component {
	constructor(props) {
		super(props);
		this.state = {
			mousehover: false,
			hoverdata: '',
			data: []
		};
	}
	componentDidUpdate(prevProps) {
		const { loadingTwilioData: prev_loadingTwilioData } = prevProps;
		const { loadingTwilioData, twilioFormType } = this.props;

		if (prev_loadingTwilioData && prev_loadingTwilioData !== loadingTwilioData && twilioFormType === 'updating') {
			this.props.initialize({ ...this.props.twilio_connections });
		}
		if (this.props.twilio_connections.length && prevProps.twilio_connections.length !== this.props.twilio_connections.length) {
			this.setState({
				data: this.props.twilio_connections.map(({ account_sid }) => ({
					hovered: false,
					account_sid
				}))
			});
		}
	}

	handleMouseEnter = account_sid => {
		const data = this.state.data.slice().map(o => {
			if (o.account_sid === account_sid) {
				return { ...o, hovered: true };
			}
			return { ...o };
		});

		this.setState({ data });
	};

	handleMouseLeave = account_sid => {
		const data = this.state.data.slice().map(o => {
			if (o.account_sid === account_sid) {
				return { ...o, hovered: false };
			}
			return { ...o };
		});

		this.setState({ data });
	};
	handledata = Webhook => {
		const isHovered = this.state.mousehover;
		return (
			<div>
				{isHovered ? (
					<div>
						<a>{Webhook}</a>
						<span>
							<Icon source={ClipboardMinor} />
						</span>
					</div>
				) : (
					<div>{Webhook}</div>
				)}
			</div>
		);
	};

	handleClickedData = Webhook => {
		const el = document.createElement('textarea');
		el.value = Webhook;
		document.body.appendChild(el);
		el.select();
		document.execCommand('copy');
		document.body.removeChild(el);
	};

	handleTwilioConnectionId = async id => {
		await this.props.getTwilioConnectionById({ id });
		this.props.modalHandle('twilioConnectionForm', true, id);
	};

	renderTwilioConnections = twilio_connections => {
		const { handledata } = this;

		return (
			<React.Fragment>
				<div style={styles.connectionHeader}>
					<div style={{ display: 'flex' }}>
						<TextStyle variation="strong">{'WhatsApp Connection List'}</TextStyle>
					</div>
				</div>
				<ResourceList
					loading={false}
					resourceName={{ singular: 'connection', plural: 'connections' }}
					items={twilio_connections}
					renderItem={(item, id, i) => {
						const { _id, name, enabled, account_sid, auth_token } = item;
						const btoa = window.btoa(`${account_sid}:${auth_token}`);
						const status = enabled ? 'enabled' : 'disabled';
						const Webhook = btoa.length > 10 ? btoa.substring(0, 20 - 3) + '...' : btoa;
						let key = account_sid,
							hovered = false;
						if (this.state.data.length) {
							hovered = this.state.data[i].hovered;
						}
						return (
							<div
								key={hovered}
								style={styles.connectionsContainer}
								onMouseEnter={() =>
									setTimeout(() => {
										this.handleMouseEnter(account_sid);
									}, 0)
								}
								onMouseLeave={() =>
									setTimeout(() => {
										this.handleMouseLeave(account_sid);
									}, 0)
								}
							>
								<Card>
									<ResourceItem>
										<div style={{ display: 'flex', justifyContent: 'space-between', flexDirection: 'row' }}>
											<table style={{ flexDirection: 'column', width: 560, padding: 10 }}>
												<tbody>
													<tr>
														<td style={{ textAlign: 'left' }}>
															<TextStyle variation="subdued">Connection Name: </TextStyle>
														</td>
														<td style={{ textAlign: 'left' }}>
															<TextStyle variation="strong">{name}</TextStyle>
														</td>
													</tr>
													<tr>
														<td style={{ textAlign: 'left' }}>
															<TextStyle variation="subdued">Status: </TextStyle>
														</td>
														<td style={{ textAlign: 'left' }}>
															<TextStyle variation="strong">{status}</TextStyle>
														</td>
													</tr>
													<tr>
														<td style={{ textAlign: 'left' }}>
															<TextStyle variation="subdued">Webhook route: </TextStyle>
														</td>
														<div onClick={() => this.handleClickedData(btoa)}>
															<HoverIcon
																Webhook={Webhook}
																hovered={hovered}
															/>
														</div>
													</tr>
												</tbody>
											</table>

											<div
												onClick={() => this.handleTwilioConnectionId(_id)}
												style={{ display: 'flex', alignItems: 'center' }}
											>
												<span style={{ marginRight: 10 }}>
													<Icon
														source={EditMinor}
														color="primary"
													/>
												</span>
											</div>
										</div>
									</ResourceItem>
								</Card>
							</div>
						);
					}}
				/>
			</React.Fragment>
		);
	};

	render() {
		const { twilio_connections, loadingTwilioConnections } = this.props;
		return (
			<div style={{ width: 600 }}>
				{loadingTwilioConnections ? (
					<Loading />
				) : twilio_connections.length <= 0 ? (
					<div style={{ textAlign: 'center', marginTop: 10 }}>
						<TextStyle variation="strong">No twilio connection found.</TextStyle>
					</div>
				) : (
					this.renderTwilioConnections(twilio_connections)
				)}
			</div>
		);
	}
}
const styles = {
	container: {
		display: 'flex',
		justifyContent: 'space-between'
	},
	connectionsContainer: {
		// display: 'flex',
		// justifyContent: 'space-between',
		// alignItems: 'center'
	},
	connectionHeader: {
		padding: 10,
		display: 'flex',
		width: '100%',
		justifyContent: 'space-between',
		borderBottom: '1px solid black'
	}
};
export default TwilioConnectionList;
