import React, { Component } from 'react';
import _ from 'lodash';
import { compose } from 'redux';
import { withAlert } from 'react-alert';
import { Avatar, Button, Card, Image, Row, Space, Typography } from 'antd';
import Icon from '@ant-design/icons';
import { Link } from 'react-router-dom';
import GeneralHelper from '../../../helpers/general';
const { Text, Title, Paragraph } = Typography;
class Staff extends Component {
	constructor(props) {
		super(props);
		this.state = {
			
		};
	}
	render(){

		return(
			<Row
				style={{
					height: '82vh'
				}}
				justify="center"
				align="middle"
			>
				<Card style={styles.mainHeader}>
					<Typography>
						<Space
							direction="vertical"
							align="center"
						>
							<Image
								src={`${GeneralHelper.getImageBasePath()}/images/caution.svg`}
								preview={false}
							/>
							<Title level={2}>Account Pending</Title>  
							<Paragraph style={{fontSize: 18}}>
            Your account is pending for approval. We will send out an email notification once your
            account is ready. For the mean time, please setup your Staff accounts in the <span style={{fontWeight: 'bold'}}>Settings</span> page.
							</Paragraph>
							<Button
								size="large"
								style={{background: '#f9e45b', borderColor: '#f9e45b', boxShadow: "0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19)"}}
							>
								<Link
									to="/settings"
									style={{ width: '10em'}}
								><strong style={{color: 'black'}}>Go to Settings</strong></Link></Button>
						</Space>
					</Typography>
				</Card>
			</Row>
		);
	}
}

const styles = {
	mainHeader: {
		paddingLeft: '35em',
		paddingRight: '35em',
		display: 'flex',
		justifyContent: 'center',
		alignItems: 'center',
		height: '60em',
		width: '100%'
	}
};

export default compose(withAlert())(Staff);
