import { push } from 'connected-react-router';
import * as ActionTypes from '../constants/ActionTypes';
import API from '../helpers/api';
import FileHelper from '../helpers/file';
import LocalStorageHelper from '../helpers/local_storage';


const MAX_FILE_LOAD_RETRIES = 50;


export function getTickets({ type, ticket_id }) {
	return async dispatch => {
		const optionsQuery = [`type=${type}`];
		if (ticket_id) {
			optionsQuery.push(`ticket_id=${ticket_id}`);
		}
		dispatch(push(`/tickets?${optionsQuery.join('&')}`));

		dispatch({
			type: ActionTypes.GET_TICKETS_REQUEST,
			payload: {
				loadingTickets: true
			}
		});
		const logInType = LocalStorageHelper.getLoginType();
		let response;
		if (logInType === 'admin') {
			response = await API.admin.tickets.getTickets({ type });
		} else {
			response = await API.staff.tickets.getTickets({ type });
		}
		if (response.meta.code !== 200) {
			return dispatch({
				type: ActionTypes.GET_TICKETS_ERROR,
				payload: {
					meta: response.meta,
					loadingTickets: false
				}
			});
		}
		
	
		const { tickets } = response.data;
		dispatch({
			type: ActionTypes.GET_TICKETS_SUCCESS,
			payload: {
				tickets: []
			}
		});
		dispatch({
			type: ActionTypes.GET_TICKETS_SUCCESS,
			payload: {
				tickets,
				loadingTickets: false
			}
		});

	};
}

