export function getTwilioMessageById({ message_id }) {
	return async (dispatch, getState) => {
		const { twilio_messages } = getState().ticketPageReducer;
		// const copyTwilioMessagesOld = [...twilio_messages];
		// const getIndex = copyTwilioMessagesOld.findIndex(x => x._id === message_id);
		// const copyMessage = {...copyTwilioMessagesOld[getIndex]};
		// if (getIndex !== -1 && twilio_messages[getIndex].twilio_file_id	 && !twilio_messages[getIndex].twilio_file) {
		// 	copyMessage.loading = true;
		// 	copyTwilioMessagesOld[getIndex] = copyMessage;
		// }
		// if (getIndex !== -1 && twilio_messages[getIndex].twilio_file) {
		// 	copyMessage.loading = false;
		// }
		dispatch({
			type: ActionTypes.GET_TWILIO_MESSAGE_BY_ID_REQUEST
		});

		let response;
		const logInType = LocalStorageHelper.getLoginType();
		if (logInType === 'admin') {
			response = await API.admin.messages.getTwilioMessageById(message_id);
		} else {
			response = await API.staff.messages.getTwilioMessageById(message_id);
		}
		if (response.meta.code !== 200) {
			return dispatch({
				type: ActionTypes.GET_TWILIO_MESSAGE_BY_ID_ERROR,
				payload: {
					meta: response.meta
				}
			});
		}
		const { twilio_message } = response.data;
		
		const copyTwilioMessagesNew = [...twilio_messages];

		// if new twilio_message still not exist on the list, add it.
		const getNewMessageIndex = twilio_messages.findIndex(x => x._id === twilio_message._id);
		if (getNewMessageIndex === -1) {
			const lastMessage = copyTwilioMessagesNew[copyTwilioMessagesNew.length-1];
			// check the last sent message if status ('frontend only') sending and change it with this new message
			if (lastMessage.flow === 'outbound' && lastMessage.outbound_status === 'sending') {
				copyTwilioMessagesNew[copyTwilioMessagesNew.length-1] = {...twilio_message};
			} else {
				copyTwilioMessagesNew.push(twilio_message);
			}
		} else {
			copyTwilioMessagesNew[getNewMessageIndex] = {...twilio_message};
		}
		dispatch({
			type: ActionTypes.GET_TWILIO_MESSAGE_BY_ID_SUCCESS,
			payload: {
				twilio_messages: copyTwilioMessagesNew
				// tickets: copyTickets
			}
		});
	};
}

export function getTwilioFileById({ message_id }) {
	return async (dispatch, getState) => {
		const { twilio_messages } = getState().ticketPageReducer;
		const copyTwilioMessagesOld = [...twilio_messages];
		const getIndex = copyTwilioMessagesOld.findIndex(x => x._id === message_id && x.twilio_file_id && !x.twilio_file);
		if (getIndex !== -1) {
			copyTwilioMessagesOld[getIndex].loading = true;
		}
		dispatch({
			type: ActionTypes.GET_TWILIO_MESSAGE_BY_ID_REQUEST,
			payload: {
				twilio_messages: copyTwilioMessagesOld
			}
		});

		let response;
		const logInType = LocalStorageHelper.getLoginType();
		if (logInType === 'admin') {
			response = await API.admin.messages.getTwilioMessageById(message_id);
		} else {
			response = await API.staff.messages.getTwilioMessageById(message_id);
		}
		if (response.meta.code !== 200) {
			return dispatch({
				type: ActionTypes.GET_TWILIO_MESSAGE_BY_ID_ERROR,
				payload: {
					meta: response.meta
				}
			});
		}
		const { twilio_message } = response.data;

		const { twilio_messages: newMessages } = getState().ticketPageReducer;
		
		const copyTwilioMessagesNew = [...newMessages];

		// if new twilio_message still not exist on the list, add it.
		const getNewMessageIndex = twilio_messages.findIndex(x => x._id === twilio_message._id  && x.twilio_file_id && !x.twilio_file);
		// if (getNewMessageIndex === -1) {
		// 	copyTwilioMessagesNew.push(twilio_message);
		// }
		if (getNewMessageIndex !== -1) {
			copyTwilioMessagesNew[getNewMessageIndex] = {...twilio_message };
			// const copyMessage = {...copyTwilioMessagesNew[getNewMessageIndex]};
			if (!twilio_message.twilio_file) {
				if (copyTwilioMessagesNew[getNewMessageIndex].retry >= MAX_FILE_LOAD_RETRIES) {
					// Set loading to false
					copyTwilioMessagesNew[getNewMessageIndex].loading = false;
				} else {
					// Set loading to true if there is no file yet
					// and increment retry to record how many retries in loading the file
					copyTwilioMessagesNew[getNewMessageIndex].loading = true;
					copyTwilioMessagesNew[getNewMessageIndex].retry = copyTwilioMessagesNew[getNewMessageIndex].retry + 1;
					copyTwilioMessagesNew[getNewMessageIndex].loading = true;
				}
			} else {
				copyTwilioMessagesNew[getNewMessageIndex].loading = false;
			}
		}

		dispatch({
			type: ActionTypes.GET_TWILIO_MESSAGE_BY_ID_SUCCESS,
			payload: {
				twilio_messages: copyTwilioMessagesNew
				// tickets: copyTickets
			}
		});
	};
}




export function getTicketById({ ticket_id }) {
	return async (dispatch, getState) => {
		const { tickets } = getState().ticketPageReducer;

		dispatch({
			type: ActionTypes.GET_TICKET_BY_ID_REQUEST,
			payload: {
				loadingSelectedTicket: true
			}
		});

		let response;
		const logInType = LocalStorageHelper.getLoginType();
		if (logInType === 'admin') {
			response = await API.admin.tickets.getTicketById(ticket_id);
		} else {
			response = await API.staff.tickets.getTicketById(ticket_id);
		}
		if (response.meta.code !== 200) {
			dispatch(push(`/tickets?type=all`));
			return dispatch({
				type: ActionTypes.GET_TICKET_BY_ID_ERROR,
				payload: {
					loadingSelectedTicket: false,
					meta: response.meta
				}
			});
		}
		const { ticket } = response.data;
		const copyTickets = tickets && tickets.length > 0 && [...tickets];
		// clear unread messages count on ticket detail page in frontend (already cleared on backend on chatpage didmount)
		const getIndex = tickets.findIndex(x => x._id === ticket_id);
		if (getIndex !== -1) {
			copyTickets[getIndex].unread_messages = 0;
			dispatch({
				type: ActionTypes.GET_TICKET_BY_ID_SUCCESS,
				payload: {
					tickets: []
				}
			});
		}
		
		dispatch({
			type: ActionTypes.GET_TICKET_BY_ID_SUCCESS,
			payload: {
				loadingSelectedTicket: false,
				ticket,
				...(tickets.length > 0 && { tickets: copyTickets }),
				showNewTicketAssignedModal: ticket.status === 'pending_acceptance' && ticket.staff_id === LocalStorageHelper.getLoggedIn()._id ? true: false
			}
		});
	};
}

export function sendMessage({ ticket_id, message = '', file = null, filename = null, mime = null }) {
	return async (dispatch, getState) => {
		const { twilio_messages, tickets } = getState().ticketPageReducer;
		// show in the message list after staff sent the message, put "sending" status for FRONTEND only.
		// and wait for the frontend to return if successfully created the message.
		const copyOldMessages = [...twilio_messages];
		const fileDataToUrl = file && await FileHelper.fileToDataUrl(file);
		const newMessage = {
			created_at: new Date(),
			flow: 'outbound',
			message,
			ticket_id,
			...(file && { twilio_file: { file: fileDataToUrl, mime } }),
			outbound_status: 'sending'
		};
		// add the newly created message to list of messages
		copyOldMessages.push(newMessage);

		dispatch({
			type: ActionTypes.SEND_MESSAGE_REQUEST,
			payload: {
				loadingSendMessage: true,
				twilio_messages: copyOldMessages
			}
		});
		const body = {
			twilio_message: {
				ticket_id,
				...(!message && file ? { message: filename } : { message }),
				...(file && { file: fileDataToUrl }),
				...(filename && { filename }),
				...(mime && { mime })
			}
		};
		let response;
		const logInType = LocalStorageHelper.getLoginType();
		if (logInType === 'admin') {
			response = await API.admin.messages.createTwilioMessage(body);
		} else {
			response = await API.staff.messages.createTwilioMessage(body);
		}
		if (response.meta.code !== 200) {
			return dispatch({
				type: ActionTypes.SEND_MESSAGE_ERROR,
				payload: {
					loadingSendMessage: false,
					meta: response.meta
				}
			});
		}
		// success
		const { twilio_messages: newMessages } = getState().ticketPageReducer;
		const { twilio_message } = response.data;
		const copyTwilioMessages = [...newMessages];
		const copyTickets = tickets && tickets.length > 0 && [...tickets];
		// clear unread messages count on ticket detail page in frontend (already cleared on backend on chatpage didmount)
		const getIndex = tickets.findIndex(x => x._id === ticket_id);
		if (getIndex !== -1) {
			copyTickets[getIndex].twilio_message = {...twilio_message};
			dispatch({
				type: ActionTypes.SEND_MESSAGE_SUCCESS,
				payload: {
					tickets: []
				}
			});
		}
		// push new messge in list of messages
		copyTwilioMessages.push(twilio_message);
		dispatch({
			type: ActionTypes.SEND_MESSAGE_SUCCESS,
			payload: {
				loadingSendMessage: false,
				// twilio_messages: copyTwilioMessages
				tickets: copyTickets
			}
		});
	};
}

