export function newTicket({ ticket_id }) {
	return async (dispatch, getState) => {
		const { tickets } = getState().ticketPageReducer;

		dispatch({
			type: ActionTypes.NEW_TICKET_REQUEST,
			payload: {
				loadingTickets: true,
				loadingNewTicket: true
			}
		});

		let response;
		const logInType = LocalStorageHelper.getLoginType();
		if (logInType === 'admin') {
			response = await API.admin.tickets.getTickets({ ticket_id });
		} else {
			response = await API.staff.tickets.getTickets({ ticket_id });
		}

		if (response.meta.code !== 200) {
			return dispatch({
				type: ActionTypes.NEW_TICKET_ERROR,
				payload: {
					meta: response.meta,
					loadingTickets: false,
					loadingNewTicket: false
				}
			});
		}

		// dirty fix, clear first all tickets and update new, after this dispatch.
		// it's not re rendering even the state is already changed.

		const { tickets: responseTickets } = response.data;
		const ticket = responseTickets[0];
		// success
		const copyTickets = tickets && tickets.length > 0 ? [...tickets] : [];
		// check if the new ticket is already in the list, if yes: update it so the last message will update,
		// else add it to the new list of tickets.
		const getTicketIndex = copyTickets.findIndex( x => x._id === ticket_id);
		if (getTicketIndex === -1) {
			dispatch({
				type: ActionTypes.NEW_TICKET_SUCCESS,
				payload: {
					// loadingTickets: true,
					tickets: []
				}
			});
			// add new
			copyTickets.push(ticket);
		} else {
			// update,
			copyTickets[getTicketIndex] = {...ticket};	
		}
		dispatch({
			type: ActionTypes.NEW_TICKET_SUCCESS,
			payload: {
				loadingTickets: false,
				loadingNewTicket: false,
				newTicket: ticket //this will use to show notification in ticket page.
			}
		});
		// clear new ticket after
		dispatch({
			type: ActionTypes.NEW_TICKET_SUCCESS,
			payload: {
				newTicket: null
			}
		});
		// dispatch(push(`/tickets?type=my&ticket_id=${ticket._id}`));
	};
}

export function updateTicketStatus({ ticket_id, status }) {
	return async (dispatch, getState) => {
		const { tickets, ticket: stateTicket } = getState().ticketPageReducer;
		dispatch({
			type: ActionTypes.UPDATE_TICKET_STATUS_REQUEST,
			payload: {
				loadingUpdateTicket: true
			}
		});

		let response;
		const logInType = LocalStorageHelper.getLoginType();
		if (logInType === 'staff') {
			response = await API.staff.tickets.updateTicketStatusById({ id: ticket_id, status });
		} else {
			response = await API.admin.tickets.updateTicketStatusById({ id: ticket_id, status });
		}


		if (response.meta.code !== 200) {
			return dispatch({
				type: ActionTypes.UPDATE_TICKET_STATUS_ERROR,
				payload: {
					loadingUpdateTicket: false,
					meta: response.meta
				}
			});
		}

		// success
		const { ticket } = response.data;
		const copyTickets = [...tickets];
		const getIndex = copyTickets.findIndex(x => x._id === ticket_id);
		if (getIndex !== -1) {
			copyTickets[getIndex] = { ...copyTickets[getIndex], ...ticket };
		}
		dispatch({
			type: ActionTypes.UPDATE_TICKET_STATUS_SUCCESS,
			payload: {
				meta: response.meta,
				loadingUpdateTicket: false,
				tickets: copyTickets,
				ticket: { ...stateTicket, ...ticket }
			}
		});
		// redirect to assigned ticket list tab
		if (status === 'in-progress') {
			dispatch(push(`/tickets?type=my&ticket_id=${ticket._id}`));
		}
	};
}

