export function updateCustomerInformation({ fields, id }) {
	return async (dispatch, getState) => {
		const { ticket: stateTicket } = getState().ticketPageReducer;

		dispatch({
			type: ActionTypes.UPDATE_CUSTOMER_REQUEST,
			payload: {
				loadingUpdateTicket: true
			}
		});

		const body = {
			twilio_contact: {
				...fields
			}
		};

		let response;
		const logInType = LocalStorageHelper.getLoginType();
		if (logInType === 'staff') {
			response = await API.staff.contacts.updateTwilioContact({ body, id });
		} else {
			response = await API.admin.contacts.updateTwilioContact({ body, id });
		}

		if (response.meta.code !== 200) {
			return dispatch({
				type: ActionTypes.UPDATE_CUSTOMER_ERROR,
				payload: {
					loadingUpdateTicket: false,
					meta: response.meta
				}
			});
		}
		// success
		const { twilio_contact } = response.data;
		dispatch({
			type: ActionTypes.UPDATE_CUSTOMER_SUCCESS,
			payload: {
				ticket: {...stateTicket, twilio_contact: { ...twilio_contact }},
				loadingUpdateTicket: false,
				meta: response.meta
			}
		});
	};
}


export function addTicketNote({ id, fields }) {
	return async (dispatch, getState) => {
		const { ticket: stateTicket } = getState().ticketPageReducer;
		dispatch({
			type: ActionTypes.ADD_TICKET_NOTE_REQUEST,
			payload: {
				loadingUpdateTicket: true
			}
		});

		const body = {
			note: {
				note: fields.note
			}
		};
		let response;
		const logInType = LocalStorageHelper.getLoginType();
		if (logInType === 'staff') {
			response = await API.staff.tickets.createTicketNote({ body, id });
		} else {
			response = await API.admin.tickets.createTicketNote({ body, id });
		}

		if (response.meta.code !== 200) {
			return dispatch({
				type: ActionTypes.ADD_TICKET_NOTE_ERROR,
				payload: {
					loadingUpdateTicket: false,
					meta: response.meta
				}
			});
		}
		// success
		const { ticket } = response.data;
		dispatch({
			type: ActionTypes.ADD_TICKET_NOTE_SUCCESS,
			payload: {
				ticket: {...stateTicket, ...ticket},
				loadingUpdateTicket: false,
				meta: response.meta
			}
		});
	};
}


export function getUsers() {
	return async dispatch => {

		dispatch({
			type: ActionTypes.GET_TICKET_STAFFS_REQUEST,
			payload: {
				loadingUsers: true
			}
		});

		let response;
		const logInType = LocalStorageHelper.getLoginType();
		if (logInType === 'admin') {
			response = await API.admin.users.getUsers();
		} else {
			response = await API.staff.users.getUsers();
		}
		if (response.meta.code !== 200) {
			return dispatch({
				type: ActionTypes.GET_TICKET_STAFFS_ERROR,
				payload: {
					loadingUsers: false
				}
			});
		}
		// success
		const { users } = response.data;
		dispatch({
			type: ActionTypes.GET_TICKET_STAFFS_SUCCESS,
			payload: {
				loadingUsers: false,
				users
			}
		});
	};
}


export function resetTicket() {
	return async dispatch => {
		dispatch({
			type: ActionTypes.RESET_MESSAGES
		});
	};
}

export function resetMeta() {
	return async dispatch => {
		dispatch({
			type: ActionTypes.RESET_TICKETS_PAGE_META
		});
	};
}


export function resetPage() {
	return async dispatch => {
		dispatch({
			type: ActionTypes.RESET_TICKETS_PAGE
		});
	};
}
