import { connect } from 'react-redux';
import StaffBasicPage from './StaffBasicPage';
import { onSubmit, resetMeta } from '../../../actions/StaffDetailsPage';

const mapsStateToProps = state => ({
	pageProps: state.staffDetailsPageReducer
});

const mapsDispatchToProps = dispatch => ({
	onSubmit(fields) {
		dispatch(onSubmit(fields));
	},
	resetMeta() {
		dispatch(resetMeta());
	}
});

export default connect(
	mapsStateToProps,
	mapsDispatchToProps
)(StaffBasicPage);
