import { connect } from 'react-redux';
import DashboardPage from './DashboardPage';
import { getAccount, reset, resetMeta } from '../../actions/DashboardPage/StaffList';


// const mapsStateToProps = state => ({
// 	settingsPageProps: state.settingsPageReducer
// });

const mapsStateToProps = state => ({
	staffListProps: state.staffListReducer
});

const mapsDispatchToProps = dispatch => ({
	getAccount(){
		dispatch(getAccount());
	},

	resetPage() {
		dispatch(reset());
	},

	resetMeta() {
		dispatch(resetMeta());
	}
});

export default connect(mapsStateToProps, mapsDispatchToProps)(DashboardPage);