import React, { Component } from 'react';
import { TextContainer, Layout, Card, Icon, Modal, TextField, Form, FormLayout,
	Select, SkeletonBodyText, SkeletonDisplayText, TextStyle } from '@shopify/polaris';
import _ from 'lodash';
import { Field, reduxForm } from 'redux-form';
import UrlParse from 'url-parse';
import moment from 'moment';
import { CirclePlusMinor, EditMinor } from '@shopify/polaris-icons';
import LocalStorageHelper from '../../../helpers/local_storage';
import { renderFormInput, validateRequired } from '../../../helpers/redux_form';


// show this if the chatview is loading.
const skeletonConversation = () => {
	return (
		<Layout>
			<Layout.Section>
				<Card sectioned>
					<TextContainer>
						<SkeletonDisplayText size="extraLarge" />
						<SkeletonBodyText lines={8}/>
						<SkeletonBodyText lines={4}/>
					</TextContainer>
				</Card>
			</Layout.Section>
		</Layout>
	);
};

class TicketInformation extends Component {
	constructor(props){
		super(props);
		this.state = {
			ticket_id: '',
			loggedIn: null,
			openNoteModal: false,
			note: '',
			openCustomerInformationModal: false,
			customerName: ''
		};
	}

	componentDidMount() {
		const { location } = this.props;
		const { query } = UrlParse(location.search, true);
		const { ticket_id } = query;
		const loggedIn = LocalStorageHelper.getLoggedIn();
		const loggedInType = LocalStorageHelper.getLoginType();

		this.setState({
			loggedIn: {...loggedIn, loggedInType },
			ticket_id
		});
	}
	
	componentDidUpdate(prevProps) {
		const { location: prevLocation } = prevProps;
		const { location} = this.props;
		const { query } = UrlParse(location.search, true);
		const { query: prevQuery } = UrlParse(prevLocation.search, true);
		const { ticket_id } = query;
		if (ticket_id && ticket_id !== prevQuery.ticket_id) {
			this.setState({
				ticket_id
			});
		}		
	}

	noteModalHandle = (action) => {
		this.setState({
			openNoteModal: action
		});
	}

	customerModalHandle = (action) => {
		const { ticketPageProps } = this.props;
		const { ticket } = ticketPageProps;
		this.props.initialize({ name: ticket.twilio_contact.name  });

		this.setState({
			openCustomerInformationModal: action
		});
	}

	renderNoteModal = () => {
		const { pristine, submitting, invalid } = this.props;

		return (
			<Modal
				open={this.state.openNoteModal}
				onClose={() => this.noteModalHandle(false)}
				title="Add note"
				primaryAction={{
					content: 'Add',
					onAction: this.props.handleSubmit((fields) => [this.props.addTicketNote({ fields, id: this.state.ticket_id }), this.noteModalHandle(false) ]),
					disabled: pristine || submitting || invalid
				}}
				secondaryActions={[
					{
						content: 'Cancel',
						onAction: () => this.noteModalHandle(false)
					
					}
				]}
			>
				<Modal.Section>
					<div style={{ textAlign: 'center' }}>
						{/* <TextField
							label="Note"
							value={this.state.note}
							onChange={(value) => this.setState({ note: value })}
							multiline
						/> */}
						<Form>
							<FormLayout>
								<Field
									component={renderFormInput}
									name="note"
									label="Note"
									validate={[validateRequired]}
									multiline
								/>
							</FormLayout>
						</Form>

					</div>

				</Modal.Section>
			</Modal>
		);
	}

	renderCustomerInformationModal = () => {
		const { pristine, submitting, invalid, ticketPageProps } = this.props;
		const { ticket } = ticketPageProps;
		return (
			<Modal
				open={this.state.openCustomerInformationModal}
				onClose={() => this.customerModalHandle(false)}
				title="Edit Customer Information"
				primaryAction={{
					content: 'Save',
					onAction: this.props.handleSubmit((fields) => [this.props.updateCustomerInformation({ fields, id: ticket.twilio_contact_id }), this.customerModalHandle(false) ]),
					disabled: pristine || submitting || invalid
				}}
				secondaryActions={[
					{
						content: 'Cancel',
						onAction: () => this.customerModalHandle(false)
					
					}
				]}
			>
				<Modal.Section>
					<div style={{ textAlign: 'center' }}>
						<Form>
							<FormLayout>
								<Field
									component={renderFormInput}
									name="name"
									label="Name"
									value={_.get(ticket, 'twilio_contact.name')}
								/>
								<TextField
									label="Phone Number"
									disabled
									value={_.get(ticket, 'twilio_contact.phone')}
								/>
							</FormLayout>
						</Form>
					</div>

				</Modal.Section>
			</Modal>
		);
	}

	render() {
		const { loggedIn, ticket_id } = this.state;
		const { updateTicketStatus, updateTicketAsignee, ticketPageProps } = this.props;
		const { users, ticket, loadingSelectedTicket, loadingUpdateTicket, loadingMessages } = ticketPageProps;
		const ticketStatusOptions = [
			{ label: 'Pending Acceptance', value: 'pending_acceptance', disabled: true },
			{ label: 'Pending', value: 'pending', disabled: true },
			{ label: 'In progress', value: 'in-progress' },
			{ label: 'Resolved', value: 'resolved' }
		];
		const userOptions = users && users.map((user => {
			return (
				{ label: user.name, value: user._id  }
			);
		}));
		const customer_phone_number = _.get(ticket, 'twilio_contact.phone');
		const customer_name = _.get(ticket, 'twilio_contact.name');
		const ticket_status = _.get(ticket, 'status');
		const loggedInType = _.get(loggedIn, 'loggedInType'); //user or staff
		const loggedInId = _.get(loggedIn, '_id');
		const ticketAssigneeId = _.get(ticket, 'staff_id');
		const notes = _.sortBy(_.get(ticket, 'notes'), function(o) { return new moment(_.get(o, 'created_at')); }).reverse();

		const isAssigned = loggedInId === ticketAssigneeId; //identify if the ticket is assigned to the logged in staff.
		const ticketIsPendingAcceptance = isAssigned && ticket_status === 'pending_acceptance'; //check if the ticket is assigned to the logged in staff & ticket status is pending_acceptance
		const ticketIsResolved = ticket_status === 'resolved';
		if (loadingSelectedTicket || loadingMessages) {
			return (
				skeletonConversation()
			);
		}
		return (
			<React.Fragment>
				<Card>
					<Card.Section>
						<TextStyle variation="subdued">Ticket No. </TextStyle>
						<TextContainer>
							<p style={{ fontWeight: 'bold', wordWrap: 'break-word' }}>{_.get(ticket, '_id')}</p>
						</TextContainer>
					</Card.Section>
				</Card>
				
				<Card>
					<Card.Section>
						<TextStyle variation="subdued">Ticket Status</TextStyle>
						<Select
							name="status"
							labelHidden
							options={ticketStatusOptions}
							value={ticket_status}
							disabled={loadingUpdateTicket || ticketIsResolved || loggedInType === 'user' || !isAssigned || ticketIsPendingAcceptance}
							onChange={(value) => {
								if (value === 'resolved') {
									if (window.confirm('Are you sure to resolve this ticket now?')) {
										updateTicketStatus({ ticket_id, status: value });
									}
								} else {
									updateTicketStatus({ ticket_id, status: value });
								}
							}}
						/>
					</Card.Section>
				</Card>

				{ticket_status !== 'pending' && <Card>
					<Card.Section>
						<TextStyle variation="subdued">Asignee</TextStyle>
						<Select
							name="asignee"
							labelHidden
							options={userOptions}
							value={_.get(ticket, 'staff_id')}
							disabled={loggedInType === 'user' || loadingUpdateTicket || ticketIsResolved || !isAssigned}
							onChange={(value) => updateTicketAsignee({ ticket_id, user_id: value })}
						/>
					</Card.Section>
				</Card>}

				<Card>
					<Card.Section>
						<div style={{ display: 'flex', justifyContent: 'space-between' }}>
							<TextStyle variation="subdued">Customer Information</TextStyle>
							{isAssigned && !ticketIsResolved && <span
								onClick={() => this.customerModalHandle(true)}
								style={{ cursor: 'pointer', color: '#006FBB' }}
							                                    >
								<Icon 
									source={EditMinor} 
								/>
							</span>}
						</div>
						<div>
							<TextStyle variation="subdued">Name: </TextStyle>
							<TextStyle variation="strong">{customer_name}</TextStyle>
						</div>
						<div>
							<TextStyle variation="subdued">Phone Number: </TextStyle>
							<TextStyle variation="strong">{customer_phone_number}</TextStyle>
						</div>
					</Card.Section>
				</Card>

				<Card>
					<Card.Section>
						<div style={{ display: 'flex', justifyContent: 'space-between' }}>
							<TextStyle variation="subdued">Notes</TextStyle>
							{isAssigned && !ticketIsResolved &&  <span
								onClick={() => this.noteModalHandle(true)}
								style={{ cursor: 'pointer', color: '#006FBB' }}
							                                     >
								<Icon 
									source={CirclePlusMinor} 
								/>
							</span>}
						</div>
						{notes && notes.map((note, index) => {
							return (
								<div 
									style={styles.noteList} 
									key={index}
								>
									<span style={styles.noteDate}>{moment(note.created_at).format('MMM DD, YYYY HH:mm')}</span>
									<p style={{ fontSize: 12}}>{note.note}</p>
									<span style={{...styles.noteDate, fontStyle: 'italic' }}>{_.get(note, 'user.name')}</span>
								</div>
							);
						})}
					</Card.Section>
				</Card>
				{this.renderNoteModal()}
				{this.renderCustomerInformationModal()}
			</React.Fragment>
		);
	}
}

const styles = {
	container: {
		marginBottom: 10,
		marginTop: 10
	},
	noteList: {
		borderBottom: '1px solid black',
		marginBottom: 5
	},
	noteDate: {
		fontSize: 11,
		color: '#919EAB'
	}
};

export default reduxForm({
	form: 'TicketInformation'
})(TicketInformation);