import request from '../request';

export default {
	twilio: {
		async getTwilioConnections(query) {
			const res = await request.get(`/admin/twilio-connections/me`, query);
			return res;
		},
		async getTwilioConnectionById(id) {
			const res = await request.get(`/admin/twilio-connections/id?id=${id}`);
			return res;
		},
		async createTwilioConnection(body) {
			const res = await request.post(`/admin/twilio-connections/me`, body);
			return res;
		},
		async updateTwilioConnectionById({ id, body }) {
			const res = await request.patch(`/admin/twilio-connections/id?id=${id}`, body);
			return res;
		}
	}
};
