import { connect } from 'react-redux';
import ReportPage from './ReportPage';
import { getReports, resetPage, resetMeta } from '../../actions/ReportPage';

const mapsStateToProps = state => ({
	reportPageProps: state.reportPageReducer
});

const mapsDispatchToProps = dispatch => ({
	getReports(){
		dispatch(getReports());
	},
	resetPage() {
		dispatch(resetPage());
	},
	resetMeta() {
		dispatch(resetMeta());
	}
});

export default connect(mapsStateToProps, mapsDispatchToProps)(ReportPage);
