import React, { Component } from 'react';
import UrlParse from 'url-parse';
import { Layout, Breadcrumb, Typography, Row, Col, Modal } from 'antd';
import { compose } from 'redux';
import { withAlert } from 'react-alert';

import StaffBasicForm from './components/StaffBasicForm';
import Loading from '../../../components/loading';

const { Header, Content } = Layout;
const { Title } = Typography;

class StaffBasicPage extends Component {
	constructor(props) {
		super(props);
		this.state = {
			disabled: true
		};
	}

	componentDidUpdate(prevProps, prevState) {
		const { meta: prevMeta } = prevProps.pageProps;
		const { meta } = this.props.pageProps;
		if (meta && meta !== prevMeta) {
			const alertOptions = {
				type: meta.code === 200 ? 'success' : 'error',
				...(meta.code === 40399 && { timeout: 0 })
			};
			this.props.alert.show(meta.message, alertOptions);
			this.props.resetMeta();
		}
	}

	showForm(staff) {
		const { onSubmit } = this.props;
		const { loadingSubmit } = this.props.pageProps;
		return (
			<div className="main-content">
				<Row justify="space-around">
					<StaffBasicForm 
						staff={staff}
						onSubmit={onSubmit}
						loadingSubmit={loadingSubmit}
					/>
				</Row>
			</div>
		);
	}

	renderCreateForm(settings) {
		const { sections } = settings;
		return (
			<>
				<Header
					className="breadcrumb-header"
					style={{ backgroundColor: 'white' }}
				>
					<Row>
						<Col span={21}>
							<Breadcrumb style={{ marginTop: '0.5rem'}}>
								{sections && sections.map(section => {
									return (
										<Breadcrumb.Item key={section.key}>
											{section.link?
												// eslint-disable-next-line jsx-a11y/anchor-is-valid
												<a
													href="#"
													onClick={section.onClick}
												>{section.content}</a> : section.content
											}
										</Breadcrumb.Item>
									);
								})}
							</Breadcrumb>
							<Title
								level={5}
								style={{ paddingTop: 10 }}
							>Create Staff</Title>
						</Col>
					</Row>
				</Header>
				<Content style={{ margin: '24px 16px 0', backgroundColor: 'white' }}>
					{this.showForm()}
				</Content>
			</>
		);
	}

	renderUpdateForm() {
		const { staff } = this.props.pageProps;
		
		return(
			this.showForm(staff)
		);
	}

	render() {
		const { location, history } = this.props;
		const { loading } = this.props.pageProps;

		if (loading) {
			return <Loading />;
		}

		const { query } = UrlParse(location.search, true);
		const sections = [
			{ key: '/staff', content: 'Staff', link: true, onClick: () => history.push('/staffs') },
			{ key: 'Create', content: 'Create', active: true}
		];
		const settings = {
			sections,
			location,
			query
		};

		return (
			<>
				{ query && query.id && this.renderUpdateForm() }
				{ !query.id && this.renderCreateForm(settings)}
			</>
		);
	}
}

export default compose(withAlert())(StaffBasicPage);