import React from 'react';
import { Icon } from '@shopify/polaris';
import { ClipboardMinor } from '@shopify/polaris-icons';

export default ({ hovered = false, Webhook }) => {
	return (
		<td style={{ display: 'flex', flexDirection: 'row', textAlign: 'left' }}>
			{
				<div>
					{hovered ? (
						<div style={{ display: 'flex', flexDirection: 'row' }}>
							<u style={{ color: '#afb4b7' }}>{Webhook}</u>
							<span>
								<Icon
									source={ClipboardMinor}
									color="inkLightest"
								/>
							</span>
						</div>
					) : (
						<div>{Webhook}</div>
					)}
				</div>
			}
		</td>
	);
};
