import * as ActionTypes from '../../constants/ActionTypes';
import API from '../../helpers/api';

export function addFacebokConnection({ accessToken, userID, name }) {
	return async dispatch => {
		dispatch({
			type: ActionTypes.ADD_FACEBOOK_CONNECTION_REQUEST,
			payload: {
				loadingSubmitFacebookConnection: true
			}
		});
		const body = {
			facebook_connection: {
				access_token: accessToken,
				facebook_user_name: name,
				facebook_user_id: userID
			}
		};
		const response = await API.admin.connections.facebook.createFacebookConnection(body);
		if (response.meta.code !== 200) {
			return dispatch({
				type: ActionTypes.ADD_FACEBOOK_CONNECTION_ERROR,
				payload: {
					loadingSubmitFacebookConnection: false,
					meta: response.meta
				}
			});
		}
		// success
		dispatch({
			type: ActionTypes.ADD_FACEBOOK_CONNECTION_SUCCESS,
			payload: {
				loadingSubmitFacebookConnection: false,
				meta: response.meta
			}
		});
		// dispatch(getFacebookConnections());
	};
}

export function getFacebookConnections(){
	return async (dispatch) => {
		dispatch({
			type: ActionTypes.GET_FACEBOK_CONNECTIONS_REQUEST,
			payload: {
				loadingFacebookConnections: true
			}
		});
		const response = await API.admin.connections.facebook.getFacebookConnections();
		if (response.meta.code !== 200) {
			return dispatch({
				type: ActionTypes.GET_FACEBOK_CONNECTIONS_ERROR,
				payload: {
					loadingFacebookConnections: false,
					meta: response.meta
				}
			});
		}
		// success
		const { facebook_connections } = response.data;
		dispatch({
			type: ActionTypes.GET_FACEBOK_CONNECTIONS_SUCCESS,
			payload: {
				loadingFacebookConnections: false,
				facebook_connections
			}
		});
	};
}

export function updatePageSubcribedFieldById({ facebook_page_id: id, facebook_connection_id, status, type}){
	return async (dispatch, getState) => {
		dispatch({
			type: ActionTypes.UPDATE_PAGE_SUBCRIBED_FIELD_REQUEST,
			payload: {
				loadingSubcribedFieldUpdate: true
			}
		});

		const body = {
			facebook_page: {
				facebook_connection_id,
				status,
				type
			}
		};
		const response = await API.admin.connections.facebook.updatePageSubcribedFieldById({ id, body });
		if (response.meta.code !== 200) {
			return dispatch({
				type: ActionTypes.UPDATE_PAGE_SUBCRIBED_FIELD_ERROR,
				payload: {
					loadingSubcribedFieldUpdate: false,
					meta: response.meta
				}
			});
		}
		// success
		const { facebook_page } = response.data;
		const { facebook_connections } = getState().settingsPageReducer;
		const copyFacebookConnections = [...facebook_connections];
		const getFacebookConnectionIndex = facebook_connections.findIndex(x => x._id === facebook_page.facebook_connection_id);
		if (getFacebookConnectionIndex !== -1) {
			const getFacebookPageIndex = facebook_connections[getFacebookConnectionIndex].facebook_pages.findIndex(x => x._id === facebook_page._id);
			if (getFacebookPageIndex !== -1) {
				dispatch({
					type: ActionTypes.UPDATE_PAGE_SUBCRIBED_FIELD_SUCCESS,
					payload: {
						facebook_connections: []
					}
				});
				copyFacebookConnections[getFacebookConnectionIndex].facebook_pages[getFacebookPageIndex].subscribed_fields = {...facebook_page.subscribed_fields};
			}
		}
		dispatch({
			type: ActionTypes.UPDATE_PAGE_SUBCRIBED_FIELD_SUCCESS,
			payload: {
				meta: response.meta,
				loadingSubcribedFieldUpdate: false,
				facebook_connections: copyFacebookConnections
			}
		});
	};
}