import { connect } from 'react-redux';
import RegisterPage from './RegisterPage';
import { reset, resetMeta, submitPage } from '../../actions/RegisterPage';

const mapsStateToProps = state => ({
	registerPageProps: state.registerPageReducer
});

const mapsDispatchToProps = dispatch => ({
	submitPage(options) {
		dispatch(submitPage(options));
	},
	resetPage() {
		dispatch(reset());
	},
	resetMeta() {
		dispatch(resetMeta());
	}
});

export default connect(
	mapsStateToProps,
	mapsDispatchToProps
)(RegisterPage);
