import React, { Component } from 'react';
import _ from 'lodash';
import { Button, Card, Col, DatePicker, Dropdown, Form, Input, Menu, message, Row, Select, Space, Table, Tag } from 'antd';
import { DownloadOutlined, PlusOutlined, SearchOutlined, UserOutlined } from '@ant-design/icons';
import { compose } from 'redux';
import { withAlert } from 'react-alert';
const { Search } = Input;
const { Option } = Select;
class ReportPage extends Component {
	constructor(props) {
		super(props);
		this.state = {
			pages: [],
			filters: {
				status: '',
				search: '',
				date: ''
			}
		};
	}

	componentDidMount() {
		this.props.getReports();
	}

	componentWillUnmount() {
		this.props.resetPage();
	}

	render(){
		const { filters } = this.state;
		const { reportPageProps } = this.props;
		const { reports } = reportPageProps;
		// const { tickets: data } = this.props;

		const columns = [
			{
				title: 'Ticket ID',
				dataIndex: '_id',
				key: 'key'
			},
			{
				title: 'Assignee',
				dataIndex: 'assignee',
				key: 'key'
			},
			{
				title: 'Date',
				dataIndex: 'date',
				key: 'key'
			},
			{
				title: 'Status',
				dataIndex: 'status',
				key: 'key'
			},
			{
				title: 'Action',
				dataIndex: 'action',
				key: 'key',
				render: (text, data) => {
					return (
						<Button
							type="link"
							icon={<DownloadOutlined />}
							ghost
							onClick={() => handleDownload(obj2CSV(data), `report-${data._id}`)}
						>
						</Button>
					);}
			}
		];

		const modalHandle = (type, e) => {
			//Check if 'e' has value and return the value else return empty string.
			const value = (e) ? e: ''; 
			const newFilter = filters;
			switch(type){
				case 'status':
					newFilter.status = value;
					break;
				case 'search':
					newFilter.search = value;
					break;
				case 'date':
					newFilter.date = e.dateString;
					break;
				default:
			}
			this.setState({filters: newFilter});	
		};

		const searchResults = () => {
			return reports.filter((element)=> {
				//If filter state is empty return true.
				const statusFilter = ("" === filters.status) ? true : element.status === filters.status;
				const nameSearchFilter = ('' === filters.search) ? true : (element._id + element.assignee).toLowerCase().includes(filters.search.toLowerCase());
				const dateFilter = ('' === filters.date) ? true : element.date === filters.date;
				
				//return data that satisfies filtering 
				return (statusFilter && nameSearchFilter && dateFilter);
			});
		};

		const obj2CSV = (data) => {
			const csvRows = [];
			const headers = Object.keys(data);
			csvRows.push(headers.join(','));
			const values = Object.values(data).join(',');
			csvRows.push(values);
			return csvRows.join('\n');
		};

		const arr2CSV = (data) => {
			const csvRows = [];
			const headers = Object.keys(data[0]);
			csvRows.push(headers.join(','));
			data.map(element => {
				csvRows.push(Object.values(element).join(','));
			});
			return csvRows.join('\n');
		};

		const handleDownload = (data, name) => {
			const blob = new Blob([data], { type: 'text/csv' });
			const url = window.URL.createObjectURL(blob);
			const a = document.createElement('a');
			a.setAttribute('href', url);
			a.setAttribute('download', `${name}.csv`);
			a.click();
		};
		
		return(
			<div style={{ width: '95%', margin: 'auto' }}>
				<Card title="Reports List">
					<Space
						direction="vertical"
						size="middle"
						style={{ display: 'flex' }}
					>
						<Row justify="end">
							<Space>
								<Select
									style={styles.searchBox}
									allowClear
									placeholder="Search by Status"
									onChange={(value) => modalHandle('status', value)}
								>
									<Option value="Not Started">Not Started</Option>
									<Option value="In Progress">In Progress</Option>
									<Option value="Resolved">Resolved</Option>
								</Select>

								<DatePicker
									placeholder="Search by Date"
									format={"MM-DD-YYYY"}
									onChange={(date, dateString) => {const e = {date, dateString}; modalHandle("date", e);}}
								/>

								<Search
									placeholder="Search by Ticket ID, Assignee"
									allowClear
									onSearch={(value) => modalHandle('search', value)}
									style={{ width: 250 }}
								/>

								<Button
									type="primary"
									icon={<DownloadOutlined />}
									style={{backgroundColor: '#09509d', borderColor: '#09509d'}}
									onClick={() => {
										handleDownload(arr2CSV(reports), "reports-all");
									}}
								>
									Download All
								</Button>
							</Space>
						</Row>
						<Table
							dataSource={searchResults()}
							columns={columns}
						/>
					</Space>
				</Card>
			</div>
		);
	}
}
const styles = {
	searchBox: {
		width: '12em'
	}
};
export default compose(withAlert())(ReportPage);
