import { connect } from 'react-redux';
import MessengerIntegrationPage from './MessengerIntegrationPage';
import { getPages, resetPage, resetMeta } from '../../../actions/MessengerIntegrationPage';

const mapsStateToProps = state => ({
	messengerIntegrationPage: state.messengerIntegrationPageReducer
});

const mapsDispatchToProps = dispatch => ({
	getPages(){
		dispatch(getPages());
	},
	resetPage() {
		dispatch(resetPage());
	},
	resetMeta() {
		dispatch(resetMeta());
	}
});

export default connect(mapsStateToProps, mapsDispatchToProps)(MessengerIntegrationPage);
