import React, { Component, useState } from 'react';
import { DownOutlined, PlusOutlined, SearchOutlined, UserOutlined } from '@ant-design/icons';
import { Button, Card, Col, Divider, Dropdown, Form, Input, Menu, message, Row, Space, Table, Tag } from 'antd';
import { compose } from 'redux';
import { withAlert } from 'react-alert';
import Loading from '../../components/loading';
import LabelHelper from '../../helpers/Label';
import StaffForm from './components/StaffForm';

class StaffListPage extends Component {
	constructor(props) {
		super(props);
		this.state = {
			statusValue: 'All',
			searchValue: '',
			staffForm: false,
			staffFormType: null,
			data: []
		};
	}

	componentDidMount() {
		this.props.getStaffs();
	}

	componentDidUpdate(prevProps) {
		const { meta: prevMeta, loadingSubmitFacebookConnection: prev_loadingSubmitFacebookConnection, loadingTwilioData: prev_loadingTwilioData } = prevProps.settingsPageProps;
		const { meta, loadingSubmitFacebookConnection, loadingTwilioData } = this.props.settingsPageProps;

		if (meta && meta !== prevMeta) {
			if (meta.code === 200) {
				this.setState({
					staffForm: false,
					staffFormType: null
				});
			}
		}
	}

	componentWillUnmount() {
		this.props.resetPage();
	}

	modalHandle = async (value, id = '') => {
		if (id) {
			// fetch staff by id.
			await this.props.getStaffById({ id });
		}
		if (!value) {
			this.props.resetSelectedStaff();
		}
		this.setState({
			staffForm: value,
			staffFormType: id ? 'updating' : 'adding'
		});
	};


	render() {
		const { settingsPageProps, submitStaff } = this.props;
		const {
			staffs,
			selectedStaff,
			loadingStaffModal,
			loadingSubmit
		} = settingsPageProps;
		const { statusValue, searchValue, staffForm, staffFormType } = this.state;
		const columns = [
			{
				title: 'Staff ID',
				dataIndex: 'staffID',
				key: 'staffID',
				render: text => text
			},
			{
				title: 'Name',
				dataIndex: 'name',
				key: 'name'
			},
			{
				title: 'Status',
				key: 'tags',
				dataIndex: 'tags',
				render: (_, { tags }) => (
					<>
						{tags.map(tag => {
							let color = tag === 'online' ? 'green' : 'volcano';
							return (
								<Tag
									color={color}
									key={tag}
								>
									{tag.toUpperCase()}
								</Tag>
							);
						})}
					</>
				)
			},
			{
				title: 'Action',
				key: 'action',
				render: (_, record) => (
					<Space size="middle">
						<Button
							type="link"
							onClick={() => this.props.navigateToDetailsPage(record._id)}
						>Edit</Button>
					</Space>
				)
			}
		];
		

		const data = staffs.map((staff, index) => {
			staff.tags = [];
			if(!staff.status){
				staff.status = 'offline';
			}
			staff.tags.push(staff.status);
			staff.staffID = staff._id;
			staff.key = index+1;
			return staff;
		});

		const statusList = () => {
			return (statusValue === 'All') ? data : 
				data.filter((element) => {
					return (element.tags[0].toLowerCase() === statusValue.toLowerCase());
				});
		};

		const searchResults = () => {
			return statusList().filter((element) => {
				const searchItems = element.staffID.toLowerCase() + element.name.toLowerCase();
				const searchQuery = searchValue.toLowerCase();
				return searchItems.includes(searchQuery);
			});
		};

		const items = [
			{
				label: 'All',
				key: '1'
			},
			{
				label: 'Online',
				key: '2'
			},
			{
				label: 'Offline',
				key: '3'
			}
		];

		const handleMenuClick = e => {
			message.info(`${items[e.key-1].label}`);
			this.setState({statusValue: items[e.key-1].label});
		};

		const menu = <Menu
			onClick={handleMenuClick}
			items={items}
		             />;

		const { Search } = Input;
		const onSearch = (value) => {
			message.info(value);
			this.setState({searchValue: value});
		};
		return (
			<div style={{ width: '95%', margin: 'auto' }}>
				<Card title="Staff List">
					{staffForm && (
						<StaffForm
							modalHandle={this.modalHandle}
							loadingStaffModal={loadingStaffModal}
							staffFormType={staffFormType} // adding or updating
							isOpen={staffForm}
							data={selectedStaff}
							submitStaff={submitStaff}
							loadingSubmit={loadingSubmit}
						/>
					)}
					<Space
						direction="vertical"
						size="middle"
						style={{ display: 'flex' }}
					>
						<Row justify="end">
							<Space>
								<Dropdown overlay={menu}>
									<Button>
										<Space>
											{statusValue}
											<DownOutlined />
										</Space>
									</Button>
								</Dropdown>

								<Search
									placeholder="Search by Staff ID, Name"
									allowClear
									onSearch={onSearch}
									style={{ width: 200 }}
								/>

								<Button
									type="primary"
									icon={<PlusOutlined />}
									style={{backgroundColor: '#09509d', borderColor: '#09509d'}}
									onClick={() => this.props.navigateToAddPage()}
								>
									New Staff
								</Button>
							</Space>
						</Row>
						<Table
							dataSource={searchResults()}
							columns={columns}
						/>
					</Space>
				</Card>
			</div>
		);
	}
}

export default compose(withAlert())(StaffListPage);
