import { connect } from 'react-redux';
import ForgotPasswordPage from './ForgotPasswordPage';
import { reset } from '../../actions/ForgotPasswordPage';

const mapsStateToProps = state => ({
	forgotPasswordPageProps: state.forgotPasswordReducer
});

const mapsDispatchToProps = dispatch => ({
	resetPage() {
		dispatch(reset());
	}
});

export default connect(
	mapsStateToProps,
	mapsDispatchToProps
)(ForgotPasswordPage);
