import React, { Component } from 'react';
import _ from 'lodash';
import { compose } from 'redux';
import { withAlert } from 'react-alert';
import Loading from '../../components/loading';
import { Card } from 'antd';
import TicketStatistics from './components/TicketStatistics';
import StaffList from './components/StaffList';
import TicketList from './components/TicketList';
import AccountPending from './components/AccountPending';

class DashboardPage extends Component {
	constructor(props) {
		super(props);
		this.state = {
			pending: false
		};
	}

	componentDidMount(){
		this.props.getAccount();
	}


	componentWillUnmount() {
		this.props.resetPage();
	}

	render(){
		const { staffListProps } = this.props;
		const { pending } = this.state;
		const { staffs } = staffListProps;

		//temporary Ticket Data
		const tickets = [
			{
				key: '001',
				customer: 'Jim Halpert',
				channel: 'Messenger',
				date: '06-10-2022',
				priority: 'Medium',
				name: 'Jane Doe',
				status: 'Pending'
			},
			{
				key: '002',
				customer: 'John Doe',
				channel: 'Instagram',
				date: '06-09-2022',
				priority: 'Medium',
				name: 'Jet Lee',
				status: 'In Progress'
			},
			{
				key: '003',
				customer: 'Maria Georgies',
				channel: 'Email',
				date: '06-08-2022',
				priority: 'High',
				name: 'Michael Scott',
				status: 'Resolved'
			}
		];
		
		return(
			<div>
				{ (pending) ? <AccountPending /> 
					: <div style={styles.mainContainer}>
						<div style={styles.container}>
							<TicketStatistics tickets={tickets} />
							<StaffList staffs={staffs}/>
						</div>
						<TicketList tickets={tickets} />
					</div>
				}
			</div>
		);
	}
}

const styles = {
	container: {
		display: 'flex',
		justifyContent: "space-between",
		marginBottom: 16
	}
};

export default compose(withAlert())(DashboardPage);
