import React, { Component } from 'react';
import { Caption, Checkbox, Avatar, ResourceItem, TextStyle, ResourceList, TextContainer, Icon } from '@shopify/polaris';
import { EditMinor, DeleteMinor } from '@shopify/polaris-icons';
import Loading from '../../../components/loading';
import LabelHelper from '../../../helpers/Label';

class ConnectionList extends Component {
	renderFacebookConnections = facebook_connections => {
		const changeStatusHandle = ({ type, status, facebook_connection_id, facebook_page_id }) => {
			if (window.confirm(`Are you sure to ${status ? 'enable' : 'disable'} this feature?`)) {
				// call action and API call change status
				this.props.updatePageSubcribedFieldById({ type, status, facebook_connection_id, facebook_page_id });
			}
		};

		return (
			<ResourceList
				loading={false}
				resourceName={{ singular: 'connection', plural: 'connections' }}
				items={facebook_connections}
				renderItem={item => {
					const { _id, facebook_pages } = item;
					const connection = LabelHelper.connectionType('facebook');
					return (
						<ResourceItem id={_id}>
							<div
								// onClick= {()=> !loadingDelete && !loadingStaffModal && !loadingSubmit &&
								// 	this.modalHandle('staffForm', true, _id)
								// }
								style={styles.connectionsContainer}
							>
								<div style={styles.connectionHeader}>
									<div>
										<TextStyle variation="strong">{connection.title}</TextStyle>
										<Caption>{connection.description}</Caption>
									</div>
									<div style={{ display: 'flex' }}>
										<Icon
											source={DeleteMinor}
											color="red"
										/>
									</div>
								</div>
								{facebook_pages.length < 0 ? (
									<span>display TBD</span>
								) : (
									facebook_pages.map(page => {
										const { loadingSubcribedFieldUpdate } = this.props;
										const { _id: page_id, name, subscribed_fields } = page;
										const media = <Avatar
											size="small"
											name={name}
											initials={name.charAt(0)}
										              />;
										return (
											<ResourceItem
												media={media}
												key={page_id}
											>
												<div>
													<TextStyle variation="strong">{name}</TextStyle>
													{/* <Caption>Facebook</Caption> */}
												</div>
												<div>
													<Checkbox
														disabled={loadingSubcribedFieldUpdate}
														label="Enable Messenger"
														checked={subscribed_fields.messages}
														onChange={value => changeStatusHandle({ type: 'messages', status: value, facebook_connection_id: _id, facebook_page_id: page_id })}
													/>
													<br />
													<Checkbox
														disabled={loadingSubcribedFieldUpdate}
														label="Enable Facebook Comments"
														checked={subscribed_fields.feed}
														onChange={value => changeStatusHandle({ type: 'feed', status: value, facebook_connection_id: _id, facebook_page_id: page_id })}
													/>
												</div>
											</ResourceItem>
										);
									})
								)}
							</div>
						</ResourceItem>
					);
				}}
			/>
		);
	};

	render() {
		const { loadingFacebookConnections, facebook_connections, loadingSubmitFacebookConnection } = this.props;
		return (
			<div style={{ width: 600 }}>
				{loadingFacebookConnections || loadingSubmitFacebookConnection ? (
					<Loading />
				) : facebook_connections.length <= 0 ? (
					<div style={{ textAlign: 'center', marginTop: 10 }}>
						<TextStyle variation="strong">No facebook connection found.</TextStyle>
					</div>
				) : (
					this.renderFacebookConnections(facebook_connections)
				)}
			</div>
		);
	}
}

const styles = {
	container: {
		display: 'flex',
		justifyContent: 'space-between'
	},
	connectionsContainer: {
		// display: 'flex',
		// justifyContent: 'space-between',
		// alignItems: 'center'
	},
	connectionHeader: {
		padding: 10,
		display: 'flex',
		width: '100%',
		justifyContent: 'space-between',
		borderBottom: '1px solid black'
	}
};

export default ConnectionList;
