import React, { Component } from 'react';
import _, { random } from 'lodash';
import { compose } from 'redux';
import { withAlert } from 'react-alert';
import { Card } from 'antd';
import Staff from './Staff';
import request from '../../../helpers/api/request';
class StaffList extends Component {
	constructor(props) {
		super(props);
		this.state = {
		};
	}


	
	render(){

		//Temporary Profile Image Randomizer
		const getImage = () => {
			const randomizer = Math.floor(Math.random() * 200);
			return `https://waifu.vercel.app/sfw/waifu?random=${randomizer}`;
		};
		
		const { staffs } = this.props;
		
		return(
			<Card
				title="Staff List"
				style={{width: '30%'}}
			>
				<div style={styles.parentDiv}>
					<div style={styles.childDiv}>
						{staffs.map(staff => {
							staff.img = getImage();
							return <Staff
								key={staff._id}
								staff={staff}
							       />;
						})}
					</div>
				</div>
			</Card>
		);
	}
}

const styles = {
	
	parentDiv: {
		width: '100%', 
		height: '27em', 
		overflow: 'hidden', 
		position: 'relative'
	},

	childDiv: {
		position: 'absolute',
		top: 0, 
		bottom: 0, 
		left: 0, 
		right: '-17px', 
		overflowY: 'scroll'
	}
};

export default compose(withAlert())(StaffList);
