import React, { Component } from 'react';
import _ from 'lodash';
import { Button, Card, Col, Divider, Dropdown, Form, Input, Menu, message, Row, Select, Space, Table, Tag } from 'antd';
import { DownOutlined, PlusOutlined, SearchOutlined, UserOutlined } from '@ant-design/icons';
import { compose } from 'redux';
import { withAlert } from 'react-alert';
const { Search } = Input;
const { Option } = Select;
class MessengerIntegrationPage extends Component {
	constructor(props) {
		super(props);
		this.state = {
			pages: [],
			filters: {
				channel: '',
				search: ''
			}
		};
	}

	componentDidMount() {
		this.props.getPages();
	}

	componentWillUnmount() {
		this.props.resetPage();
	}

	render(){
		const { filters } = this.state;
		const { messengerIntegrationPage } = this.props;
		const { pages } = messengerIntegrationPage;
		// const { tickets: data } = this.props;

		const columns = [
			{
				title: 'Page ID',
				dataIndex: '_id',
				key: 'key'
			},
			{
				title: 'Page Name',
				dataIndex: 'pageName',
				key: 'key'
			},
			{
				title: 'Channel',
				dataIndex: 'channel',
				key: 'key'
			},
			{
				title: 'Action',
				dataIndex: 'action',
				key: 'key',
				render: (text) => {
					return (text === 'Disconnect') ? <span style={{color: '#e33434'}}>{text}</span> : <span style={{color: '#26d367'}}>{text}</span>;
				}
			}
		];

		const modalHandle = (type, e) => {
			//Check if 'e' has value and return the value else return empty string.
			const value = (e) ? e: ''; 
			const newFilter = filters;
			switch(type){
				case 'channel':
					newFilter.channel = value;
					break;
				case 'search':
					newFilter.search = value;
					break;
				default:
			}
			this.setState({filters: newFilter});	
		};

		const searchResults = () => {
			return pages.filter((element)=> {
				//If filter state is empty return true.
				const channelFilter = ("" === filters.channel) ? true : element.channel === filters.channel;
				const pageNameSearchFilter = ('' === filters.search) ? true : (element._id + element.pageName + element.channel).toLowerCase().includes(filters.search.toLowerCase());

				//return data that satisfies filtering 
				return (channelFilter && pageNameSearchFilter);
			});
		};

		return(
			<div style={{ width: '95%', margin: 'auto' }}>
				<Card title="Messenger Integration">
					<Space
						direction="vertical"
						size="middle"
						style={{ display: 'flex' }}
					>
						<Row justify="end">
							<Space>
								<Select
									style={styles.searchBox}
									allowClear
									placeholder="Search by Channel"
									onChange={(value) => modalHandle('channel', value)}
								>
									<Option value="Messenger">Messenger</Option>
									<Option value="Instagram">Instagram</Option>
									<Option value="Email">Email</Option>
									<Option value="WhatsApp">WhatsApp</Option>
								</Select>

								<Search
									placeholder="Search by Page ID, Page Name"
									allowClear
									onSearch={(value) => modalHandle('search', value)}
									style={{ width: 200 }}
								/>

								<Button
									type="primary"
									icon={<PlusOutlined />}
									style={{backgroundColor: '#09509d', borderColor: '#09509d'}}
									onClick={() => this.props.navigateToAddPage()}
								>
									New Page
								</Button>
							</Space>
						</Row>
						<Table
							dataSource={searchResults()}
							columns={columns}
						/>
					</Space>
				</Card>
			</div>
		);
	}
}
const styles = {
	searchBox: {
		width: '12em'
	}
};
export default compose(withAlert())(MessengerIntegrationPage);
