import * as ActionTypes from '../constants/ActionTypes';

const initialState = {
	loading: false,
	register: null
};

export default function reducer(state = initialState, action) {
	switch (action.type) {
		case ActionTypes.GET_REGISTER_PAGE_REQUEST:
		case ActionTypes.GET_REGISTER_PAGE_SUCCESS:
		case ActionTypes.GET_REGISTER_PAGE_ERROR:
		case ActionTypes.SUBMIT_REGISTER_PAGE_SUCCESS:
		case ActionTypes.SUBMIT_REGISTER_PAGE_REQUEST:
		case ActionTypes.SUBMIT_REGISTER_PAGE_ERROR:
			return {
				...state,
				...action.payload
			};
		case ActionTypes.RESET_REGISTER_PAGE_META:
			return {
				...state,
				meta: null
			};
		case ActionTypes.RESET_REGISTER_PAGE:
			return {
				...initialState
			};
		default:
			return state;
	}
}
