import * as ActionTypes from '../../constants/ActionTypes';
import API from '../../helpers/api';

export function getTwilioConnections() {
	return async dispatch => {
		dispatch({
			type: ActionTypes.GET_TWILIO_CONNECTIONS_REQUEST,
			payload: {
				loadingTwilioConnections: true
			}
		});
		const response = await API.admin.twilioconnections.twilio.getTwilioConnections();
		if (response.meta.code !== 200) {
			return dispatch({
				type: ActionTypes.GET_TWILIO_MESSAGE_BY_ID_ERROR,
				payload: {
					loadingTwilioConnections: false,
					meta: response.meta
				}
			});
		}

		// success
		const { twilio_connections } = response.data;
		dispatch({
			type: ActionTypes.GET_TWILIO_CONNECTIONS_SUCCESS,
			payload: {
				loadingTwilioConnections: false,
				twilio_connections
			}
		});
	};
}
export function getTwilioConnectionById({ id }) {
	return async dispatch => {
		dispatch({
			type: ActionTypes.GET_TWILIO_CONNECTION_BY_ID_REQUEST,
			payload: {
				loadingTwilioForm: true
			}
		});

		const response = await API.admin.twilioconnections.twilio.getTwilioConnectionById(id);
		if (response.meta.code !== 200) {
			return dispatch({
				type: ActionTypes.GET_TWILIO_CONNECTION_BY_ID_ERROR,
				payload: {
					loadingTwilioForm: false
				}
			});
		}

		const { twilio_connection } = response.data;
		dispatch({
			type: ActionTypes.GET_TWILIO_CONNECTION_BY_ID_SUCCESS,
			payload: {
				loadingTwilioForm: false,
				selectedTwilio_connection: twilio_connection
			}
		});
	};
}

export function submitTwilioConnection(fields) {
	return async (dispatch, getState) => {
		const { twilio_connections } = getState().settingsPageReducer;
		const { selectedTwilio_connection } = fields; // if true this is update

		console.log(selectedTwilio_connection);

		if (selectedTwilio_connection) {
			dispatch({
				type: ActionTypes.UPDATE_TWILIO_CONNECTION_REQUEST,
				payload: {
					loadingTwilioData: true
				}
			});
		} else {
			dispatch({
				type: ActionTypes.ADD_TWILIO_CONNECTION_REQUEST,
				payload: { loadingTwilioData: true }
			});
		}
		// For updating

		const { name, _id, account_sid, auth_token, enabled } = fields;

		const body = {
			twilio_connection: {
				name: name,
				_id: _id,
				account_sid: account_sid,
				auth_token: auth_token,
				enabled: enabled
			}
		};
		let response;
		const id = _id;
		if (selectedTwilio_connection) {
			//For update
			response = await API.admin.twilioconnections.twilio.updateTwilioConnectionById({ id, body });
		} else {
			//For Add
			response = await API.admin.twilioconnections.twilio.createTwilioConnection(body);
		}

		if (response.meta.code !== 200) {
			if (selectedTwilio_connection) {
				//dispatch for update Error

				return dispatch({
					type: ActionTypes.UPDATE_TWILIO_CONNECTION_ERROR,
					payload: {
						loadingTwilioData: false,
						metta: response.meta
					}
				});
			} else {
				// dispatch for add Error
				return dispatch({
					type: ActionTypes.ADD_TWILIO_CONNECTION_ERROR,
					payload: {
						loadingTwilioData: false,
						meta: response.meta
					}
				});
			}
		}
		const { twilio_connection } = response.data;

		let payload = {
			loadingTwilioData: false,
			meta: response.meta
		};

		if (selectedTwilio_connection) {
			const updatedtwilio_connection = [...twilio_connections];

			const findIndexToupdate = updatedtwilio_connection.findIndex(x => x.id === twilio_connection._id);

			updatedtwilio_connection[findIndexToupdate] = { ...twilio_connection };
			payload.twilio_connections = updatedtwilio_connection;
		} else {
			//For add success dispatch
			payload.twilio_connections = [...twilio_connections, twilio_connection];
		}

		dispatch({
			type: ActionTypes.UPDATE_TWILIO_CONNECTION_SUCCESS,
			payload
		});
	};
}
