/* eslint-disable jsx-a11y/anchor-has-content */
/* eslint-disable react/jsx-max-props-per-line */
/* eslint-disable react/jsx-no-target-blank */
import React, { Component } from 'react';
import { Button, Form, FormLayout, Card, DisplayText, TextStyle, Layout } from '@shopify/polaris';
import { Field, reduxForm } from 'redux-form';
import { compose } from 'redux';
import { withAlert } from 'react-alert';
import GeneralHelper from '../../helpers/general';
import { renderFormInput, renderCheckbox,  validateRequired } from '../../helpers/redux_form';

class CustomerForm extends Component {
	componentWillUnmount() {
		this.props.reset();
	}
	componentDidMount() {
	}
	render() {
		const { registerPageProps } = this.props;
		return (
			<Layout>
				<Layout.Section>
					<div style={{ width: '50%', margin: 'auto', marginBottom: 100 }}>
						<center>
							<DisplayText 
								size="large" 
								element="h2"
							>
								We need more information about you.
							</DisplayText>
							<DisplayText 
								size="small"
							>
								Please fill up the form.
							</DisplayText>
						</center>
						<br />
						<Card>
							<Card.Section>
								<p>
									For Facebook Business Manager ID:
									<br />
									To locate your Facebook Business Manager ID, click &nbsp; 
									<a 
										href="https://business.facebook.com/settings/info" target="_blank" 
									>
										here
									</a>.
									<br />
									If you do not have a Facebook Business Manager ID yet, click  &nbsp;
									<a 
										href="https://www.facebook.com/business/help/1710077379203657" target="_blank" 
									>
										here
									</a>.
									<br />
									For Business Verification field;
									<br />
									Check your business information under Business &nbsp;
									<a 
										href="https://business.facebook.com/settings/info" target="_blank" 
									>
										Verification
									</a> Status.
									<br />
									For the <a 
										href="https://www.facebook.com/business/help/2058515294227817" target="_blank"
									        >
										Unverified
									</a> account.
								</p>
							</Card.Section>
						</Card>
						<Card>
							<Card.Section>
								<Form>
									<FormLayout>
										<Field
											name="first_name"
											label="First Name*"
											component={renderFormInput}
											validate={[validateRequired]}
										/>
										<Field
											name="last_name"
											label="Last Name*"
											component={renderFormInput}
											validate={[validateRequired]}
										/>
										<Field
											name="phone_number"
											label="Phone Number"
											component={renderFormInput}
										/>
										<Field
											name="email_address"
											label="Email Address*"
											component={renderFormInput}
										/>
										<Field
											name="company_name"
											label="Company Name*"
											component={renderFormInput}
										/>
										<Field
											name="company_website"
											label="Company Website*"
											component={renderFormInput}
										/>
										<Field
											name="company_hq_country"
											label="Company HQ Country*"
											component={renderFormInput}
										/>
										<Field
											name="twilio_account_sid"
											label="Twilio Account SID*"
											component={renderFormInput}
										/>
										<Field
											name="company_vertical"
											label="Company Vertical*"
											component={renderFormInput}
										/>
										<Field
											name="use_case"
											label="What is your use case?*"
											component={renderFormInput}
										/>
										<Field
											name="region_sending_messages"
											label="Which regions are you sending messages to?*"
											component={renderFormInput}
										/>
										<Field
											name="how_many_messages"
											label="How many messages are you sending per month during the first year?*"
											component={renderFormInput}
										/>
										<Field
											name="provision"
											label="I wish to provision this account with a third party vendor*"
											component={renderFormInput}
										/>
										<Field
											name="assign_approval"
											label="Assign approval to alternate account SID (Optional)*"
											component={renderFormInput}
										/>
										<Field
											name="twilio_can_modify"
											label="I understand twilio can modify my SMS/VOICE URL and that approval is only for the brand submitted.*"
											component={renderFormInput}
										/>
										<Field
											name="agree_policies"
											label="Do you agree that all information you provide can be used to screen your application and ensure compliance with WhatsApp and Facebook policies?"
											component={renderCheckbox}
											type="checkbox"
										/>
										<Button 
											primary 
											fullWidth
										>
											Create Account
										</Button>
									</FormLayout>
								</Form>
							</Card.Section>
						</Card>
					</div>
				</Layout.Section>
			</Layout>
		);
	}
}


export default compose(
	withAlert(),
	reduxForm({
		form: 'CustomerForm'
	})
)(CustomerForm);