import React, { Component } from 'react';
import { Form, Row, Col, Input, Button, DatePicker, Select, notification, Modal } from 'antd';
import { MinusCircleOutlined, PlusSquareFilled, ExclamationCircleOutlined } from '@ant-design/icons';
import CommonEnum from '../../../../constants/CommonEnum';
import LabelHelper from '../../../../helpers/Label';
import GeneralHelper from '../../../../helpers/general';

const { Option } = Select;

const permissions = [
	{
		name: 'Permission A',
		value: 'permission_a'
	},
	{
		name: 'Permission B',
		value: 'permission_b'
	}
];

class StaffBasicForm extends Component {
	formRef = React.createRef();

	state = {
		disabled: true
	};

	async submitFormData(values) {
		// You can add more pre-processing of the form data here
		const { onSubmit } = this.props;
		const { staff_id, name, email, status } = values;
		const fields = {
			staff_id,
			name,
			email,
			status
		};

		await onSubmit(fields);
	};

	checkFormValues = () => {
		if (
			this.formRef.current.getFieldValue('name') &&
			this.formRef.current.getFieldValue('email') &&
			this.formRef.current.getFieldValue('status')
		) {
			this.setState({
				disabled: false
			});
		}
	}


	renderForm() {
		const { staff, loadingSubmit } = this.props;
		const { _id, name, email, enabled } = staff || {};
		return(
			<>
				<Form
					layout="vertical"
					ref={this.formRef}
					initialValues={{
						staff_id: _id,
						name,
						email,
						status: enabled ? 'active' : 'inactive'
					}}
					style={{ width: 400, marginTop: '2rem' }}
					onFinish={async (values) => { await this.submitFormData(values)}}
				>
					<Form.Item
						name="staff_id"
						label="Staff ID"
					>
						<Input
							disabled={true}
							onChange={this.checkFormValues}
						/>
					</Form.Item>
					<Form.Item
						name="name"
						label="Name"
						rules={[{ required: true, message: 'Name is invalid' }]}
					>
						<Input/>
					</Form.Item>
					<Form.Item
						name="email"
						label="Email Address"
						onChange={this.checkFormValues}
						rules={[
							{ required: true, type: 'email', message: 'Please enter a valid email' }
						]}
					>
						<Input/>
					</Form.Item>
					<Form.Item
						name="status"
						label="Status"
					>
						<Select
							onChange={this.checkFormValues}
						>
							{CommonEnum.status.map(status => {
								return (
									<Option
										value={status}
									>
										{LabelHelper.staffStatus(status)}
									</Option>
								);
							})}
						</Select>
					</Form.Item>
					<Form.List
						name="selected_locations"
					>
						{(fields, { add, remove }) => (
							<>
								{fields.map(({ key, name, fieldKey, ...restField }, index) => {
									return(
										<div
											key={key}
											style={{ display: 'flex', marginBottom: -20 }}
										>
											<Form.Item
												{...restField}
												name={[name, 'permission']}
												fieldKey={[fieldKey, 'key']}
												label={index === 0 ? 'Permission:' : ''}
												style={{ width: '100%', paddingRight: 10 }}
												rules={[{ required: true, message: 'Missing value' }]}
											>
												<Select
													key={key}
													onChange={(val) => {
														const filtered = [...this.state.current_selected_locations.filter(_id => _id !== this.state.current_selected_locations[name])];
														filtered.splice(name, 0, val);
														this.setState({
															current_selected_locations: [...filtered]
														});
														// this.checkFormValues();
														const filteredValues = this.formRef.current.getFieldValue('selected_doors') && this.formRef.current.getFieldValue('selected_doors').filter(obj => obj ? obj.location !== val : false);
														this.formRef.current.setFieldsValue({ 'selected_doors': filteredValues });
														// this.checkFormValues();
													}}
												>
													{
														permissions && permissions.length && permissions.map((permission, index) => {
															return (														
																<Select.Option
																	key={index}
																	value={permission.value}
																	// disabled={this.state.current_selected_locations && this.state.current_selected_locations.includes(location._id) && this.state.current_selected_locations[name] !== location._id}
																>
																	{permission.name}
																</Select.Option>
															);
														})
													}
												</Select>
											</Form.Item>
											<MinusCircleOutlined
												// onClick={() => {
												// 	remove(name);
												// 	// 	this.checkFormValues();
												// 	this.setState({
												// 		current_selected_locations: this.state.current_selected_locations.length ? this.state.current_selected_locations.filter(id => id !== this.state.current_selected_locations[name]) : this.state.current_selected_locations
												// 	});
												// 	this.checkFormValues();
												// }}
												style={( index === 0 ? { marginTop: 38 } : { marginTop: 8 })}	
											/>
										</div>
									);
								}
								)}
							
								<Form.Item 
									style={{ marginTop: 10 }}
								>
								Add Permission &nbsp;
									<PlusSquareFilled 
										onClick={() => {
											add();
											// this.checkFormValues();
										}}
										style={{ fontSize: '110%', backgroundColor: '#fff', color: '#0B3266', cursor: 'pointer' }}
									/>
								</Form.Item>
							</>
						)}
					</Form.List>
					<Form.Item style={{ float: 'right' }}>
						<Button
							size="middle"
							type="primary"
							htmlType="submit"
							style={{ width: 100, borderRadius: 3 }}
							loading={loadingSubmit}
							disabled={this.state.disabled}
						>Save</Button>
					</Form.Item>
				</Form>
			</>
		);
	}

	render() {
		return (
			<>
				{this.renderForm()}
			</>
		);
	}
}


export default StaffBasicForm;