import React, { Component } from 'react';
import _ from 'lodash';
import { compose } from 'redux';
import { withAlert } from 'react-alert';
import { Avatar, Card, Space } from 'antd';
class Customer extends Component {
	constructor(props) {
		super(props);
		this.state = {
			
		};
	}
	render(){
  
		const { name, _id, status, img, lastMessage, date, channel } = this.props.customer;
		return(
			<div style={styles.parentContainer}>
				<div style={styles.childContainer}>
					<Space>
						<Avatar
							src={img}
							size={64}
						/>
						<div>
							<h1 style={{margin: 0}}>{name}</h1>
							<h2 style={{color: 'grey'}}>{lastMessage}</h2>
						</div>
					</Space>
				</div>
				<div>
					<h1 style={{margin: 0}}>{date}</h1>
					<h2>{channel}</h2>
				</div>
			</div>
		);
	}
}

const styles = {
	hover: {
			
	},
	parentContainer: {
		paddingBottom: 16,
		width: '100%', 
		display: 'flex', 
		alignItems: 'center', 
		justifyContent: 'space-between'
	},
	childContainer: {
		display: 'flex', 
		alignItems: 'center'
	}
};

export default compose(withAlert())(Customer);
