import { push } from 'connected-react-router';
import * as ActionTypes from '../constants/ActionTypes';
import API from '../helpers/api';

export function getStaffById(id) {
	return async dispatch => {
		dispatch({
			type: ActionTypes.GET_STAFF_BY_ID_REQUEST,
			payload: {
				loading: true
			}
		});

		const response = await API.admin.users.getStaffById(id);

		if (response.meta.code !== 200) {
			return dispatch({
				type: ActionTypes.GET_STAFF_BY_ID_ERROR,
				payload: {
					loading: false
				}
			});
		}

		const { staff } = response.data;
		dispatch({
			type: ActionTypes.GET_STAFF_BY_ID_SUCCESS,
			payload: {
				loading: false,
				staff
			}
		});
	};
};

export function onSubmit(fields) {
	return async (dispatch, getState) => {
		const { staff } = getState().staffDetailsPageReducer;

		dispatch({
			type: ActionTypes.SUBMIT_STAFF_REQUEST,
			payload: {
				loadingSubmit: true
			}
		});

		const body = {
			staff: {
				password: 123456,
				...fields
			}
		};

		let response;
		if (staff._id) {
			body.staff.updated_at = staff.updated_at;
			response = await API.admin.users.updateStaffById(staff._id, body);
		} else {
			response = await API.admin.users.createStaff(body);
		}

		if (response.meta.code !== 200) {
			return dispatch({
				type: ActionTypes.SUBMIT_STAFF_ERROR,
				payload: {
					loadingSubmit: false,
					meta: response.meta
				}
			});
		}

		const { staff: newStaff } = response.data;
		dispatch({
			type: ActionTypes.SUBMIT_STAFF_SUCCESS,
			payload: {
				loadingSubmit: false,
				meta: response.meta,
				staff: newStaff
			}
		});
		dispatch(push(`/staffs/id/basic?id=${newStaff._id}`));
	};
}

export function navigateToListPage() {
	return async dispatch => {
		dispatch(push('/staffs'));
	};
};

export function resetPage() {
	return async dispatch => {
		return dispatch({
			type: ActionTypes.RESET_STAFF_DETAILS_PAGE
		})
	};
};

export function resetMeta() {
	return async dispatch => {
		return dispatch({
			type: ActionTypes.RESET_STAFF_DETAILS_PAGE_META
		})
	};
};
