import _ from 'lodash';
import React, { Component } from 'react';
import PropTypes from 'prop-types';
import { Link } from 'react-router-dom';
import { Form, FormLayout, Card, DisplayText, TextStyle, Layout } from '@shopify/polaris';
import { Button, Row } from 'antd';
import { Field, reduxForm } from 'redux-form';
import { compose } from 'redux';
import { withAlert } from 'react-alert';
import { renderFormInput } from '../../helpers/redux_form';

class ForgotPasswordPage extends Component {
	componentWillUnmount() {
		this.props.reset();
	}
	componentDidMount() {
	}
	render() {
		const { forgotPasswordPageProps } = this.props;
		return (
			<Row
				style={{
					height: '92vh'
				}}
				justify="center"
				align="middle"
			>
				<div style={{ width: '30%', margin: 'auto'}}>
					<br />
					<Card>
						<Card.Section>
							<div style={{padding: '1em'}}>
								<div style={{ marginBottom: '2em'}}>
									<span style={{fontSize: '1.5em'}}><strong>Forgot your password?</strong></span>
									<br />
									<span>
										Please enter your email address associated with your account.
									</span>
								</div>

								<Form>
									<FormLayout>
										<Field
											name="email_address"
											label="Email Address"
											component={renderFormInput}
										/>
										{/* <center>
											<TextStyle>
													Already have an account? &nbsp;
												<Link to="/login">Login</Link>
											</TextStyle>
										</center> */}
										<Button
											type="primary"
											block
											style={{background: '#09509d', borderColor: '#09509d'}}
										>
											Submit
										</Button>
									</FormLayout>
								</Form>
							</div>
						</Card.Section>
					</Card>
				</div>
			</Row>
		);
	}
}


export default compose(
	withAlert(),
	reduxForm({
		form: 'ForgotPasswordPage'
	})
)(ForgotPasswordPage);