import * as ActionTypes from '../constants/ActionTypes';

const initialState = {
	loading: false,
	reports: []
};

export default function reducer(state = initialState, action) {
	switch (action.type) {
		case ActionTypes.GET_REPORTS_REQUEST:
		case ActionTypes.GET_REPORTS_ERROR:
		case ActionTypes.GET_REPORTS_SUCCESS:
			return {
				...state,
				...action.payload
			};
		case ActionTypes.RESET_REPORTS_PAGE_META:
			return {
				...state,
				meta: null
			};
		case ActionTypes.RESET_ACCOUNT_PAGE:
			return {
				...initialState
			};
		default:
			return state;
	}
}
