import * as ActionTypes from '../constants/ActionTypes';

const initialState = {
	loading: false,
	pages: []
};

export default function reducer(state = initialState, action) {
	switch (action.type) {
		case ActionTypes.GET_PAGES_REQUEST:
		case ActionTypes.GET_PAGES_ERROR:
		case ActionTypes.GET_PAGES_SUCCESS:
			return {
				...state,
				...action.payload
			};
		case ActionTypes.RESET_ACCOUNT_PAGE_META:
			return {
				...state,
				meta: null
			};
		case ActionTypes.RESET_ACCOUNT_PAGE:
			return {
				...initialState
			};
		default:
			return state;
	}
}
