import React, { Component, Grid } from 'react';
//import useBreakpoint from 'use-breakpoint';
import {
	MenuUnfoldOutlined,
	MenuFoldOutlined,
	UserOutlined,
	LogoutOutlined
} from '@ant-design/icons';
import { Space, Divider, Layout, Menu, Avatar, Select, Form, Typography, Dropdown, Button, Row } from 'antd';
import _ from 'lodash';
import LocalStorageHelper from '../../helpers/local_storage';

const { Header } = Layout;
const { SubMenu } = Menu;
const { Option } = Select;
const { Title, Link } = Typography;


export class NavigationHeader extends Component {
	handleLanguageMenuClick = () => { };
	handleSettingMenuClick = () => { };
	handleLogout = () => {
		this.props.history.push('/logout');
	};

	getUsernameAvatar = (username, size = 'large') => {
		return (
			<div>
				<Avatar
					style={{
						color: '#322105',
						backgroundColor: '#faa61a',
						verticalAlign: 'middle'
					}}
					size={size}
				>
					{username ? username.charAt(0).toUpperCase() : ''}
				</Avatar>
				<span style={{ paddingLeft: 20 }}>{username}</span>
			</div>
		);
	};


	render() {

		const userMenu = (
			<Menu>
				<Menu.Item
					key="setting:2"
					onClick={this.handleLogout}
				>
					<span>
						<Space>
							<LogoutOutlined />
							Logout
						</Space>
					</span>
				</Menu.Item>
			</Menu>
		);

		return (
			<React.Fragment>
				<div>
					<Header
						trigger={null}
						className="header fixed-header-action"
						style={{ background: '#fff', width: '100%'}}
					>
						<Space
							align="end"
							style={{display: 'flex', justifyContent: 'space-between'}}
						>
							<> </>
							<Dropdown
								className="header-user-part"
								overlay={userMenu}
								placement="bottomRight"
							>
								<Row
									justify="space-around"
									style={{
										alignSelf: 'center',
										display: 'inline-flex',
										marginRight: '15px',
										maxWidth: '200px',
										border: 'none',
										whiteSpace: 'nowrap'
									}}
								>
									{this.getUsernameAvatar(LocalStorageHelper.getName())}
								</Row>
							</Dropdown>
						</Space>
					</Header>
				</div>
			</React.Fragment>
		);
	}
}

export default NavigationHeader;
