const LabelHelper = {
	staffStatus(casUserType) {
		switch (casUserType) {
			case 'online':
				return 'Online';
			case 'offline':
				return 'Offline';
			default:
		}
		return '';
	},
	connectionType(connection) {
		switch (connection) {
			case 'facebook':
				return { title: 'Facebook', description: 'Create tickets from Facebook messages and comments.' };
			case 'whatsapp':
				return { title: 'WhatsApp', description: 'Create tickets from WhatsApp messages' };
			default:
		}
		return { title: '', description: '' };
	},
	staffStatus(status) {
		switch (status) {
			case 'active':
				return 'Active';
			case 'inactive':
				return 'Inactve';
			default:
				return status;
		}
	}
};

export default LabelHelper;
