import React, { Component } from 'react';
// import PropTypes from 'prop-types';
import { Banner, Layout } from '@shopify/polaris';
import { withAlert } from 'react-alert';
import UrlParse from 'url-parse';
import { compose } from 'redux';
import TicketList from './components/TicketList';
import Conversation from './components/Conversation';
import FacebookPostComment from './components/FacebookPostComment';
import TicketInformation from './components/TicketInformation';
import LocalStorageHelper from '../../helpers/local_storage';
// import PendingIntegration from './components/PendingIntegration';
import InstagramPostComment from './components/InstagramPostComment';
import FacebookChatView from './components/FacebookChatView';

class TicketPage extends Component {
	constructor(props) {
		super(props);
		this.state = {
			showNewTicketAssignedModal: false,
			newTicketId: null
		};
	}

	componentDidUpdate(prevProps) {
		const { meta: prevMeta, loadingNewTicket: prev_loadingNewTicket } = prevProps.ticketPageProps;
		const { meta, loadingNewTicket, newTicket } = this.props.ticketPageProps;

		if (meta && meta !== prevMeta) {
			const alertOptions = {
				type: meta.code === 200 ? 'success' : 'error',
				...(meta.code === 40399 && { timeout: 0 })
			};

			this.props.alert.show(meta.message, alertOptions);
			this.props.resetMeta();
		}

		// // check if pending ticket and the ticket is assigned to the logged in staff.
		if (newTicket && !loadingNewTicket && loadingNewTicket !== prev_loadingNewTicket) {
			const loggedInUserId = LocalStorageHelper.getLoggedIn()._id;
			// this will show notification on upper right.
			if (newTicket.status === 'pending_acceptance' && newTicket.staff_id === loggedInUserId) {
				this.setState({ showNewTicketAssignedModal: true, newTicketId: newTicket._id });
			}
		}
	}

	onAcceptTicket = () => {
		this.setState({ showNewTicketAssignedModal: false, newTicketId: null });
		this.props.updateTicketStatus({ ticket_id: this.state.newTicketId, status: 'in-progress' });
	};

	render() {
		const { location } = this.props;
		const { showNewTicketAssignedModal } = this.state;
		const { query } = UrlParse(location.search, true);
		return (
			<Layout>
				{/* Pending Twilio account for integration */}
				{/* <Layout.Section>
					<PendingIntegration />
				</Layout.Section> */}

				<Layout.Section oneThird>
					<TicketList {...this.props} />
				</Layout.Section>
				{query.ticket_id && (
					<React.Fragment>
						<Layout.Section oneThird>
							{query.ticket_type && query.ticket_type === 'whatsapp' && <Conversation {...this.props} />}
							{query.ticket_type && query.ticket_type === 'ig_comment' && <InstagramPostComment {...this.props} />}
							{query.ticket_type && query.ticket_type === 'fb_comment' && <FacebookPostComment {...this.props} />}
							{query.ticket_type && query.ticket_type === 'fb_messenger' && <FacebookChatView {...this.props} />}
						</Layout.Section>
						<Layout.Section oneThird>
							<TicketInformation {...this.props} />
						</Layout.Section>
					</React.Fragment>
				)}
				{/* Show a modal if there is new ticket assigned to the staff */}
				{showNewTicketAssignedModal && (
					<div style={{ width: 400, position: 'absolute', right: 0, top: 0, zIndex: 999, margin: 5 }}>
						<Banner
							title="Notification"
							status="success"
							onDismiss={() => this.setState({ showNewTicketAssignedModal: false, newTicketId: null })}
							action={{ content: 'Accept', onAction: () => this.onAcceptTicket() }}
						>
							<p>New ticket has been assigned to you. Click Accept and start chatting.</p>
						</Banner>
					</div>
				)}
			</Layout>
		);
	}
}

export default compose(withAlert())(TicketPage);
