/* eslint-disable indent */
import React, { Component } from 'react';
import { Card, Avatar, Icon, TextField, Layout, TextContainer, SkeletonDisplayText, SkeletonBodyText } from '@shopify/polaris';
import {
	CircleInformationMajorFilled, ChevronDownMinor, SmileyHappyMajorMonotone, SendMajorMonotone, ThumbsUpMajorMonotone
} from '@shopify/polaris-icons';
import Picker, { SKIN_TONE_NEUTRAL } from 'emoji-picker-react';
import Moment from 'moment';
import { compose } from 'redux';
import { reduxForm } from 'redux-form';
import _ from 'lodash';
import UrlParse from 'url-parse';
import Dropzone from 'react-dropzone';
import ioClient from 'socket.io-client';
import { withAlert } from 'react-alert';
import GeneralHelper from '../../../helpers/general';
import config from './../../../config';
import FileTypes from '../../../constants/FileTypes';
import LocalStorageHelper from '../../../helpers/local_storage';
const moment = Moment;

class FacebookChatView extends Component {

	constructor(props){
		super(props);
		this.state = {
			messageValue: '',
			showEmojisPicker: false,
			ticket_id: null,
			loggedIn: null,
			file: null
		};

		this._handleClick = this._handleClick.bind(this);
		this._handleOutsideClick = this._handleOutsideClick.bind(this);

		this.socket = ioClient(config.socket.host, {
			path: config.socket.path,
			forceNew: true
		});
	}

	componentDidMount() {
		const { getTicketById, getFbMessages, getFbMessagesByID,
			getUsers, history, location } = this.props;
		const { query } = UrlParse(location.search, true);
		const { socket } = this;
		const { ticket_id } = query;
		// const loggedInUser = JSON.parse(localStorage.getItem('app'));
		if (!ticket_id) {
			return history.push('/tickets?type=all');
		}

		const loggedIn = LocalStorageHelper.getLoggedIn();
		const loggedInType = LocalStorageHelper.getLoginType();

		// get ticket by id .
		getTicketById({ ticket_id });

		// get messages
		getFbMessages({ ticket_id });

		// get users list, so it ticket can change the asignee
		getUsers();

		socket.on('connection', () => { });
		socket.on('valid_connection', () => {
			this.socketJoinTicket({ ticket_id, user_id: loggedIn.user_id || loggedIn._id, staff_id: loggedIn._id });
			// open chat emiting to inform that all messages are read.
			// socket.emit('open_chat',{ session_id, user_id: loggedInUserId  });
			setTimeout(() => {
				this.messagesEnd && this.messagesEnd.scrollIntoView({ behavior: 'auto' });
			}, 500);
		});
		this.socket.on('message', async message => {
			// RECEIVER
			if (message) {
				getFbMessagesByID({ message_id: message.facebook_message_id });
				// emit || receiver able read the message.
				socket.emit('open_chat',{ user_id: loggedIn.user_id || loggedIn._id, ticket_id, staff_id: loggedIn._id  });

				// get twillio message by id.
				this.messagesEnd.scrollIntoView({ behavior: 'smooth' });
			}
		});


		this.setState({
			loggedIn: {...loggedIn, loggedInType },
			ticket_id
		});
	}

	// show this if the chatview is loading.
	skeletonConversation = () => {
		return (
			<Layout>
				<Layout.Section>
					<Card sectioned>
						<TextContainer>
							<SkeletonDisplayText size="extraLarge" />
							<SkeletonBodyText lines={2}/>
							<SkeletonBodyText lines={4}/>
							<SkeletonBodyText lines={8}/>
							<SkeletonBodyText lines={4}/>
						</TextContainer>
					</Card>
				</Layout.Section>
			</Layout>
		);
	};

	componentDidUpdate(prevProps) {
		const { location: prevLocation, ticketPageProps: prev_ticketPageprops } = prevProps;
		const { location, getFbMessages, getTicketById, ticketPageProps } = this.props;
		const { query } = UrlParse(location.search, true);
		const { query: prevQuery } = UrlParse(prevLocation.search, true);
		const { ticket_id } = query;
		if (ticket_id && ticket_id !== prevQuery.ticket_id) {
			
			getFbMessages({ ticket_id });
			getTicketById({ ticket_id });
			this.socketJoinTicket({ ticket_id });
			setTimeout(() => {
				this.messagesEnd.scrollIntoView && this.messagesEnd.scrollIntoView({ behavior: 'auto' });
			}, 500);

			this.setState({
				ticket_id
			});
		}
		
		// if(this.props.ticketPageProps.fb_messages.length !== prevProps.ticketPageProps.fb_messages.length){
		// 	setTimeout(() => {
		// 		this.messagesEnd.scrollIntoView && this.messagesEnd.scrollIntoView({ behavior: 'smooth' });
		// 	}, 500);
		// }
	}

	componentWillUnmount() {
		this.socket.disconnect();
		this.props.resetTicket();
	}

	
	socketJoinTicket = ({ ticket_id, user_id, staff_id }) => {
		const { socket } = this;
		socket.emit('join_ticket', { ticket_id });
		socket.emit('open_chat',{ user_id, ticket_id, staff_id  });

	}

	socketConnection = () => {
		const { socket } = this;
		const { getFbMessagesByID, location } = this.props;
		const { query } = UrlParse(location.search, true);
		const { ticket_id } = query;
		socket.on('connection', () => { });
		socket.on('valid_connection', () => {
			socket.emit('join_ticket', { ticket_id });
			// open chat emiting to inform that all messages are read.
			// socket.emit('open_chat',{ session_id, user_id: loggedInUserId  });
			this.messagesEnd.scrollIntoView({ behavior: 'auto' });
		});
		this.socket.on('message', async message => {
			// RECEIVER
			if (message) {
				getFbMessagesByID({ message_id: message.facebook_message_id });
				// get twillio message by id.
				this.messagesEnd.scrollIntoView({ behavior: 'smooth' });
			}
		});
	}

	_chatBubble = (v) => {
		return (
			<div style={{display: 'flex', margin: v.flow === "inbound" ? '15px 20px 15px 0px' : '15px 0px 20px 0px', justifyContent: v.flow === "outbound" ? 'flex-end' : null}}>
				<div style={styles.chatBubble}>
					<div style={styles.dFlex}>
						<div style={styles.bubbleMessageHolder}>
							<p style={styles.tJustify}>
								{
									v.message
								}
							</p>
						</div>
						<div>
							<button
								className="fb-chatview-btn"
								onClick={()=>console.log('Hello World')}
								style={styles.transparentBtn}
							>
								<Icon
									source={ChevronDownMinor}
									color="skyDark"
								/>
							</button>
						</div>
					</div>
					<div style={styles.bubbleDateHolder}>
						<div>
							<small style={styles.bubbleDate}>{ moment(v.flow === "outbound" ? v.timestamp * 1000 : v.timestamp).format('hh:mm A') }</small>
						</div>
					</div>
				</div>
			</div>
		);
	}

    _handeMessageValue = v => this.setState({messageValue: v});

    _handleEmojiPicked = v => this.setState({messageValue: this.state.messageValue.concat(v.emoji)});

    _handleClick() {

    	if (!this.state.showEmojisPicker) {
			document.addEventListener('click', this._handleOutsideClick, false);
    	} else {
			document.removeEventListener('click', this._handleOutsideClick, false);
    	}
    
    	this.setState(prevState => ({
    		showEmojisPicker: !prevState.showEmojisPicker
    	}));
    }

    _handleOutsideClick(e) {

    	if (this.node.contains(e.target)) {
    		return;
    	}
        
    	this._handleClick();
    }

    _handleOnClickSend = (type) => this._handleMessageRequest(type);

    _handleMessageRequest = async (type) => {

		const { messageValue, ticket_id, file } = this.state;
		if (messageValue && type === "send") {
			// text message type
			await this.props.sendMessageFb({ message: messageValue, ticket_id });

		} else {
			await this.props.sendMessageFb({ message: "", ticket_id });
		}
		this.setState({
			messageValue: '',
			file: null
		});
    }

    render(){

		const { messageValue, file, loggedIn, showEmojisPicker } = this.state;
		const { updateTicketStatus, ticketPageProps } = this.props;
		const { fb_messages, ticket, loadingSelectedTicket, loadingUpdateTicket, loadingSendMessage, loadingMessages } = ticketPageProps;
		const { renderPreviewModal, handleSendMessage, dropZoneAttachment } = this;
		const customer_phone_number = _.get(ticket, 'twilio_contact.phone');
		const customer_name = _.get(ticket, 'twilio_contact.name');
		const ticket_status = _.get(ticket, 'status');
		const loggedInType = _.get(loggedIn, 'loggedInType'); //user or staff
		const isStaffLoggedIn = loggedInType === 'staff' ? true : false;
		const loggedInId = _.get(loggedIn, '_id');
		const ticketAssigneeId = _.get(ticket, 'staff_id');
		const isAssigned = loggedInId === ticketAssigneeId; //identify if the ticket is assigned to the logged in staff.
		const ticketIsPendingAcceptance = isAssigned && ticket_status === 'pending_acceptance'; //check if the ticket is assigned to the logged in staff & ticket status is pending_acceptance
		const ticketIsResolved = ticket_status === 'resolved';
		const showChatInput = isAssigned && !ticketIsResolved && !ticketIsPendingAcceptance;
		const sortMessagesBasedOnTime = fb_messages.sort(function (left, right) {
			return moment.utc(left.created_at).diff(moment.utc(right.created_at));
		});
		const messagesGroupByDate = sortMessagesBasedOnTime && _(sortMessagesBasedOnTime)
			.groupBy(message => message && moment(message.created_at).format('ddd, D MMM YYYY'))
			.map((value, key) => ({ dateTime: key, messages: value }))
			.value();
		if (loadingSelectedTicket || loadingMessages) {
			return (
				this.skeletonConversation()
			);
		}
        
    	const sampleData = [
    		{
    			senderPhoto: "https://picsum.photos/100/100",
    			senderName: "Lee Chan"
    		}
		];

		return (
			<Card>
				<div>
					<div style={styles.chatHeader}>
						<div>
							<Avatar 
								customer
								source={sampleData[0].senderPhoto}
							/>
						</div>
						<div style={styles.chatHeaderName}>
							{sampleData[0].senderName}
						</div>
						<div>
							<Icon
								source={CircleInformationMajorFilled}
								color="white"
							/>
						</div>
					</div>
					<div
						
						style={styles.chatBubbleHolder}
					>
						{messagesGroupByDate && messagesGroupByDate.map((item, index) => {
							return (
								<React.Fragment  key={index}>
									<div 
										style={{ textAlign: 'center', fontSize: 11, marginBottom: 5, marginTop: 5 }}
									>{item.dateTime}</div>
									{_.get(item, 'messages').map((msg, idx) => {
										if (msg) {
											return (
												<div key={idx}>
													{this._chatBubble(msg)}
												</div>
											);
										}
									})}
								</React.Fragment>
							);})
						}
						<div 
							ref={el => { this.messagesEnd = el; }}
							style={{ float: 'left', clear: 'both' }}
						></div>
					</div>
					{
						showChatInput &&
						<div style={styles.bgWhite}>
							<div>
								<TextField
									placeholder="Type message..."
									value={messageValue}
									onChange={this._handeMessageValue}
									multiline
								/>
							</div>
							<div>
								<div
									style={styles.chatBottomHolder}
									ref={node => { this.node = node; }}
								>
									<div>
										{
											showEmojisPicker ?
												<div
													style={styles.pRelative}
												>
													<div
														style={styles.pAbsolute}
													>
														<Picker
															skinTone={SKIN_TONE_NEUTRAL}
															onEmojiClick={(e,emoji) => this._handleEmojiPicked(emoji)}
														/>
													</div>
												</div>
											: null
										}
										<div>
											<button
												className="fb-chatview-btn"
												onClick={this._handleClick}
												style={styles.transparentBtn}
											>
												<Icon
													source={SmileyHappyMajorMonotone}
													color={showEmojisPicker ? "blue" : "skyDark"}
												/>
											</button>
										</div>
									</div>
									<div style={styles.mLeft}>
										{
											messageValue.trim() === "" ?
												<button
													className="fb-chatview-btn"
													style={styles.transparentBtn}
													onClick={()=>this._handleOnClickSend('like')}
												>
													<Icon
														source={ThumbsUpMajorMonotone}
														color="blue"
													/>
												</button>
												: 
												<button
													className="fb-chatview-btn"
													style={styles.transparentBtn}
													onClick={()=>this._handleOnClickSend('send')}
												>
													<Icon
														source={SendMajorMonotone}
														color="blue"
													/>
												</button>
										}
									</div>
								</div>
							</div>
						</div>
					}
				</div>
			</Card>
    	);
    }
}

const styles = {
	chatHeader: {
		display: 'flex',
		flexDirection: 'row',
		alignItems: 'center',
		padding: '15px',
		backgroundColor: '#3b5998 ',
		color: '#f2f2f2',
		fontWeight: 600,
		borderTopLeftRadius: 3,
		borderTopRightRadius: 3
	},
	chatHeaderName: {
		marginLeft: '15px',
		marginRight: 'auto'
	},
	chatBubbleHolder: {
		height: 600,
		backgroundColor: '#f2f2f2',
		padding: '20px 15px 15px 15px',
		overflowY: 'auto',
		flexDirection: 'column',
		display: 'flex',
		zIndex: 1000
	},
	bgWhite: {
		backgroundColor: 'white'
	},
	inputMessage: {
		color: '#555',
		width: '100%',
		border: '0px none',
		outline: 'none',
		fontSize: '55px !important',
		resize: 'none',
		height: '50px !important'
	},
	chatBottomHolder: {
		display: 'flex',
		flexDirection: 'row',
		padding: '2px 15px 5px 10px'
	},
	pRelative: {
		position: 'relative'
	},
	pAbsolute: {
		position: 'absolute',
		bottom: 35
	},
	transparentBtn: {
		backgroundColor: 'inherit',
		border: '0px none',
		outline: 'none'
	},
	mLeft: {
		marginLeft: 'auto'
	},
	chatBubble: {
		padding: '10px 13px 15px 15px',
		width: '250px',
		backgroundColor: 'white',
		borderRadius: 5,
		boxShadow: '1px 1px #d9d9d9'
	},
	dFlex: {
		display: 'flex'
	},
	bubbleMessageHolder: {
		fontSize: '14px',
		color: '#555',
		alignSelf: 'center',
		flex: 1,
		lineHeight: 1.3,
		fontWeight: 'bold'
	},
	tJustify: {
		textAlign: 'left'
	},
	bubbleDateHolder: {
		display: 'flex',
		flexDirection: 'row',
		justifyContent: 'flex-end',
		marginTop: 5
	},
	bubbleDate: {
		fontSize: '11px',
		color: '#999',
		fontWeight: 600
	}
};

export default FacebookChatView;