import React, { Component } from 'react';
import { Heading, Icon, Avatar, ResourceItem, Badge, Card, TextStyle, Layout, ResourceList } from '@shopify/polaris';
import _ from 'lodash';
import { compose } from 'redux';
import { withAlert } from 'react-alert';
import { EditMinor, DeleteMinor } from '@shopify/polaris-icons';
import AccountDetailsForm from './components/AccountDetailsForm';
import StaffForm from './components/StaffForm';
import ConnectionForm from './components/ConnectionForm';
import Loading from '../../components/loading';
import LabelHelper from '../../helpers/Label';
import ConnectionList from './components/ConnectionList';
import TwilioConnectionList from './components/TwilioConnectionList';
import TwilioConnectionForm from './components/TwilioConnectionForm';

class SettingsPage extends Component {
	constructor(props) {
		super(props);
		this.state = {
			accountDetailsForm: false,
			staffForm: false,
			connectionForm: false,
			staffFormType: null,
			twilioConnectionForm: false,
			twilioFormType: null // for identification if update or add
		};
	}

	componentDidMount() {
		this.props.getAccount();
		this.props.getFacebookConnections();
		this.props.getTwilioConnections();
	}

	componentDidUpdate(prevProps) {
		const { meta: prevMeta, loadingSubmitFacebookConnection: prev_loadingSubmitFacebookConnection, loadingTwilioData: prev_loadingTwilioData } = prevProps.settingsPageProps;
		const { meta, loadingSubmitFacebookConnection, loadingTwilioData } = this.props.settingsPageProps;

		if (meta && meta !== prevMeta) {
			if (meta.code === 200) {
				this.setState({
					accountDetailsForm: false,
					staffForm: false,
					staffFormType: null,
					twilioFormType: null
				});
			}
		}

		if (!loadingTwilioData && loadingTwilioData !== prev_loadingTwilioData) {
			this.modalHandle('twilioConnectionForm', false);
			this.props.getTwilioConnections();
		}
		if (!loadingSubmitFacebookConnection && loadingSubmitFacebookConnection !== prev_loadingSubmitFacebookConnection) {
			this.props.getFacebookConnections();
		}
	}

	componentWillUnmount() {
		this.props.resetPage();
	}

	modalHandle = async (name, value, id = '') => {
		if (name === 'accountDetails') {
			this.setState({
				accountDetailsForm: value
			});
		} else if (name === 'connectionForm') {
			this.setState({
				connectionForm: value
			});
		} else if (name === 'twilioConnectionForm') {
			this.setState({
				twilioConnectionForm: value,
				twilioFormType: id ? 'updating' : 'adding'
			});
		} else {
			if (id) {
				// fetch staff by id.
				await this.props.getStaffById({ id });
			}
			if (!value) {
				this.props.resetSelectedStaff();
			}
			this.setState({
				staffForm: value,
				staffFormType: id ? 'updating' : 'adding'
			});
		}
	};

	onDeleteStaff = ({ e, _id }) => {
		e.stopPropagation();
		if (_id) {
			if (window.confirm('Are you sure to delete this staff?')) {
				this.props.deleteStaff({ id: _id });
			}
		}
	};

	render() {
		const { loadingTwilioData, settingsPageProps, submitStaff, submitTwilioConnection, addFacebokConnection, updatePageSubcribedFieldById, getTwilioConnectionById } = this.props;
		const {
			loadingDelete,
			staffs,
			selectedStaff,
			loadingSubmitFacebookConnection,
			loadingStaffs,
			loadingFacebookConnections,
			facebook_connections,
			twilio_connections,
			loadingTwilioForm,
			loadingSubcribedFieldUpdate,
			loadingStaffModal,
			loadingSubmit,
			loadingAccount
		} = settingsPageProps;
		
		const { details, email, name } = _.get(settingsPageProps, 'account') || '';
		const { business_name, industry, mobile_number } = details || '';
		const { accountDetailsForm, staffForm, connectionForm, staffFormType, twilioConnectionForm, twilioConnectionId, twilioFormType } = this.state;
		return (
			<Layout>
				<Layout.Section>
					<hr />
					{accountDetailsForm && name && (
						<AccountDetailsForm
							modalHandle={this.modalHandle}
							isOpen={accountDetailsForm}
							data={_.get(settingsPageProps, 'account')}
							updateAccount={this.props.updateAccount}
							loadingSubmit={loadingSubmit}
						/>
					)}
					{staffForm && (
						<StaffForm
							modalHandle={this.modalHandle}
							loadingStaffModal={loadingStaffModal}
							staffFormType={staffFormType} // adding or updating
							isOpen={staffForm}
							data={selectedStaff}
							submitStaff={submitStaff}
							loadingSubmit={loadingSubmit}
						/>
					)}
					{twilioConnectionForm && (
						<TwilioConnectionForm
							modalHandle={this.modalHandle}
							twilioFormType={twilioFormType} //for verification if update or add
							loadingTwilioForm={loadingTwilioForm}
							isOpen={twilioConnectionForm} //to Open the modal twilio
							submitTwilioConnection={submitTwilioConnection}
							data={_.get(settingsPageProps, 'selectedTwilio_connection')}
						/>
					)}
					{connectionForm && (
						<ConnectionForm
							modalHandle={this.modalHandle}
							isOpen={connectionForm}
							loadingSubmit={loadingSubmit}
							addFacebokConnection={addFacebokConnection}
							twilioConnectionId={twilioConnectionId}
						/>
					)}
					<div style={styles.container}>
						<Heading>My account</Heading>
						<Card
							title="Account Details"
							sectioned
							primaryFooterAction={{
								content: 'Edit Information',
								onClick: () => this.modalHandle('accountDetails', true),
								disabled: loadingAccount || loadingSubmit
							}}
						>
							<table style={{ width: 560 }}>
								<tbody>
									<tr>
										<td>
											<TextStyle variation="subdued">Company Name: </TextStyle>
										</td>
										<td>
											<TextStyle variation="strong">{business_name}</TextStyle>
										</td>
									</tr>
									<tr>
										<td>
											<TextStyle variation="subdued">Business owner's Name: </TextStyle>
										</td>
										<td>
											<TextStyle variation="strong">{name}</TextStyle>
										</td>
									</tr>
									<tr>
										<td>
											<TextStyle variation="subdued">Mobile Number: </TextStyle>
										</td>
										<td>
											<TextStyle variation="strong">{mobile_number}</TextStyle>
										</td>
									</tr>
									<tr>
										<td>
											<TextStyle variation="subdued">Email Address: </TextStyle>
										</td>
										<td>
											<TextStyle variation="strong">{email}</TextStyle>
										</td>
									</tr>
									<tr>
										<td>
											<TextStyle variation="subdued">Password: </TextStyle>
										</td>
										<td>
											<TextStyle variation="strong">**************</TextStyle>
										</td>
									</tr>
								</tbody>
							</table>
						</Card>
					</div>
				</Layout.Section>
				<Layout.Section>
					<hr />
					<div style={styles.container}>
						<Heading>Connections</Heading>
						<div style={{ display: 'flex', flexDirection: 'column' }}>
							<Card
								primaryFooterAction={{
									content: 'Add Connection',
									onClick: () => this.modalHandle('connectionForm', true),
									disabled: loadingFacebookConnections || loadingSubmitFacebookConnection
								}}
							>
								<ConnectionList
									facebook_connections={facebook_connections}
									loadingSubcribedFieldUpdate={loadingSubcribedFieldUpdate}
									loadingFacebookConnections={loadingFacebookConnections}
									loadingSubmitFacebookConnection={loadingSubmitFacebookConnection}
									updatePageSubcribedFieldById={updatePageSubcribedFieldById}
								/>
							</Card>
							<Card
								primaryFooterAction={{
									content: 'Add Connection',
									onClick: () => this.modalHandle('twilioConnectionForm', true)
								}}
							>
								<TwilioConnectionList
									twilioFormType={twilioFormType}
									modalHandle={this.modalHandle}
									twilio_connections={twilio_connections}
									getTwilioConnectionById={getTwilioConnectionById}
									loadingTwilioData={loadingTwilioData}
								/>
							</Card>
						</div>
					</div>
				</Layout.Section>
				<Layout.Section>
					<hr />
					<div style={styles.container}>
						<Heading>List of Staffs</Heading>
						<Card
							primaryFooterAction={{
								content: 'Add Staff Account',
								onClick: () => this.modalHandle('staffForm', true),
								disabled: loadingStaffs || loadingSubmit
							}}
						>
							<div style={{ width: 600 }}>
								{loadingStaffs ? (
									<Loading />
								) : staffs.length <= 0 ? (
									<div style={{ textAlign: 'center', marginTop: 10 }}>
										<TextStyle variation="strong">No staff found.</TextStyle>
									</div>
								) : (
									<ResourceList
										loading={false}
										resourceName={{ singular: 'staff', plural: 'staffs' }}
										items={staffs}
										renderItem={item => {
											const { _id, name, status } = item;
											const media = <Avatar
												size="small"
												name={name}
												initials={name.charAt(0)}
											              />;
											return (
												<div>
													<ResourceItem
														id={_id}
														// url={url}
														media={media}
														accessibilityLabel={`View details for ${name}`}
													>
														<div
															onClick={() => !loadingDelete && !loadingStaffModal && !loadingSubmit && this.modalHandle('staffForm', true, _id)}
															style={styles.staffListContainer}
														>
															<div>
																<h1>{name}</h1>
																<TextStyle variation="strong">{name}</TextStyle>
																<div style={{ display: 'flex', justifyContent: 'space-between' }}>
																	<TextStyle variation="subdued">
																		<Badge status={!status || status === 'offline' ? 'critical' : 'success'}>{LabelHelper.staffStatus(item.status || 'offline')}</Badge>
																	</TextStyle>
																	{/* <TextStyle variation="subdued">Last login  was (TBA)</TextStyle> */}
																</div>
															</div>
															<div style={{ display: 'flex' }}>
																<span style={{ marginRight: 10 }}>
																	<Icon
																		source={EditMinor}
																		color="primary"
																	/>
																</span>
																<span onClick={e => !loadingDelete && !loadingStaffModal && !loadingSubmit && this.onDeleteStaff({ e, _id })}>
																	<Icon
																		source={DeleteMinor}
																		color="red"
																	/>
																</span>
															</div>
														</div>
													</ResourceItem>
												</div>
											);
										}}
									/>
								)}
							</div>
						</Card>
					</div>
				</Layout.Section>
			</Layout>
		);
	}
}

const styles = {
	container: {
		display: 'flex',
		justifyContent: 'space-between'
	},
	staffListContainer: {
		display: 'flex',
		justifyContent: 'space-between',
		alignItems: 'center'
	}
};

export default compose(withAlert())(SettingsPage);
