import request from './request';

export default {
	async login(email, password) {
		const response = await request.post('/app/auth/login', {
			email,
			password
		});

		return response;
	},
	async signUp(body) {
		const response = await request.post('/app/auth/sign-up', body);

		return response;
	},
	async logout() {
		const res = await request.post('/app/auth/logout');
		return res;
	}
};
