import React, { Component } from 'react';
import _ from 'lodash';
import { compose } from 'redux';
import { withAlert } from 'react-alert';
import { Avatar, Card, Space } from 'antd';
class Staff extends Component {
	constructor(props) {
		super(props);
		this.state = {
			
		};
	}
	render(){
  
		const {name, _id, status, img} = this.props.staff;
		return(
			<div style={styles.parentContainer}>
				<div style={styles.childContainer}>
					<Space>
						<Avatar
							src={img}
							size={64}
						/>
						<div>
							<h1 style={{margin: 0}}>{name}</h1>
							<h2>Staff ID: {_id}</h2>
						</div>
					</Space>
				</div>
				<div>
					{status}
				</div>
			</div>
		);
	}
}

const styles = {
	parentContainer: {
		marginBottom: '1em', 
		width: '100%', 
		display: 'flex', 
		alignItems: 'center', 
		justifyContent: 'space-between'
	},
	childContainer: {
		display: 'flex', 
		alignItems: 'center'
	}
};

export default compose(withAlert())(Staff);
