import React, { Component } from 'react';
import PropTypes from 'prop-types';
import { Button, Form, Caption, Card, FormLayout,
	TextField, Modal, TextContainer, Icon } from '@shopify/polaris';
import { SendMajorMonotone } from '@shopify/polaris-icons';
import { compose } from 'redux';
import { Field, reduxForm } from 'redux-form';
import { withAlert } from 'react-alert';
import { renderFormInput, validateRequired, validateEmail } from '../../../helpers/redux_form';

class AccountDetailsForm extends Component {
	componentDidMount(){
		this.props.initialize({ ...this.props.data });
	}

	render() {
		const { pristine, submitting, invalid, isOpen, modalHandle, updateAccount, loadingSubmit } = this.props;
		return (
			<Card>
				<Modal
					open={isOpen}
					onClose={() => modalHandle('accountDetails', false)}
					title="Account Information"
					primaryAction={{
						content: 'Save',
						disabled: pristine || submitting || invalid || loadingSubmit,
						onAction: this.props.handleSubmit((data) => updateAccount(data))
					}}
					size="Full"
				>
					<Modal.Section>
						<Form
							onSubmit={this.props.handleSubmit((data) => updateAccount(data))}
						>
							<FormLayout>
								<Field
									name="details.business_name"
									label="Company Name"
									component={renderFormInput}
									validate={[validateRequired]}
								/>
								<Field
									name="name"
									label="Business owner's name"
									component={renderFormInput}
									validate={[validateRequired]}
								/>
								<Field
									name="details.mobile_number"
									label="Mobile Number"
									component={renderFormInput}
									validate={[validateRequired]}
								/>
								<Field
									name="email"
									label="Email Address (Username)"
									component={renderFormInput}
									validate={[validateRequired, validateEmail]}
								/>
								<Field
									name="old_password"
									label="New Password"
									component={renderFormInput}
									type="password"
								/>
								<Field
									name="password"
									label="Confirm Password"
									component={renderFormInput}
									type="password"
								/>
							</FormLayout>
						</Form>

					</Modal.Section>
				</Modal>
			</Card>
		);
	}
}

export default compose(
	withAlert(),
	reduxForm({
		form: 'AccountDetailsForm'
	})
)(AccountDetailsForm);