import auth from './auth';
import assets from './assets';
import accounts from './admin/accounts';
import connections from './admin/connections';
import twilioconnections from './admin/twilioconnections';

import adminTickets from './admin/tickets';
import adminMessages from './admin/messages';
import adminUsers from './admin/users';
import adminContacts from './admin/contacts';

import staffTickets from './staff/tickets';
import staffMessages from './staff/messages';
import staffUsers from './staff/users';

import staffContacts from './staff/contacts';

export default {
	admin: {
		accounts,
		twilioconnections,
		connections,
		tickets: adminTickets,
		messages: adminMessages,
		users: adminUsers,
		contacts: adminContacts
	},
	staff: {
		tickets: staffTickets,
		messages: staffMessages,
		users: staffUsers,
		contacts: staffContacts
	},
	assets,
	auth
};
