import request from '../request';

export default {
	async getUsers(type) {
		const res = await request.get(`/app/users`, type && { type });
		return res;
	},
	async getStaffs() {
		const res = await request.get('/app/staffs');
		return res;
	},
	async getStaffById(id) {
		const res = await request.get(`/app/staffs/id?id=${id}`);
		return res;
	},
	async createStaff(body) {
		const res = await request.post(`/app/staffs`, body);
		return res;
	},
	async updateStaffById(id, body) {
		const res = await request.patch(`/app/staffs/id?id=${id}`, body);
		return res;
	},
	async deleteStaffById(id) {
		const res = await request.delete(`/app/staffs/id?id=${id}`);
		return res;
	}
};
