import React, { Component } from 'react';
import _ from 'lodash';
import { compose } from 'redux';
import { withAlert } from 'react-alert';
import { Avatar, Button, Card, Col, DatePicker, Divider, Form, Image, Input, Row, Select, Space, Typography } from 'antd';
import { Field, reduxForm } from 'redux-form';
import Icon from '@ant-design/icons';
import { Link } from 'react-router-dom';
import GeneralHelper from '../../../helpers/general';
import Customer from './Customer';
import { renderFormInput } from '../../../helpers/redux_form';
const { Title } = Typography;
const { Option } = Select;
class TicketInfo extends Component {
	constructor(props) {
		super(props);
		this.state = {
			selected: 'NON',
			currentStatus: 'In Progress',
			currentPriority: 'High'
		};
	}
	render(){
		
		const { selected, currentStatus, currentPriority } = this.state;

		return(
			<Card
				title="Ticket Information"
				style={{width: '100%', padding: 0, marginBottom: '1em'}}
			>
				<div>
					<Form
						layout="vertical"
					>
						<Form.Item label="Ticket Number">
							<Input
								disabled
								value={"001"}
							/>
						</Form.Item>
						<Form.Item label="Status">
							<Select defaultValue={currentStatus}>
								<Option value="Pending">Pending</Option>
								<Option value="In Progress">In Progress</Option>
								<Option value="Resolved">Resolved</Option>
							</Select>
						</Form.Item>
						<Form.Item label="Status">
							<Select defaultValue={currentPriority}>
								<Option value="Low">Low</Option>
								<Option value="Medium">Medium</Option>
								<Option value="High">High</Option>
							</Select>
						</Form.Item>
					</Form>
					<Row justify="space-between">
						<Button
							style={{width: '10em'}}
						>
                Transfer Ticket
						</Button>
						<Button
							style={{width: '10em', background: '#09509d', borderColor: '#09509d'}}
							type="primary"
						>
                Save
						</Button>
					</Row>
				</div>
			</Card>
		);
	}
}

const styles = {
	filterBox: {
		width: '12em'
	}
};

export default compose(withAlert(), 
	reduxForm({
		form: 'ForgotPasswordPage'
	})
)(TicketInfo);
