import * as ActionTypes from '../constants/ActionTypes';

const initialState = {
	loading: false,
	staff: {}
};

export default function reducer(state = initialState, action) {
	switch(action.type) {
		case ActionTypes.GET_STAFF_BY_ID_REQUEST:
		case ActionTypes.GET_STAFF_BY_ID_SUCCESS:
		case ActionTypes.GET_STAFF_BY_ID_ERROR:
		case ActionTypes.SUBMIT_STAFF_REQUEST:
		case ActionTypes.SUBMIT_STAFF_SUCCESS:
		case ActionTypes.SUBMIT_STAFF_ERROR:
			return {
				...state,
				...action.payload
			};
		case ActionTypes.RESET_STAFF_DETAILS_PAGE_META:
			return {
				...state,
				meta: null
			};
		case ActionTypes.RESET_STAFF_DETAILS_PAGE:
			return initialState;
		default:
			return state;
	}
};
