import React, { Component } from 'react';
import _, { filter } from 'lodash';
import { compose } from 'redux';
import { withAlert } from 'react-alert';
import { Button, Card, Col, DatePicker, Divider, Dropdown, Form, Input, Menu, message, Row, Select, Space, Table, Tag } from 'antd';
import { DownOutlined, PlusOutlined, SearchOutlined, UserOutlined } from '@ant-design/icons';
import tickets from '../../../helpers/api/admin/tickets';
const { Option } = Select;
class TicketList extends Component {
	constructor(props) {
		super(props);
		this.state = {
			filters: {
				status: '',
				priority: '',
				channel: '',
				date: ''
			}
		};
	}
	
	render(){
		const { tickets: data } = this.props;
		const { filters } = this.state;

		const columns = [
			{
				title: 'Ticket ID',
				dataIndex: 'key',
				key: 'key',
				render: (text) => <a>{text}</a>
			},
			{
				title: 'Customer',
				dataIndex: 'customer',
				key: 'key'
			},
			{
				title: 'Channel',
				dataIndex: 'channel',
				key: 'key'
			},
			{
				title: 'Date',
				dataIndex: 'date',
				key: 'key'
			},
			{
				title: 'Priority',
				dataIndex: 'priority',
				key: 'key'
			},
			{
				title: 'Staff',
				dataIndex: 'name',
				key: 'key'
			},
			{
				title: 'Status',
				dataIndex: 'status',
				key: 'key'
			}
		];

		
		//Search Result Filters
		const searchResult = () => {
			return data.filter((element)=> {
				//If filter state is empty return true.
				const statusFilter = ("" === filters.status) ? true : element.status === filters.status;
				const priorityFilter = ("" === filters.priority) ? true : element.priority === filters.priority;
				const channelFilter = ("" === filters.channel) ? true : element.channel === filters.channel;
				const dateFilter = ('' === filters.date) ? true : element.date === filters.date;
				//return data that satisfies filtering
				return (statusFilter && priorityFilter && channelFilter && dateFilter);
			});
		};

		const modalHandle = (type, e) => {
			//Check if 'e' has value and return the value else return empty string.
			const value = (e) ? e: ''; 
			
			const newFilter = filters;
			switch(type){
				case 'status':
					newFilter.status = value;
					break;
				case 'priority':
					newFilter.priority = value;
					break;
				case 'channel':
					newFilter.channel = value;
					break;
				case 'date':
					newFilter.date = e.dateString;
					break;
				default:
			}
			this.setState({filters: newFilter});	
		};

		return(
			<Card
				title="List"
			>
				<Space
					direction="vertical"
					size="middle"
					style={{ display: 'flex' }}
				>
					<Row justify="end">
						<Space>
							<Select
								style={styles.searchBox}
								allowClear
								placeholder="Search by Status"
								onChange={(value) => modalHandle('status', value)}
							>
								<Option value="Pending">Pending</Option>
								<Option value="In Progress">In Progress</Option>
								<Option value="Resolved">Resolved</Option>
							</Select>

							<Select
								style={styles.searchBox}
								allowClear
								placeholder="Search by Priority"
								onChange={(value) => modalHandle('priority', value)}
							>
								<Option value="Low">Low</Option>
								<Option value="Medium">Medium</Option>
								<Option value="High">High</Option>
							</Select>

							<Select
								style={styles.searchBox}
								allowClear
								placeholder="Search by Channel"
								onChange={(value) => modalHandle('channel', value)}
							>
								<Option value="Messenger">Messenger</Option>
								<Option value="Instagram">Instagram</Option>
								<Option value="Email">Email</Option>
								<Option value="WhatsApp">WhatsApp</Option>
							</Select>
							<DatePicker
								placeholder="Search by Date"
								format={"MM-DD-YYYY"}
								onChange={(date, dateString) => {const e = {date, dateString}; modalHandle("date", e);}}
							/>

						</Space>
					</Row>
					<Table
						dataSource={searchResult()}
						columns={columns}
					/>
				</Space>
			</Card>
		);
	}
}

const styles = {
	searchBox: {
		width: '12em'
	}
};

export default compose(withAlert())(TicketList);
