import * as ActionTypes from '../../constants/ActionTypes';
import API from '../../helpers/api';

export function getStaffs() {
	return async dispatch => {
		dispatch({
			type: ActionTypes.GET_STAFFS_REQUEST,
			payload: {
				loadingStaffs: true
			}
		});

		const response = await API.admin.users.getStaffs();
		if (response.meta.code !== 200) {
			return dispatch({
				type: ActionTypes.GET_STAFFS_ERROR,
				payload: {
					loadingStaffs: false
				}
			});
		}
		// success
		const { staffs } = response.data;
		dispatch({
			type: ActionTypes.GET_STAFFS_SUCCESS,
			payload: {
				loadingStaffs: false,
				staffs: staffs
			}
		});
	};
}

export function getAccount() {
	return async dispatch => {
		dispatch({
			type: ActionTypes.GET_SETTINGS_ACCOUNT_REQUEST,
			payload: {
				loadingAccount: true
			}
		});

		const response = await API.admin.accounts.getAccount();
		if (response.meta.code !== 200) {
			return dispatch({
				type: ActionTypes.GET_SETTINGS_ACCOUNT_ERROR,
				payload: {
					meta: response.meta,
					loadingAccount: false
				}
			});
		}
		// success
		const { user } = response.data;
		dispatch({
			type: ActionTypes.GET_SETTINGS_ACCOUNT_SUCCESS,
			payload: {
				loadingAccount: false,
				account: user
			}
		});
		// get staffs
		dispatch(getStaffs());
	};
}

export function reset() {
	return async dispatch => {
		dispatch({
			type: ActionTypes.RESET_ACCOUNT_PAGE
		});
	};
}

export function resetMeta() {
	return async dispatch => {
		dispatch({
			type: ActionTypes.RESET_ACCOUNT_PAGE_META
		});
	};
}
