import { push } from 'connected-react-router';
import * as ActionTypes from '../constants/ActionTypes';
import API from '../helpers/api';

export function getStaffs() {
	return async dispatch => {
		dispatch({
			type: ActionTypes.GET_STAFFS_REQUEST,
			payload: {
				loadingStaffs: true
			}
		});

		const response = await API.admin.users.getStaffs();

		if (response.meta.code !== 200) {
			return dispatch({
				type: ActionTypes.GET_STAFFS_ERROR,
				payload: {
					loadingStaffs: false
				}
			});
		}

		const { staffs } = response.data;
		dispatch({
			type: ActionTypes.GET_STAFFS_SUCCESS,
			payload: {
				loadingStaffs: false,
				staffs
			}
		});
	};
}

export function navigateToDetailsPage(id) {
	return async dispatch => {
		dispatch(push(`/staffs/id/basic?id=${id}`));
	};
}

export function navigateToAddPage() {
	return async dispatch => {
		dispatch(push('/staffs/create'));
	};
}

