import React, { Component } from 'react';
import {  Layout, Menu, Space } from 'antd';
import { Link } from 'react-router-dom';
import URLParse from 'url-parse';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faHandshake, faFlag, faTachometerAlt, faCircleNotch, faBullhorn, faGear, faCalendarAlt, faClipboardList, faUserFriends, faToolbox, faChalkboardTeacher, faFolderOpen, faCog, faLaptop, faListOl, faDollarSign, faUsers } from '@fortawesome/free-solid-svg-icons';
import { CalendarOutlined } from '@ant-design/icons';

const { SubMenu } = Menu;
const { Sider } = Layout;

const rootSubmenuKeys = ['sub-bookings', 'sub-projects', 'sub-job-orders', 'sub-users', 'sub-serv-groups-and-equipment', 'sub-advanced', 'sub-consums-assories', 'sub-consum-orders', 'sub-billings'];

export class Drawer extends Component {
	state = {
		openKeys: []
	};

	componentDidMount() {
		this.getActiveSubmenu();
	}

	getActiveItem({ pathname, search }) {
		const { query } = URLParse(search, true);
		switch (true) {
			case /\/dashboard/.test(pathname):
				return ['dashboard'];
			case /\/staffs/.test(pathname):
				return ['staffs'];
			case /\/tickets/.test(pathname):
				return ['tickets'];
			case /\/settings/.test(pathname):
				return ['settings'];
			case /\/reports/.test(pathname):
				return ['reports'];
			case /\/subscription/.test(pathname):
				return ['subscription'];
			case /\/messenger-integration/.test(pathname):
				return ['messenger-integration'];
			case /\/messenger-settings/.test(pathname):
				return ['messenger-settings'];
			case /\/jo-sams/.test(pathname):
				return ['jo-sams'];
			case /\/jo-workflows/.test(pathname):
				return ['jo-workflows'];
			case /\/users/.test(pathname):
				return ['users'];
			case /\/pis/.test(pathname):
				return ['pis'];
			case /\/user-application-settings/.test(pathname):
				return ['user-application-settings'];
			case /\/user-application/.test(pathname):
				return ['user-application'];
			case /\/external-user-application-settings/.test(pathname):
				return ['external-user-application-settings'];
			case /\/external-user-application/.test(pathname):
				return ['external-user-application'];
			case /\/servgp/.test(pathname):
				return ['servgp'];
			case /\/equips/.test(pathname):
				return ['equips'];
			case /\/equip-workflows/.test(pathname):
				return ['equips'];
			case /\/equip-penalties/.test(pathname):
				return ['equips'];
			case /\/equip-bks/.test(pathname):
				return ['equips'];
			case /\/equip-charging-schemes/.test(pathname):
				return ['equips'];
			case /\/assories/.test(pathname):
				return ['assories'];
			case /\/consums/.test(pathname):
				return ['consums'];
			case /\/admins/.test(pathname):
				return ['admins'];
			case /\/charge-types/.test(pathname):
				return ['charge-types'];
			case /\/user-types/.test(pathname):
				return ['user-types'];
			case /\/locations/.test(pathname):
				return ['locations'];
			case /\/assorycats/.test(pathname):
				return ['assorycats'];
			case /\/consumcats/.test(pathname):
				return ['consumcats'];
			case /\/consum-inventory/.test(pathname):
				return ['consum-inventory'];
			case /\/consum-batch-maintain/.test(pathname):
				return ['consum-batch-maintain'];
			// case /\/admins/.test(pathname):
			// 	return ['equips'];
			case /\/skills/.test(pathname):
				return ['skills'];
			case /\/trainings/.test(pathname):
				return ['trainings'];
			case /\/users/.test(pathname):
				return ['users'];
			default:
				return;
		}
	}

	getActiveSubmenu() {
		const pathname = this.props.location.pathname;
		switch (true) {
			case/\/subscriptions/.test(pathname):
				this.setState({
					openKeys: ['subscriptions']
				});
				break;
			case/\/settings/.test(pathname):
				this.setState({
					openKeys: ['settings']
				});
				break;
			case/\/subscription/.test(pathname) || /\/project-types/.test(pathname):
				this.setState({
					openKeys: ['subscription']
				});
				break;					
			case /\/jo-forms/.test(pathname) || /\/jo-types/.test(pathname) || /\/jo-sams/.test(pathname) || /\/jo-workflows/.test(pathname):
				this.setState({
					openKeys: ['sub-job-orders']
				});
				break;
			case /\/admins/.test(pathname) || /\/charge-types/.test(pathname) || /\/user-types/.test(pathname) || /\/locations/.test(pathname):
				this.setState({
					openKeys: ['sub-advanced']
				});
				break;
			case /\/users/.test(pathname) || /\/pis/.test(pathname) || /\/user-application/.test(pathname) || /\/user-application-settings/.test(pathname) || /\/external-user-application/.test(pathname) || /\/external-user-application-settings/.test(pathname):
				this.setState({
					openKeys: ['sub-users']
				});
				break;
			case /\/equips/.test(pathname) || /\/servgp/.test(pathname):
				this.setState({
					openKeys: ['sub-serv-groups-and-equipment']
				});
				break;
			case /\/assories/.test(pathname) || /\/consums/.test(pathname) || /\/consumcats/.test(pathname) || /\/assorycats/.test(pathname) || /\/consum-inventory/.test(pathname) || /\/consum-batch-maintain/.test(pathname):
				this.setState({
					openKeys: ['sub-consums-assories']
				});
				break;
			case /\/skills/.test(pathname) || /\/trainings/.test(pathname):
				this.setState({
					openKeys: ['sub-skills-and-trainings']
				});
				break;
			default:
				break;
		}
	}

	onOpenChange = keys => {
		const latestOpenKey = keys.find(key => this.state.openKeys.indexOf(key) === -1);
		if (rootSubmenuKeys.indexOf(latestOpenKey) === -1) {
			this.setState({
				openKeys: keys
			});
		} else {
			this.setState({
				openKeys: latestOpenKey ? [latestOpenKey] : []
			});
		}
	};

	render() {
		const activeItem = this.getActiveItem({
			pathname: this.props.location.pathname,
			search: this.props.location.search
		});

		return (
			<Sider
				trigger={null}
				breakpoint="lg"
				collapsedWidth="0"
				onCollapse={this.props.handleOnCollapse}
				collapsed={this.props.collapsed}
				width="256"
				style={{
					zIndex: 30
				}}
			>
				<div className="menu-logo" />
				<Menu
					theme="dark"
					mode="inline"
					openKeys={this.state.openKeys}
					onOpenChange={this.onOpenChange}
					selectedKeys={activeItem}
				>
					<div  style={{textAlign: 'center', margin: '1em', padding: '1em'}}>
						<span style={{fontSize: '20px'}}><Link
							to="/dashboard"
							style={{color: "white"}}
						                                 >Anndesk</Link></span>
					</div>
					<Menu.Item key="dashboard">
						<FontAwesomeIcon
							icon={faTachometerAlt}
							fixedWidth
						/>
						<span style={{ marginLeft: 10 }}>Dashboard</span>
						<Link to="/dashboard" />
					</Menu.Item>

					<Menu.Item key="tickets">
						<FontAwesomeIcon
							icon={faClipboardList}
							fixedWidth
						/>
						<span style={{ marginLeft: 10 }}>Tickets</span>
						<Link to="/tickets" />
					</Menu.Item>

					<Menu.Item key="staffs">
						<FontAwesomeIcon
							icon={faUserFriends}
							fixedWidth
						/>
						<span style={{ marginLeft: 10 }}>Staff</span>
						<Link to="/staffs" />
					</Menu.Item>
					
					<SubMenu
						key="sub-bookings"
						icon={<FontAwesomeIcon
							icon={faCircleNotch}
							fixedWidth
						      />}
						title="Channel"
					>
						<Menu.Item key="messenger-integration">
							<span className="nav-text">Messenger Integration</span>
							<Link to="/messenger-integration" />
						</Menu.Item>
						<Menu.Item key="messenger-settings">
							<span className="nav-text">Messenger Settings</span>
							<Link to="/messenger-settings" />
						</Menu.Item>
					</SubMenu>

					<Menu.Item key="reports">
						<FontAwesomeIcon
							icon={faFlag}
							fixedWidth
						/>
						<span style={{ marginLeft: 10 }}>Reports</span>
						<Link to="/reports" />
					</Menu.Item>

					<SubMenu
						key="settings"
						icon={<FontAwesomeIcon
							icon={faCog}
							fixedWidth
						      />}
						title="Settings"
					>
						<Menu.Item key="settings">
							<span className="nav-text">My Account</span>
							<Link to="/settings" />
						</Menu.Item>
						<Menu.Item key="subscription">
							<span className="nav-text">Subscription</span>
							<Link to="/subscription" />
						</Menu.Item>
					</SubMenu>

				</Menu>

			</Sider>
		);
	}
}

export default Drawer;
