import { push } from 'connected-react-router';
import * as ActionTypes from '../constants/ActionTypes'; 
import API from '../helpers/api';

export function getPages() {
	return async dispatch => {
		dispatch({
			type: ActionTypes.GET_PAGES_REQUEST,
			payload: {
				loadingPages: true
			}
		});

		// const response = await API.admin.users.getPages();

		const response = {
			data: {
				pages: [
					{
						_id: '001', 
						pageName: 'Page A',
						channel: 'Instagram',
						action: 'Connect'
					},
					{
						_id: '002', 
						pageName: 'Page B',
						channel: 'Messenger',
						action: 'Connect'
					},
					{
						_id: '003', 
						pageName: 'Page C',
						channel: 'WhatsApp',
						action: 'Connect'
					},
					{
						_id: '004', 
						pageName: 'Page D',
						channel: 'Email',
						action: 'Disconnect'
					}
				]
			},
			meta: {
				code: 200
			}
		};

		if (response.meta.code !== 200) {
			return dispatch({
				type: ActionTypes.GET_PAGES_ERROR,
				payload: {
					loadingPages: false
				}
			});
		}

		const { pages } = response.data;
		dispatch({
			type: ActionTypes.GET_PAGES_SUCCESS,
			payload: {
				loadingPages: false,
				pages
			}
		});

	};
}

export function resetPage() {
	return async dispatch => {
		return dispatch({
			type: ActionTypes.RESET_MESSENGER_INTEGRATION_PAGE
		});
	};
};

export function resetMeta() {
	return async dispatch => {
		return dispatch({
			type: ActionTypes.RESET_MESSENGER_INTEGRATION_PAGE_META
		});
	};
};
