import { connect } from 'react-redux';
import StaffListPage from './StaffListPage';
import { deleteStaff, submitStaff, getStaffById, getAccount, resetSelectedStaff, reset, resetMeta } from '../../actions/SettingsPage/SettingsPage';
import { getStaffs, navigateToAddPage, navigateToDetailsPage } from '../../actions/StaffListPage';

const mapsStateToProps = state => ({
	settingsPageProps: state.settingsPageReducer
});

const mapsDispatchToProps = dispatch => ({
	getAccount() {
		dispatch(getAccount());
	},

	getStaffById(options) {
		dispatch(getStaffById(options));
	},
	
	submitStaff(options) {
		dispatch(submitStaff(options));
	},

	deleteStaff(options) {
		dispatch(deleteStaff(options));
	},

	resetSelectedStaff() {
		dispatch(resetSelectedStaff());
	},
	getStaffs() {
		dispatch(getStaffs());
	},
	navigateToDetailsPage(id) {
		dispatch(navigateToDetailsPage(id))
	},
	navigateToAddPage() {
		dispatch(navigateToAddPage());
	},
	resetPage() {
		dispatch(reset());
	},
	resetMeta() {
		dispatch(resetMeta());
	}
});

export default connect(mapsStateToProps, mapsDispatchToProps)(StaffListPage);
