import * as ActionTypes from '../constants/ActionTypes';

const initialState = {
	loading: false,
	account: null,
	loadingSubmit: false,
	staffs: [],
	connections: [],
	facebook_connections: [],
	selectedStaff: null,

	twilio_connections: [], //Gamit

	selectedTwilio_connection: null, // for selected by id

	loadingTwilioForm: false,

	loadingTwilioData: false,

	loadingAccount: false,
	loadingStaffs: false,
	loadingStaffModal: false,
	loadingDeleteStaff: false,
	loadingConnections: false,
	loadingFacebookConnections: false,
	loadingTwilioConnections: false
};

export default function reducer(state = initialState, action) {
	switch (action.type) {
		case ActionTypes.GET_SETTINGS_ACCOUNT_REQUEST:
		case ActionTypes.GET_SETTINGS_ACCOUNT_SUCCESS:
		case ActionTypes.GET_SETTINGS_ACCOUNT_ERROR:
		case ActionTypes.UPDATE_SETTINGS_ACCOUNT_REQUEST:
		case ActionTypes.UPDATE_SETTINGS_ACCOUNT_SUCCESS:
		case ActionTypes.UPDATE_SETTINGS_ACCOUNT_ERROR:
		case ActionTypes.UPDATE_STAFF_SUCCESS:
		case ActionTypes.UPDATE_STAFF_REQUEST:
		case ActionTypes.UPDATE_STAFF_ERROR:
		case ActionTypes.GET_STAFFS_REQUEST:
		case ActionTypes.GET_STAFFS_ERROR:
		case ActionTypes.GET_STAFFS_SUCCESS:
		case ActionTypes.GET_STAFF_BY_ID_REQUEST:
		case ActionTypes.GET_STAFF_BY_ID_ERROR:
		case ActionTypes.GET_STAFF_BY_ID_SUCCESS:
		case ActionTypes.DELETE_STAFF_BY_ID_SUCCESS:
		case ActionTypes.DELETE_STAFF_BY_ID_REQUEST:
		case ActionTypes.DELETE_STAFF_BY_ID_ERROR:
		case ActionTypes.UPDATE_ACCOUNT_STATUS_ERROR:
		case ActionTypes.UPDATE_ACCOUNT_STATUS_REQUEST:
		case ActionTypes.UPDATE_ACCOUNT_STATUS_SUCCESS:
		case ActionTypes.GET_CONNECTIONS_REQUEST:
		case ActionTypes.GET_CONNECTIONS_ERROR:
		case ActionTypes.GET_CONNECTIONS_SUCCESS:
		case ActionTypes.ADD_FACEBOOK_CONNECTION_ERROR:
		case ActionTypes.ADD_FACEBOOK_CONNECTION_REQUEST:
		case ActionTypes.ADD_FACEBOOK_CONNECTION_SUCCESS:
		case ActionTypes.GET_FACEBOK_CONNECTIONS_REQUEST:
		case ActionTypes.GET_FACEBOK_CONNECTIONS_ERROR:
		case ActionTypes.GET_FACEBOK_CONNECTIONS_SUCCESS:
		case ActionTypes.UPDATE_PAGE_SUBCRIBED_FIELD_REQUEST:
		case ActionTypes.UPDATE_PAGE_SUBCRIBED_FIELD_SUCCESS:
		case ActionTypes.UPDATE_PAGE_SUBCRIBED_FIELD_ERROR:
		case ActionTypes.ADD_TWILIO_CONNECTION_REQUEST:
		case ActionTypes.ADD_TWILIO_CONNECTION_ERROR:
		case ActionTypes.ADD_TWILIO_CONNECTION_SUCCESS:
		case ActionTypes.GET_TWILIO_CONNECTIONS_REQUEST:
		case ActionTypes.GET_TWILIO_CONNECTIONS_ERROR:
		case ActionTypes.GET_TWILIO_CONNECTIONS_SUCCESS:
		case ActionTypes.UPDATE_TWILIO_CONNECTION_REQUEST:
		case ActionTypes.UPDATE_TWILIO_CONNECTION_ERROR:
		case ActionTypes.UPDATE_TWILIO_CONNECTION_SUCCESS:
		case ActionTypes.GET_TWILIO_CONNECTION_BY_ID_REQUEST:
		case ActionTypes.GET_TWILIO_CONNECTION_BY_ID_ERROR:
		case ActionTypes.GET_TWILIO_CONNECTION_BY_ID_SUCCESS:
			return {
				...state,
				...action.payload
			};
		case ActionTypes.RESET_ACCOUNT_PAGE_META:
			return {
				...state,
				meta: null
			};
		case ActionTypes.RESET_ACCOUNT_PAGE:
			return {
				...initialState
			};
		case ActionTypes.RESET_SELECTED_STAFF:
			return {
				...state,
				selectedStaff: null
			};
		default:
			return state;
	}
}
