import React, { Component } from 'react';
import moment from 'moment';
import UrlParse from 'url-parse';
import _ from 'lodash';
import { Autocomplete, ResourceItem, ResourceList, Card, Badge, Icon, TextStyle, Avatar } from '@shopify/polaris';
import { Col, Row, DatePicker, Input, Select, Space, Tabs } from 'antd';
import { SearchMinor } from '@shopify/polaris-icons';
import ioClient from 'socket.io-client';
import config from './../../../config';
import LocalStorageHelper from '../../../helpers/local_storage';
import FacebookChatView from './FacebookChatView';
import CustomerList from './CustomerList';
import ChatBox from './ChatBox';
import CustomerInformation from './CustomerInformation';
import TicketInfo from './TicketInfo';
import TicketInformation from './TicketInformation';
import { MessengerChat } from "react-messenger-chat-plugin";
const { TabPane } = Tabs;

const testOptions = [
	{ value: 'rustic', label: 'Rustic' },
	{ value: 'antique', label: 'Antique' },
	{ value: 'vinyl', label: 'Vinyl' },
	{ value: 'vintage', label: 'Vintage' },
	{ value: 'refurbished', label: 'Refurbished' }
];

class TicketList extends Component {
	constructor(props) {
		super(props);
		this.state = {
			newTicket: false,
			selectedTabIndex: 0,
			options: [],
			loading: false,
			searchInputValue: '',
			selected: '',
			ticketTabs: [
				{
					id: 'my',
					content: 'My Tickets',
					panelID: 'my-tickets'
				},
				{
					id: 'unassigned',
					content: 'Unassigned',
					panelID: 'unassigned-tickets'
				},
				{
					id: 'resolved',
					content: 'Resolved',
					panelID: 'resolved-tickets'
				},
				{
					id: 'all',
					content: 'All',
					panelID: 'all-tickets'
				}
			]
		};
		this.socket = ioClient(config.socket.host, {
			path: config.socket.path,
			forceNew: true
		});
	}

	componentDidMount() {
		const { query } = UrlParse(this.props.location.search, true);
		const { history, newTicket } = this.props;
		const listOfTabs = this.state.ticketTabs.map(a => a.id);
		const { type, ticket_id } = query;
		const { socket } = this;
		if (!listOfTabs.includes(query.type)) {
			if (ticket_id) {
				history.push(`/tickets?type=all&ticket_id=${ticket_id}`);
			} else {
				history.push('/tickets?type=all');
			}
			this.getTickets({ type: 'all' });
		} else {
			this.setState({
				selectedTabIndex: listOfTabs.indexOf(type)
			});
			if (ticket_id) {
				history.push(`/tickets?type=${type}&ticket_id=${ticket_id}`);
			} else {
				history.push(`/tickets?type=${type}`);
			}
			this.getTickets({ type });
		}
		const user_id = LocalStorageHelper.getUserId();
		socket.on('connection', () => {});
		socket.on('valid_connection', () => {
			this.joinUserSession({ user_id });
		});
		this.socket.on('ticket', async ticket => {
			const { ticket_id, user_id } = ticket;
			console.log({ ticket });
			const getLoggedInUserId = LocalStorageHelper.getLoggedIn()._id;
			console.log({ getLoggedInUserId });
			// RECEIVER STAFF NEW TICKET.
			if (ticket_id && user_id === getLoggedInUserId) {
				newTicket({ ticket_id: ticket.ticket_id });
			}
		});
	}

	componentDidUpdate(prevProps) {
		const { ticketPageProps: prev_ticketPageProps, location: prev_location } = prevProps;
		const { loadingUpdateTicket: prev_loadingUpdateTicket, loadingNewTicket: prev_loadingNewTicket } = prev_ticketPageProps;

		const { ticketPageProps } = this.props;
		const { loadingUpdateTicket, loadingNewTicket, ticket } = ticketPageProps;

		const { query } = UrlParse(this.props.location.search, true);
		const { history } = this.props;
		const { query: prevQuery } = UrlParse(prev_location.search, true);

		const listOfTabs = this.state.ticketTabs.map(a => a.id);
		const { ticket_id, type } = query;
		if ((!loadingUpdateTicket && loadingUpdateTicket !== prev_loadingUpdateTicket) || (ticket && !loadingNewTicket && loadingNewTicket !== prev_loadingNewTicket)) {
			// change the active tab in list of tickets
			if (!listOfTabs.includes(query.type)) {
				if (ticket_id) {
					history.push(`/tickets?type=all&ticket_id=${ticket_id}`);
				} else {
					history.push('/tickets?type=all');
				}
				this.getTickets({ type: 'all' });
			} else {
				// history.push(`/tickets?type=${type}&ticket_id=${ticket_id}`);
				this.setState({
					selectedTabIndex: listOfTabs.indexOf(type)
				});
				this.getTickets({ type });
			}
		}
		if (type && type !== prevQuery.type) {
			this.setState({
				selectedTabIndex: listOfTabs.indexOf(type)
			});
			this.getTickets({ type });
		}
	}

	componentWillUnmount() {
		this.socket.disconnect();
		this.props.resetTicket();
	}

	joinUserSession = ({ user_id }) => {
		const { socket } = this;
		socket.emit('join_user_session', { user_id });
	};

	getTickets = ({ type }) => {
		const { query } = UrlParse(this.props.location.search, true);
		this.props.getTickets({ type, ticket_id: query.ticket_id });
	};

	handleTabChange = index => {
		this.getTickets({ type: this.state.ticketTabs[index].id });
		this.setState({
			selectedTabIndex: index
		});
	};
	renderTicketsTypeBadge = typeBadge => {
		let ticketbadge;
		if (typeBadge === 'fb_comment') {
			ticketbadge = (
				<Badge
					status="info"
					size="small"
				>
					Facebook Comment
				</Badge>
			);
		} else if (typeBadge === 'whatsapp') {
			ticketbadge = (
				<Badge
					status="success"
					size="small"
				>
					WhatsApp Message
				</Badge>
			);
		} else if (typeBadge === 'fb_messenger') {
			ticketbadge = (
				<Badge
					status="success"
					size="small"
				>
					Facebook Message
				</Badge>
			);
		}
		return ticketbadge;
	};
	renderStatusBadge = ({ status }) => {
		let badge;
		if (status === 'pending_acceptance') {
			badge = (
				<Badge
					status="attention"
					size="small"
				>
					Pending Acceptance
				</Badge>
			);
		} else if (status === 'resolved') {
			badge = (
				<Badge
					progress="complete"
					size="small"
				>
					Resolved
				</Badge>
			);
		} else if (status === 'in-progress') {
			badge = (
				<Badge
					progress="partiallyComplete"
					size="small"
				>
					In Progress
				</Badge>
			);
		} else if (status === 'pending') {
			badge = (
				<Badge
					status="incomplete"
					size="small"
				>
					Pending
				</Badge>
			);
		}
		return badge;
	};

	renderTicketList = ({ tickets, loadingTickets }) => {
		const { query } = UrlParse(this.props.location.search, true);
		return (
			<ResourceList
				loading={loadingTickets}
				resourceName={{ singular: 'staff', plural: 'staffs' }}
				items={tickets}
				selectedItems={[query.ticket_id]}
				renderItem={item => {
					const { _id, facebook_contact_id, facebook_message, twilio_contact, twilio_message, unread_messages, status, type: ticketType } = item;
					const phone = item.type === "fb_messenger" ? _.get(facebook_contact_id, 'phone') : _.get(twilio_contact, 'phone');
					const message = item.type === "fb_messenger" ? _.get(facebook_message[0], 'message') : _.get(twilio_message, 'message');
					const date = item.type === "fb_messenger" ? moment(_.get(facebook_message[0], 'timestamp')).format('YYYY-MM-DD') : _.get(twilio_message, 'date');
					const media = <Avatar
						name={phone}
						customer
					              />;

					return (
						<div>
							<ResourceItem
								id={_id}
								media={media}
								accessibilityLabel={`View details for ${phone}`}
							>
								<div onClick={() => this.props.history.push(`/tickets?type=${this.state.ticketTabs[this.state.selectedTabIndex].id}&ticket_id=${_id}&ticket_type=${ticketType}`)}>
									<div style={{ display: 'flex', justifyContent: 'space-between' }}>
										<TextStyle variation="strong">{facebook_contact_id}</TextStyle>
										<span style={{ fontSize: 11 }}>{date}</span>
									</div>
									<div style={{ display: 'flex', justifyContent: 'space-between' }}>
										<p style={styles.messagePreview}>{message}</p>
									</div>
									<div style={{ display: 'flex', justifyContent: 'space-between' }}>
										{this.renderTicketsTypeBadge(ticketType)}
										{['my', 'all'].includes(query.type) && this.renderStatusBadge({ status })}
										{unread_messages > 0 && (
											<Badge
												status="critical"
												size="small"
											>
												{unread_messages}
											</Badge>
										)}
									</div>
								</div>
							</ResourceItem>
						</div>
					);
				}}
			/>
		);
	};

	updateText = value => {
		const { loading, options } = this.state;
		this.setState({ searchInputValue: value });
		if (!loading) {
			this.setState({ loading: true });
		}
		setTimeout(() => {
			if (value === '') {
				this.setState({ options: testOptions, loading: true });
				return;
			}
			const filterRegex = new RegExp(value, 'i');
			const resultOptions = options.filter(option => option.label.match(filterRegex));
			this.setState({
				options: resultOptions,
				loading: false
			});
		});
	};

	updateSelection = selected => {
		const selectedText = selected.map(selectedItem => {
			const matchedOption = this.state.options.find(option => {
				return option.value.match(selectedItem);
			});
			return matchedOption && matchedOption.label;
		});
		this.setState({ selected, searchInputValue: selectedText });
	};

	render() {
		const { ticketPageProps, location } = this.props;
		const { selectedTabIndex, searchInputValue, ticketTabs } = this.state;
		const { tickets, loadingTickets } = ticketPageProps;
		const { query } = UrlParse(location.search, true);
		const sortedTickets =
			tickets &&
			_.orderBy(
				tickets,
				function(o) {
					if(query.ticket_type && query.ticket_type === 'fb_messenger'){
						return new moment(_.get(o, 'facebook_message.created_at'));
					} else if(query.ticket_type && query.ticket_type === 'fb_messenger'){
						return new moment(_.get(o, 'twilio_message.created_at'));
					}
				},
				['desc']
			);

		const textField = (
			<Autocomplete.TextField
				onChange={value => this.updateText(value)}
				value={searchInputValue}
				prefix={<Icon
					source={SearchMinor}
					color="inkLighter"
				        />}
				disabled={loadingTickets}
				placeholder="Search customer"
			/>
		);

		const onChange = (key) => {
			console.log(key);
		};

		return (
			<>
				<Tabs
					style={{backgroundColor: 'white', marginBottom: '1em', padding: 20, paddingBottom: 0}}
					defaultActiveKey="1"
					onChange={onChange}
				>
					<TabPane
						tab="All"
						key="1"
					>
					</TabPane>
					<TabPane
						tab="Assigned"
						key="2"
					>
					</TabPane>
					<TabPane
						tab="Unassigned"
						key="3"
					>
					</TabPane>
					<TabPane
						tab="Resolved"
						key="4"
					>
					</TabPane>
				</Tabs>
					
				<div style={{backgroundColor: '#f0f2f5', display: 'flex', justifyContent: 'space-between'}}>
					<CustomerList {...this.props} />
					<MessengerChat
						pageId="286645431852598"
						language="sv_SE"
						themeColor={"#000000"}
						bottomSpacing={300}
						loggedInGreeting="loggedInGreeting"
						loggedOutGreeting="loggedOutGreeting"
						greetingDialogDisplay={"show"}
						debugMode={true}
						onMessengerShow={() => {
							console.log("onMessengerShow");
						}}
						onMessengerHide={() => {
							console.log("onMessengerHide");
						}}
						onMessengerDialogShow={() => {
							console.log("onMessengerDialogShow");
						}}
						onMessengerDialogHide={() => {
							console.log("onMessengerDialogHide");
						}}
						onMessengerMounted={() => {
							console.log("onMessengerMounted");
						}}
						onMessengerLoad={() => {
							console.log("onMessengerLoad");
						}}
					/>
					<ChatBox />
					<div style={{width: '100%'}}>
						<CustomerInformation />
						<TicketInfo />
					</div>
				</div>
			</>
				
		);
	}
}

const styles = {
	messagePreview: {
		whiteSpace: 'nowrap',
		textOverflow: 'ellipsis',
		maxWidth: '90%',
		display: 'block',
		overflow: 'hidden',
		fontSize: 11,
		color: '#3596eb'
	}
};

export default TicketList;
