import React, { Component } from 'react';
import _ from 'lodash';
import { TextField, Caption, Card, Avatar, Icon, Modal } from '@shopify/polaris';
import { SmileyHappyMajorMonotone, ThumbsUpMajorMonotone, AttachmentMajorMonotone } from '@shopify/polaris-icons';
import EmojiPicker from 'emoji-picker-react';
import Dropzone from 'react-dropzone';
import FileTypes from '../../../constants/FileTypes';

const FILE_SIZE_MB = 4;
const MAX_FILE_SIZE = FILE_SIZE_MB * 1e6;

class FacebookPostComment extends Component {
	constructor(props) {
		super(props);
		this.state = {
			fbCommentValue: '',
			inputText: '',
			file: null
		};
	}
	handleEmojiPicker = () => {
		const showEmojiPicker = this.state.emojipicker;
		this.setState({ emojipicker: !showEmojiPicker });
	};
	onClickEmoji = (event, emojiObject) => {
		const prevEmojiText = [...this.state.inputText];
		const dataEmojiPicker = emojiObject.emoji;

		const emojiData = prevEmojiText.concat(dataEmojiPicker);
		const newEmojiData = emojiData.join('');
		this.setState({ inputText: newEmojiData });
	};
	handleInputChange = value => {
		this.setState({ inputText: value });
	};
	attachFile = files => {
		if (files) {
			this.setState({
				file: files[0]
			});
		}
	};
	onRejectedAttachFile = files => {
		if (!_.isEmpty(files)) {
			const file = files[0];
			if (file && file.errors[0].code === 'file-too-large') {
				this.props.alert.show(`File too large. Maximum file size is ${FILE_SIZE_MB} MB`, { type: 'error' });
			}
		}
	};
	handleSendMessage = async () => {
		const { inputText, file } = this.state;
		if (inputText) {
			// text message type
			await this.props.sendMessage({ message: inputText });
		} else {
			await this.props.sendMessage({ file, filename: file.name, mime: file.type });
		}
		this.setState({
			inputText: '',
			file: null
		});
	};
	closeAttachmentPreview = () => {
		this.setState({ file: '' });
	};
	renderPreviewModal = () => {
		const { file } = this.state;
		const { type, name } = file;
		return (
			<Modal
				open={Boolean(file)}
				onClose={() => this.closeAttachmentPreview()}
				title="Attachment Preview"
				primaryAction={{
					content: 'Send',
					onAction: () => this.handleSendMessage()
				}}
				secondaryActions={[
					{
						content: 'Cancel',
						onAction: () => this.closeAttachmentPreview()
					}
				]}
			>
				<Modal.Section>
					<div style={{ textAlign: 'center' }}>
						{type.includes('image') ? (
							<img
								src={URL.createObjectURL(file && file)}
								alt={file && file.name}
								style={{ maxWidth: 250, maxHeight: 250 }}
							/>
						) : type.includes('video') ? (
							<video
								src={URL.createObjectURL(file)}
								controls
								style={{ maxWidth: 250, maxHeight: 250 }}
							/>
						) : (
							<p>{name}</p>
						)}
					</div>
				</Modal.Section>
			</Modal>
		);
	};

	dropZoneAttachment = () => (
		<Dropzone
			onDrop={this.attachFile}
			accept={FileTypes.image
				.concat(FileTypes.document)
				.concat(FileTypes.video)
				.concat(FileTypes.audio)
				.join(',')}
			onDropRejected={this.onRejectedAttachFile}
			multiple={false}
			maxSize={MAX_FILE_SIZE}
		>
			{({ getRootProps, getInputProps }) => (
				<section style={{ display: 'flex', width: '10%', cursor: 'pointer', alignItems: 'center', justifyContent: 'center', marginBottom: 5 }}>
					<div {...getRootProps()}>
						<input {...getInputProps()} />
						<Icon
							source={AttachmentMajorMonotone}
							color="inkLightest"
						/>
					</div>
				</section>
			)}
		</Dropzone>
	);
	render() {
		const { fbCommentValue, inputText, file } = this.state;
		const postData = [
			{
				comments: [
					{
						profilePhoto: 'https://www.gstatic.com/tv/thumb/persons/287557/287557_v9_ba.jpg',
						created_at: '2020-06-03T08:05:46.311Z',
						date: '2020-06-03T08:05:43.000Z',
						flow: 'inbound',
						from: 'Lebron James',
						message: 'Has the order shipped already ?'
					}
				],
				PageName: 'Facepalm Store',
				commentDiscription: 'Lorem ipsum dolor sit amet, consectetur adipiscing elit, sed do eiusmod tempor incididunt ut labore et dolore magna aliqua.',
				pagePhoto: 'https://res.cloudinary.com/grohealth/image/upload/f_auto,fl_lossy,q_auto/v1581678662/DCUK/Content/iStock-959080376.jpg'
			}
		];

		return (
			<React.Fragment>
				<Card>
					<div style={styles.conversationHeader}>
						<Avatar
							customer
							source={postData[0].pagePhoto}
						/>
						<div style={{ marginLeft: 10 }}>
							<span>
								<Caption>{postData[0].PageName}</Caption>
							</span>
						</div>
					</div>
					<div style={styles.bgWhite}>
						<div className="post-text">
							<div>
								<p style={styles.postDiscription}>
									<strong>{postData[0].commentDiscription}</strong>
								</p>
							</div>
						</div>
					</div>
					<div>
						{postData[0].comments.map((data, index) => {
							return (
								<React.Fragment>
									<div style={styles.postDetails}>
										<span style={styles.avatarPhoto}>
											<div>
												<Avatar
													customer
													source={data.profilePhoto}
												/>
											</div>
										</span>
										<div style={styles.commentData}>
											<span>
												<strong style={styles.senderFrom}>{data.from}</strong>
											</span>
											<span>
												<p>{data.message}</p>
											</span>
										</div>
									</div>
									<div style={{ display: 'flex', marginLeft: '15%', marginTop: -10 }}>
										<p style={{ fontSize: 10, color: '#777' }}>2h</p>
									</div>
									<div style={{ height: 400 }}>
										<div style={styles.postCommentInput}>
											<div style={{ marginRight: 10, paddingTop: 15, paddingBottom: 10 }}>
												<span style={styles.avatarPhoto}>
													<Avatar
														source={postData[0].pagePhoto}
														size="small"
													/>
												</span>
											</div>
											<div style={styles.commentInput}>
												<div style={{ marginLeft: 10 }}>
													<span>
														<TextField
															type="text"
															placeholder="Write a comment..."
															value={inputText}
															onChange={this.handleInputChange}
															multiline
														/>
													</span>
												</div>
												<span>
													{this.state.emojipicker ? (
														<div style={styles.emojiPicker}>
															<EmojiPicker
																preload
																onEmojiClick={this.onClickEmoji}
															/>
														</div>
													) : (
														''
													)}
												</span>

												<div style={{ display: 'flex', flexDirection: 'row', padding: 10 }}>
													<div
														onClick={this.handleEmojiPicker}
														style={{ float: 'left', width: 32, height: 32 }}
													>
														<Icon
															source={SmileyHappyMajorMonotone}
															color="inkLightest"
															size="small"
														/>
													</div>

													<div style={{ float: 'left', width: 32, height: 32, marginLeft: 20 }}>{this.dropZoneAttachment()}</div>

													<div style={{ float: 'left', width: 32, height: 32 }}>
														<Icon
															source={ThumbsUpMajorMonotone}
															color="inkLightest"
															size="small"
														/>
													</div>
												</div>
											</div>
										</div>
									</div>
								</React.Fragment>
							);
						})}
					</div>
					{file && this.renderPreviewModal()}
				</Card>
			</React.Fragment>
		);
	}
}
const styles = {
	conversationHeader: {
		display: 'flex',
		alignItems: 'center',
		height: 52,
		background: '#4267b2',
		padding: 15,
		color: 'white'
	},
	inputContainer: {
		display: 'flex',
		flexDirection: 'row',
		backgroundColor: '#f1f3f4',
		borderTop: '0.1px solid #d4d4d4',
		padding: '0px 0px 0px 0px'
	},
	pendingAcceptance: {
		background: '#173630',
		padding: 5
	},
	iconSize: {},
	bgWhite: {
		backgroundColor: 'white'
	},
	postDiscription: {
		fontSize: '14px',
		fontWeight: 'normal',
		lineHeight: 1.38
	},
	commentData: {
		padding: '5px',
		backgroundColor: '#EEECEC',
		borderRadius: '18px',
		display: 'flex',
		flexDirection: 'row',
		justifyContent: 'space-between'
	},
	senderFrom: {
		display: 'flex',
		fontSize: 12,
		color: '#0077FC',
		paddingRight: 5
	},
	postDetails: {
		display: 'flex',
		alignItems: 'flex-end',
		flexDirection: 'row',
		justifyContent: 'space-around',
		marginBottom: 10,
		marginTop: 100,
		paddingLeft: 10,
		paddingRight: 10
	},
	postCommentInput: {
		display: 'flex',
		flexDirection: 'row',
		justifyContent: 'space-around',
		marginBottom: 10,
		padding: 10,
		borderTop: '2px solid #EEECEC'
	},
	avatarPhoto: {
		float: 'left',
		width: 32,
		height: 32
	},
	commentInput: {
		background: '#fff',
		display: 'flex',
		flexDirection: 'row',
		border: '1px solid #dfe1e5',
		boxShadow: 'none',
		borderRadius: 24,
		height: 30,
		justifyContent: ' space-around',
		width: '90%',
		height: 'auto',
		padding: 5
	},
	commentTextArea: {
		display: 'flex',
		flexDirection: 'row',
		border: 'none',
		resize: 'none',
		height: 40,
		float: 'left',
		width: '100%',
		marginLeft: 10
	},
	agoHolder: {
		position: 'relative'
	},
	timeAgo: {
		position: 'absolute',
		top: -4,
		fontSize: 10,
		color: '#777'
	},
	emojiPicker: {
		marginTop: 50,
		marginLeft: -200,
		display: 'flex',
		position: 'absolute'
	}
};
export default FacebookPostComment;
