import React, { Component } from 'react';
import UrlParse from 'url-parse';
import { Layout, Breadcrumb, Typography, Row, Col, Modal } from 'antd';
import { compose } from 'redux';
import { withAlert } from 'react-alert';

import DetailMenu from '../../components/DetailMenu';
import StaffBasicPage from './StaffBasicPage';
import StaffTicketsPage from './StaffTicketsPage';
const { Header, Content } = Layout;
const { Title } = Typography;

class StaffDetailsPage extends Component {
	constructor(props) {
		super(props);
		this.state = {
			staff: {}
		};
	};

	componentDidMount() {
		const { location } = this.props;
		const { query } = UrlParse(location.search, true);
		this.props.getStaffById(query.id);
	}

	componentWillUnmount() {
		this.props.resetPage();
	}

	getStaffDetailsPageComponent = () => {
		const path = this.props.location.pathname.split('/');
		const pathname = path[path.length-1];

		switch(true) {
			case (pathname === 'basic'):
				return <StaffBasicPage {...this.props} />;
			case (pathname === 'tickets'):
				return <StaffTicketsPage {...this.props} />;
			default:
				return this.props.history.push('/staffs');
		}
	}

	render() {
		const { pageProps, location, history } = this.props;
		const { query } = UrlParse(location.search, true);
		const sections = [
			{ key: '/staff', content: 'Staff', link: true },
			{ key: 'Details', content: 'Details', active: true}
		];
		const pageDetailTabs = [
			{ title: 'Basic', path: `/staffs/id/basic?id=${query.id}` },
			{ title: 'Tickets', path: `/staffs/id/tickets?id=${query.id}` }
		];

		return (
			<>
				<Header className="breadcrumb-header" style={{ backgroundColor: 'white' }}>
					<Row>
						<Col span={21}>
							<Breadcrumb style={{ marginTop: '0.5rem'}}>
								{sections && sections.map(section => {
									return (
										<Breadcrumb.Item key={section.key}>
											{section.link?
												// eslint-disable-next-line jsx-a11y/anchor-is-valid
												<a
													href="#"
													onClick={section.onClick}
												>{section.content}</a> : section.content
											}
										</Breadcrumb.Item>
									);
								})}
							</Breadcrumb>
							<Title
								level={5}
								style={{ paddingTop: 10 }}
							>Staff Detail</Title>
						</Col>
					</Row>
				</Header>
				<Content style={{ margin: '24px 16px 0', backgroundColor: 'white' }}>
					<DetailMenu
						tabs={pageDetailTabs}
						location={location}
						history={history}
					/>
					{this.getStaffDetailsPageComponent()}
				</Content>
			</>
		);
	}
};

export default compose(withAlert())(StaffDetailsPage);
