import { connect } from 'react-redux';
import TicketPage from './TicketPage';
import { sendMessage, getTickets, getTwilioMessages, getTwilioFileById, addTicketNote, updateCustomerInformation,
	updateTicketStatus, updateTicketAsignee, getTicketById, getTwilioMessageById, newTicket,
	getUsers, resetTicket, resetMeta, resetPage, getFbMessages, getFbMessagesByID, sendMessageFb } from '../../actions/TicketPage';

const mapsStateToProps = state => ({
	ticketPageProps: state.ticketPageReducer
});

const mapsDispatchToProps = dispatch => ({
	sendMessage(options) {
		dispatch(sendMessage(options));
	},
	getTickets(options) {
		dispatch(getTickets(options));
	},
	getTwilioMessages(options) {
		dispatch(getTwilioMessages(options));
	},
	getTwilioMessageById(options) {
		dispatch(getTwilioMessageById(options));
	},
	getTicketById(options) {
		dispatch(getTicketById(options));
	},
	newTicket(options) {
		dispatch(newTicket(options));
	},
	getTwilioFileById(options) {
		dispatch(getTwilioFileById(options));
	},
	updateTicketAsignee(options) {
		dispatch(updateTicketAsignee(options));
	},
	updateTicketStatus(options) {
		dispatch(updateTicketStatus(options));
	},
	updateCustomerInformation(options) {
		dispatch(updateCustomerInformation(options));
	},
	addTicketNote(options) {
		dispatch(addTicketNote(options));
	},
	getUsers(){
		dispatch(getUsers());
	},
	resetTicket() {
		dispatch(resetTicket());
	},
	resetMeta() {
		dispatch(resetMeta());
	},
	resetPage() {
		dispatch(resetPage());
	},
	getFbMessages(options) {
		dispatch(getFbMessages(options));
	},
	getFbMessagesByID(options) {
		dispatch(getFbMessagesByID(options));
	},
	sendMessageFb(options){
		dispatch(sendMessageFb(options));
	}
});

export default connect(
	mapsStateToProps,
	mapsDispatchToProps
)(TicketPage);
