import moment from 'moment';
import { push } from 'connected-react-router';
import * as ActionTypes from '../constants/ActionTypes';
import API from '../helpers/api';


export function submitPage(body) {
	return async dispatch => {
		const { password, confirm_password } = body;
		if (password !== confirm_password) {
			return dispatch({
				type: ActionTypes.SUBMIT_REGISTER_PAGE_ERROR,
				payload: {
					meta: { code: 201, message: 'Password did not matched.' }
				}
			});
		}

		dispatch({
			type: ActionTypes.SUBMIT_REGISTER_PAGE_REQUEST,
			payload: {
				loadingSubmit: true
			}
		});

		const response = await API.auth.signUp(body);
		if (response.meta.code !== 200) {
			return dispatch({
				type: ActionTypes.SUBMIT_REGISTER_PAGE_ERROR,
				payload: {
					meta: response.meta,
					loadingSubmit: false
				}
			});
		}
		// success
		dispatch({
			type: ActionTypes.SUBMIT_REGISTER_PAGE_SUCCESS,
			payload: {
				loadingSubmit: false,
				meta: response.meta
			}
		});
	};
}

export function reset() {
	return async dispatch => {
		dispatch({
			type: ActionTypes.RESET_REGISTER_PAGE
		});
	};
}

export function resetMeta() {
	return async dispatch => {
		dispatch({
			type: ActionTypes.RESET_REGISTER_PAGE_META
		});
	};
}
