import * as ActionTypes from '../../constants/ActionTypes';
import API from '../../helpers/api';
import LocalStorageHelper from '../../helpers/local_storage';

export function getAccount() {
	return async dispatch => {
		dispatch({
			type: ActionTypes.GET_SETTINGS_ACCOUNT_REQUEST,
			payload: {
				loadingAccount: true
			}
		});

		const response = await API.admin.accounts.getAccount();
		if (response.meta.code !== 200) {
			return dispatch({
				type: ActionTypes.GET_SETTINGS_ACCOUNT_ERROR,
				payload: {
					meta: response.meta,
					loadingAccount: false
				}
			});
		}
		// success
		const { user } = response.data;
		dispatch({
			type: ActionTypes.GET_SETTINGS_ACCOUNT_SUCCESS,
			payload: {
				loadingAccount: false,
				account: user
			}
		});
		// get staffs
		dispatch(getStaffs());
	};
}

