export function updateAccount(fields) {
	return async dispatch => {
		const { password, old_password } = fields;
		if (password !== old_password) {
			return dispatch({
				type: ActionTypes.UPDATE_SETTINGS_ACCOUNT_ERROR,
				payload: {
					meta: { code: 201, message: 'Password did not matched.' }
				}
			});
		}

		dispatch({
			type: ActionTypes.UPDATE_SETTINGS_ACCOUNT_REQUEST,
			payload: {
				loadingSubmit: true
			}
		});
		const { mobile_number, industry, business_name } = fields.details;
		const response = await API.admin.accounts.updateAccount({ ...fields, mobile_number, business_name, industry });
		if (response.meta.code !== 200) {
			return dispatch({
				type: ActionTypes.UPDATE_SETTINGS_ACCOUNT_ERROR,
				payload: {
					loadingSubmit: false,
					meta: response.meta
				}
			});
		}
		// success
		const { user } = response.data;
		// update localStorage logged in user value.
		localStorage.setItem(
			'app',
			JSON.stringify({
				...JSON.parse(localStorage.getItem('app')),
				user
			})
		);
		dispatch({
			type: ActionTypes.UPDATE_SETTINGS_ACCOUNT_SUCCESS,
			payload: {
				loadingSubmit: false,
				account: user,
				meta: response.meta
			}
		});
	};
}

