export function getStaffById({ id }) {
	return async dispatch => {
		dispatch({
			type: ActionTypes.GET_STAFF_BY_ID_REQUEST,
			payload: {
				loadingStaffModal: true
			}
		});

		const response = await API.admin.users.getStaffById(id);
		if (response.meta.code !== 200) {
			return dispatch({
				type: ActionTypes.GET_STAFF_BY_ID_ERROR,
				payload: {
					loadingStaffModal: false
				}
			});
		}
		// success
		const { staff } = response.data;
		dispatch({
			type: ActionTypes.GET_STAFF_BY_ID_SUCCESS,
			payload: {
				loadingStaffModal: false,
				selectedStaff: staff
			}
		});
	};
}

export function submitStaff(fields) {
	return async (dispatch, getState) => {
		const { staffs } = getState().settingsPageReducer;
		const { selectedStaff } = fields; //if there is selectedStaff, means update the staff.

		const { password, old_password } = fields;
		if (password !== old_password) {
			return dispatch({
				type: ActionTypes.UPDATE_STAFF_ERROR,
				payload: {
					meta: { code: 201, message: 'Password did not matched.' }
				}
			});
		}

		dispatch({
			type: ActionTypes.UPDATE_STAFF_REQUEST,
			payload: {
				loadingSubmit: true
			}
		});

		const body = {
			staff: {
				...fields
			}
		};

		let response;
		if (selectedStaff) {
			body.staff.updated_at = selectedStaff.updated_at;
			response = await API.admin.users.updateStaffById(selectedStaff._id, body);
		} else {
			response = await API.admin.users.createStaff(body);
		}
		if (response.meta.code !== 200) {
			return dispatch({
				type: ActionTypes.UPDATE_STAFF_ERROR,
				payload: {
					loadingSubmit: false,
					meta: response.meta
				}
			});
		}
		// success
		const { staff } = response.data;
		let payload = {
			loadingSubmit: false,
			meta: response.meta
		};

		if (selectedStaff) {
			payload.selectedStaff = null;
			const updatedStaffs = [...staffs];
			const findIndexToUpdate = updatedStaffs.findIndex(x => x._id === staff._id);
			updatedStaffs[findIndexToUpdate] = { ...staff };
			payload.staffs = updatedStaffs;
		} else {
			payload.staffs = [...staffs, staff];
		}

		dispatch({
			type: ActionTypes.UPDATE_STAFF_SUCCESS,
			payload
		});
	};
}

export function deleteStaff({ id }) {
	return async (dispatch, getState) => {
		const { staffs } = getState().settingsPageReducer;

		dispatch({
			type: ActionTypes.DELETE_STAFF_BY_ID_REQUEST,
			payload: {
				loadingDeleteStaff: true
			}
		});

		const response = await API.admin.users.deleteStaffById(id);
		if (response.meta.code !== 200) {
			return dispatch({
				type: ActionTypes.DELETE_STAFF_BY_ID_ERROR,
				payload: {
					loadingDeleteStaff: false
				}
			});
		}
		// success
		const copyStaffs = [...staffs];
		const indexToBeDeleted = copyStaffs.findIndex(x => x._id === id);
		copyStaffs.splice(indexToBeDeleted, 1);
		dispatch({
			type: ActionTypes.DELETE_STAFF_BY_ID_SUCCESS,
			payload: {
				loadingDeleteStaff: false,
				staffs: copyStaffs
			}
		});
	};
}

export function updateStatusById({ status }) {
	return async dispatch => {
		dispatch({
			type: ActionTypes.UPDATE_ACCOUNT_STATUS_REQUEST,
			payload: {
				loadingUpdateStatus: true
			}
		});
		const logInType = LocalStorageHelper.getLoginType();
		let response;
		if (logInType === 'admin') {
			response = await API.admin.accounts.updateStatus({ status });
		} else {
			response = await API.staff.users.updateStatus({ status });
		}
		if (response.meta.code !== 200) {
			return dispatch({
				type: ActionTypes.UPDATE_ACCOUNT_STATUS_ERROR,
				payload: {
					loadingUpdateStatus: false,
					meta: response.meta
				}
			});
		}
		// success
		const { staff, admin } = response.data;

		localStorage.setItem(
			'app',
			JSON.stringify({
				...JSON.parse(localStorage.getItem('app')),
				...(staff && { staff: { ...staff } }),
				...(admin && { admin: { ...admin } })
			})
		);

		dispatch({
			type: ActionTypes.UPDATE_ACCOUNT_STATUS_SUCCESS,
			payload: {
				loadingUpdateStatus: false,
				meta: response.meta
			}
		});
	};
}

